<template>
    <div class="main-bg">
        <com-manage-card class="m-b-15" v-for="(item, index) in dataSource" :key="'inHos' + index"  :data="item" :type="1" @show-code="handleGoInHosCodeDetailPage(item.inHosNo)" @go-detail="handleGoInHosInfoPage(item.inHosNo)"></com-manage-card>
        <div class="add-inHos-btn flex_dom flex_item_center flex_item_mid" @click="handleAddInHos">
            <div class="add-btn pr">
                <span class="abs-mid cross1"></span>
                <span class="abs-mid cross2"></span>
            </div>
            <span class="font16 color-fff">添加住院人</span>
        </div>
    </div>
</template>
<script>
import CManageCard from "../../common/CManageCard";
export default {
    name: "ManageInHosInfo",
    //注册组件
    components: {
        [CManageCard.name]: CManageCard
    },
    data() {
        return {
            dataSource: [],
        };
    },
    mounted() {
        this.$api.inHosNo.queryPatientInfoList({
            data: {},
            resLoading: true,
        }).then((response) => {
            if (response.data.Code == 0) {
                this.dataSource = this.wrapData(response.data.Data);
            }
            else if (response.data.Code != -2) {
                // 如果解绑了所有的住院号要把bindedInHosNo改为false
                this.globalMethods.updateStatus("bindedInHosNo", false);
            }
        })
    },
    methods: {
        handleGoInHosCodeDetailPage(inHosNo) {
            this.$router.push({name: "InHosCodeDetail", query: {inHosNo}});
        },
        handleGoInHosInfoPage(inHosNo) {
            this.$router.push({name: "InhosInfoDetail", query: {inHosNo}});
        },
        handleAddInHos() {
            this.$router.push({name: "BindInHosNo"});
        },
        wrapData(data) {
            let dataArr = data;
            for (let i = 0; i < dataArr.length; i++) {
                let item = dataArr[i];
                let arrItem = item.inHosNo.split("");
                arrItem.splice(2, 3, "***");
                item.inHosNoLabel =  arrItem.join("");
            };
            return dataArr;
        }
    }
};

</script>
<style scoped>
.add-inHos-btn {
    width: 100%;
    height: 43px;
    background-image: linear-gradient(to right, #00CAFF , #2872FF);
    border-radius: 4px;
    margin: 20px auto;
}
.add-btn {
    width: 44px;
    height: 100%;
}
.add-btn>span {
    border-radius: 1.5px;
    background-color: #fff;
}
.add-btn .cross1 {
    width: 20px;
    height: 3px;
}
.add-btn .cross2 {
    width: 3px;
    height: 20px;
}
</style>



