<template>
    <div class="service-btn flex_column flex_item_between flex_item_mid">
        <div class="img flex_column flex_item_center flex_item_mid" :style="styleObj">
            <img :src="icon">
        </div>
        <div class="text font14">{{text}}</div>
    </div>
</template>
<script>
export default {
    name: "base-service-btn",
    data() {
        return {
            
        };
    },
    props: {
        gradient: {
            type: String,
            default: "#00CAFF,#008CFF"
        },
        text: {
            type: String,
            default: "服务按钮"
        },
        icon: {
            type: String,
        }
    },
    computed: {
        styleObj() {
            let colorArr = this.gradient.split(",");
            return {
                "background-image": `linear-gradient(to bottom right, ${colorArr[0]}, ${colorArr[1]})`
            }
        },
    }
};
</script>
<style scoped>
.service-btn {
    width: 70px;
    height: 73px;
}
.service-btn .img {
    width: 50px;
    height: 50px;
    border-radius: 20px;
}
.service-btn .img img {
    width: 70%;
    height: 70%;
}
.service-btn .text {
    color: #4A4E5A;
}
</style>


