<template>
    <div>
        <com-card-detail type="1" :data="dataSource" :is-default="dataSource.isDefault" :btn-disabled="btnDisabled" @go-charge="handleGoChargeDeposit" @delete-card="handleUnbind" @switch-change="handleSetDefault($event, dataSource.inHosNo)" ref="inHosDetail"></com-card-detail>
    </div>
</template>
<script>
import CCardDetail from "../../common/CCardDetail";
export default {
    name: "CardDetail",
    components: {
        [CCardDetail.name]: CCardDetail,
    },
    data() {
        return {
            btnDisabled: false,
            dataSource: {}
        };
    },
    mounted() {
        let inHosNo = this.$route.query.inHosNo;
        this.$api.inHosNo.queryPatientInfoByID({
            data: {inHosNo},
            resLoading: true,
        }).then((response) => {
            if (response.data.Code == 0) {
                this.dataSource = response.data.Data;
            }
        })
    },
    methods: {
        handleGoChargeDeposit() {
            let inHosNo = this.dataSource.inHosNo;
            this.$router.push({name: "ChargeDeposit", query: {inHosNo}});
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        handleUnbind() {
            this.btnDisabled = true;
            let inHosNo = this.$route.query.inHosNo;
            this.$api.inHosNo.unbindPatientInfo({
                data: {inHosNo},
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.toastSuccess("解绑成功");
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            })
        },
        handleSetDefault(isDefault, inHosNo) {
            this.$api.inHosNo.updatePatientInfo({
                data: {
                    isDefault,
                    inHosNo
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    console.log("设置默认住院号成功");
                }
                else if (response.data.Code != -2) {
                    this.$refs.inHosDetail.toggleSwitch(true);
                }
            })
        },
    }
};

</script>
<style scoped>

</style>


