<template>
    <div class="main-bg">
        <van-cell-group class="panel radius-10">
            <van-cell title="姓名" :value="patName" title-class="cell-title-style" value-class="cell-value-style"/>
            <van-cell title="就诊卡号" :value="patMedNo" title-class="cell-title-style" value-class="cell-value-style"/>
        </van-cell-group>
        <van-cell-group class="panel radius-10">
            <van-cell title="项目名称" value="数量" title-class="cell-title-style" value-class="cell-value-style"/>
            <van-checkbox-group class="p-l-10 p-r-10" v-model="result" ref="checkboxGroup">
                <van-row class="chk-item" type="flex" align="center" v-for="(item, index) in dataSource" :key="'i' + index">
                    <van-col span="16"><van-checkbox class="tl" :name="'chk-' + index" :checked-color="checkedColor[index]" @click="handleChkClick('chk-' + index)">{{item.itemName}}{{Number(item.amount).toFixed(2) + '元'}}</van-checkbox></van-col>
                    <van-col class="chk-text" span="8"><van-stepper button-size="20" :disabled="item.isModify == 0" v-model="stepper[index]" @change="handleChkClick" /></van-col>
                </van-row>
            </van-checkbox-group>
            <div class="flex_dom flex_item_between p-l-10 p-r-10 p-t-10">
                <van-checkbox v-model="checkAll" @click="handleCheckAll()">全选</van-checkbox>
                <span>已选费用：{{totalConsume}}元</span>
            </div>
            <div class="m-t-20 pay-btn">
                <van-button type="info" :color="'linear-gradient(to right, #00CAFF, #2772FF)'" size="small" :disabled="btnDisabled" @click="handleCharge">立即支付</van-button>
            </div>
        </van-cell-group>
    </div>
</template>
<script>
export default {
    name: "NucleicAcidTest",
    data() {
        return {
            btnDisabled: true,
            dataSource: "",
            patName: "",
            patId: "",
            patMedNo: "",
            originalResult: [],
            result: [],
            checkAll: false,
            totalConsume: "0.00",
            stepper: [],
            attach: "",
            checkedColor: [],
        };
    },
    mounted() {
        this.patName = this.$route.query.patName;
        this.patId = this.$route.query.patId;
        this.patMedNo = this.$route.query.patMedNo;
        this.queryItemPaymentInfo();
    },
    methods:{
        handleChkClick(value) {
            if (value != undefined && `${value}`.includes("chk-")) {
                let curIsCheck = this.dataSource[value.split("-")[1]].isCheck;
                if (curIsCheck == 1) {
                    this.$toast({
                        message: "该项不可取消",
                        duration: 2000,
                        forbidClick: false
                    });
                    this.result.push(value);
                }
            };
            let totalConsume = 0;
            let totalItemNo = "";
            this.result.forEach((curVal) =>{
                let dataIndex = curVal.split("-")[1];
                let curAmount = Number(this.dataSource[dataIndex].amount).toFixed(2);
                let curStepperNum = this.stepper[dataIndex];
                let curItemNo = this.dataSource[dataIndex].itemNo;
                totalConsume += curAmount*curStepperNum;
                totalItemNo += `${curItemNo},${curStepperNum}|`;
            });
            this.totalConsume = totalConsume.toFixed(2);
            this.btnDisabled = (this.totalConsume == 0);
            this.checkAll = (this.result.length == this.dataSource.length);
            this.attach = `8-${this.patMedNo}-${totalItemNo.slice(0, totalItemNo.length - 1)}`;
        },
        handleCheckAll() {
            if (this.checkAll) {
                this.$refs.checkboxGroup.toggleAll(true);
            }
            else {
                this.result = this.originalResult;
            }
            this.handleChkClick();
        },
        handleCharge() {
            this.btnDisabled = true;
            this.$api.outpatient.getJsApi({
                data: {
                    patId: this.patId,
                    body: "核酸检测",
                    fee: this.totalConsume,
                    detail: "核酸检测",
                    visitNo: this.patMedNo,
                    attach: this.attach

                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = this.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        this.globalMethods.aliPay(resData);
                    }
                    else if (systemType == "0B") {
                        this.globalMethods.wxPay(resData, () => {
                            this.btnDisabled = false;
                        });
                    }
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            });
        },
        queryItemPaymentInfo() {
            this.$api.outpatient.queryItemPaymentInfo({
                data: {}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = this.wrapData(response.data.Data);
                    this.handleChkClick();
                }
            })
        },
        wrapData(data) {
            let result = [];
            let checkedColor = [];
            let newData;
            if (data) {
                newData = data.map((curVal, index) => {
                    this.stepper.push(1);
                    if (curVal.isCheck == 1) {
                        result.push(`chk-${index}`);
                        checkedColor.push("#96c8fa");
                    }
                    else {
                        checkedColor.push("#1989fa");
                    }
                    return curVal;
                })
            }
            else {
                newData = data; 
            }
            
            this.result = result;
            this.originalResult = result;
            this.checkedColor = checkedColor;

            return newData;
        },
    }
};
</script>
<style scoped>
.main-bg >>> .van-cell::after {
    right: 16px;
}
.panel {
    margin: 10px 0;
}
.cell-title-style {
    text-align: left;
    color: black;
}
.cell-value-style {
    color: black;
}
.balance {
    color: black;
}
.balance-label {
    text-align: left;
    color: #9e9e9e;
}
.pay-btn {
    padding: 10px;
    text-align: center;
}
.chk-item {
    padding: 8px 0;
}
.chk-text {
   text-align: right;
   color: #0078FF;
}

</style>


