import instance from '../http';
import loginUrl from '../url/login';
const login = {
    getAuthUrl(config) {
        //获取authUrl
        return instance(loginUrl.getAuthUrl, config);
    },
    getToken(config) {
        //获取token
        return instance(loginUrl.getToken, config);
    },
}

export default login;


