<template>
  <div class="container main-bg">
    <van-form @submit="handleSubmit" :show-error-message="false">
        <div class="radius-10">
            <!-- 姓名 -->
            <van-field
                name="patName"
                v-model="patName"
                label="姓名"
                label-width="120"
                placeholder="请输入患者姓名"
                :rules="[{ required: true, message: '请输入患者姓名' }]"
                clearable
            />
            <!-- 住院号 -->
            <van-field
                name="inHosNo"
                v-model="inHosNo"
                label="住院号"
                placeholder="请输入住院号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入住院号' }]"
                @blur="handleInHosNoBlur"
                clearable
            />
            <!-- 身份证号码 -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                label="身份证号"
                placeholder="请输入患者身份证号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入患者身份证号' }]"
                clearable
            />
            <!-- 手机号 -->
            <van-field
                name="patPhone"
                v-model="patPhone"
                label="手机号"
                placeholder="请输入患者手机号"
                label-width="120"
                maxlength="11"
                :rules="[{ required: false, message: '请输入患者手机号' }]"
                clearable
            />
        </div>
        <!-- 提示 -->
        <div class="tips">
            <span class="strong">温馨提示：</span><span class="normal">输入的【姓名】和【身份证号】必须和住院登记处预留的个人信息完全一致，才能绑定成功</span>
        </div>
        <base-button class="m-t-20" text="确认绑定" size="large" native-type="submit" :btnDisabled="btnDisabled" />
    </van-form>
  </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "BindInHosNo",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            inHosNo: "",
            patName: "",
            patIdNo: "",
            patPhone: "",
            btnDisabled: false,
        };
    },
    methods: {
        handleSubmit(values) {
            //valid the form of patIdNo
            if (!this.validForm(values)) {
                return;
            };
            this.btnDisabled = true;
            let data = {};
            data["inHosNo"] = this.wrapInHosNo(values.inHosNo);
            data["patName"] = values.patName;
            data["patIdNo"] = values.patIdNo;
            data["patPhone"] = values.patPhone;
            this.$api.inHosNo.bindPatientInfo({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("bindedInHosNo", true);
                    this.toastSuccess("绑定成功");
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        handleInHosNoBlur() {
            this.inHosNo = this.wrapInHosNo(this.inHosNo);
        },
        wrapInHosNo(inHosNo) {
            let inHosNoLen = inHosNo.length;
            if (inHosNoLen > 0 && inHosNoLen < 8) {
                return inHosNo.padStart(8, "0");
            }
            return inHosNo;
        },
        validForm(values) {
            if (!values.inHosNo && !values.patIdNo && !values.patPhone) {
                this.$toast({
                    message: "住院号、身份证号、手机号不能同时为空",
                    forbidClick: false
                });
                return false;
            };
            if (values.patIdNo) {
                if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                    this.$toast({
                        message: "身份证号格式错误",
                        forbidClick: false
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            if (values.patPhone) {
                if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                    this.$toast({
                        message: "手机号格式错误",
                        forbidClick: false
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            
            return true;
        },
        toastFail(msg) {
            this.btnDisabled = false;
            this.$toast({
                message: msg,
                forbidClick: false,
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        }
    }
};
</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
</style>



