<template>
    <div class="main-bg">
        <com-manage-card class="m-b-15" v-for="(item, index) in dataSource" :key="'card' + index"  :data="item" @show-code="handleGoCodeDetailPage(item.patId)" @go-detail="handleGoCardDetailPage(item.patId)"></com-manage-card>
        <div class="add-card-btn flex_dom flex_item_center flex_item_mid" @click="handleAddCard">
            <div class="add-btn pr">
                <span class="abs-mid cross1"></span>
                <span class="abs-mid cross2"></span>
            </div>
            <span class="font16 color-fff">添加就诊人</span>
        </div>
    </div>
</template>
<script>
import CManageCard from "../../common/CManageCard";
export default {
    name: "ManageCard",
    //注册组件
    components: {
        [CManageCard.name]: CManageCard
    },
    data() {
        return {
            dataSource: [],
        };
    },
    computed: {
        sociaSecuCard() {
            return this.$store.state.sociaSecuCard;
        }
    },
    mounted() {
        this.$api.outpatient.queryCardInfo({
            data: {patId: ""},
            resLoading: true,
        }).then((response) => {
            if (response.data.Code == 0) {
                this.dataSource = this.wrapData(response.data.Data);
            }
            else if (response.data.Code != -2) {
                // 如果解绑了所有的卡要把binded改为false
                this.globalMethods.updateStatus("binded", false);
            }
        })
    },
    methods: {
        handleGoCardDetailPage(patId) {
            this.$router.push({name: "CardDetail", query: {patId}});
        },
        handleGoCodeDetailPage(patId) {
            this.$router.push({name: "CodeDetail", query: {patId}});
        },
        handleAddCard() {
            let name = this.sociaSecuCard ? "SelectBindType" : "BindCard";
            this.$router.push({name});
        },
        wrapData(data) {
            let dataArr = data;
            for (let i = 0; i < dataArr.length; i++) {
                let item = dataArr[i];
                let arrItem = item.patMedNo.split("");
                arrItem.splice(5, 8, "****");
                item.patMedNo =  arrItem.join("");
            };
            return dataArr;
        }
    }
};

</script>
<style scoped>
.add-card-btn {
    width: 100%;
    height: 43px;
    background-image: linear-gradient(to right, #00CAFF , #2872FF);
    border-radius: 4px;
    margin: 20px auto;
}
.add-btn {
    width: 44px;
    height: 100%;
}
.add-btn>span {
    border-radius: 1.5px;
    background-color: #fff;
}
.add-btn .cross1 {
    width: 20px;
    height: 3px;
}
.add-btn .cross2 {
    width: 3px;
    height: 20px;
}
</style>



