<template>
    <div class="main-bg">
        <div class="radius-10 m-b-10" v-for="(item, index) in dataSource" :key="`list${index}`">
            <div class="content">
                <van-row class="order-state">
                    <van-col class="size1" span="12">
                        <van-tag plain type="primary">{{item.invoiceFlagLabel}}</van-tag>
                    </van-col>
                    <van-col :class="{color1: (item.invoiceStatus == 0 || item.invoiceStatus == 1), color2: item.invoiceStatus == 3, color3:item.invoiceStatus == 2}" span="12" @click="handleGoAction(index)">{{item.actionLabel}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>就诊人</span>
                    </van-col>
                    <van-col span="12">{{item.patName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>就诊卡号</span>
                    </van-col>
                    <van-col span="12">{{item.patMedNo}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>就诊科室</span>
                    </van-col>
                    <van-col span="12">{{item.deptName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>就诊医生</span>
                    </van-col>
                    <van-col span="12">{{item.doctorName}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>就诊时间</span>
                    </van-col>
                    <van-col span="12">{{item.visitTime}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="12">
                        <span>开票时间</span>
                    </van-col>
                    <van-col span="12">{{item.operateTime}}</van-col>
                </van-row>
                <div class="consume">
                    <span>消费金额：{{Number(item.consume).toFixed(2)}}元</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "OutpInvoiceList",
    data() {
        return {
            visitNo: "",
            dataSource: [],
        };
    },
    mounted() {
        this.visitNo = this.$route.query.visitNo;
        this.queryInvoiceList();
    },
    methods: {
        handleGoAction(index) {
            let curItem = this.dataSource[index];
            let invoiceFlag = curItem.invoiceFlag;
            let operationModel = curItem.operationModel;
            if (invoiceFlag != 0) {
                if (operationModel == 0) {
                    window.open(curItem.invoiceUrl)
                }
                else {
                    window.open(`/static/pdf/web/viewer.html?file=${encodeURIComponent(curItem.invoiceUrl)}`)
                }
            }
            else {
                this.makeOutInvoice(curItem.serialNo);
            }
        },
        queryInvoiceList() {
            this.$api.outpatient.queryInvoiceList({
                data: {visitNo: this.visitNo}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = this.wrapData(response.data.Data);
                }
            })
        },
        makeOutInvoice(serialNo) {
            this.$api.outpatient.makeOutInvoice({
                data: {serialNo}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.$toast({
                        message: "已插入开票队列，请稍后再来查看",
                        duration: "2000",
                        forbidClick: true
                    });
                }
            })
        },
        wrapData(data) {
            if (data) {
                return data.map((curVal) => {
                    curVal.invoiceFlagLabel = this.$store.state.locale[`invoiceFlag-${curVal.invoiceFlag}`];
                    curVal.actionLabel = this.$store.state.locale[`invoiceStatus-${curVal.invoiceStatus}`];
                    return curVal;
                })
            }
            else {
                return data;
            }
        }
    }
};
</script>
<style scoped>
.content {
    background-color: #fff;
    padding: 10px 15px;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.content .consume {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
    padding: 10px 0;
    border-top: 0.5px solid #e2d7d7;
}
.size1 {
    font-size: 14px !important;
}
.color1 {
    color: #0078ff!important;
}
.color2 {
    color:#ce8e1b !important;
}
.color3 {
    color: #ff6257 !important;
}

</style>


