<template>
    <div class="main-bg">
        <div class="title">设置默认住院号</div>
        <div class="content">
            <div class="card pr" v-for="(item, index) in dataSource" :key="'inHosNo' + index" @click="handleSetDefault(item.inHosNo, index)">
                <div class="wrap">
                    <div class="row">
                        <span class="name">{{item.patName}}</span>
                    </div>
                    <div class="row card-code">
                        <span>住院号 :</span>
                        <span>{{item.inHosNo}}</span>
                    </div>
                </div>
                <div class="default-icon pa" v-if="item.isDefault == 0">默认</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "InHosNoList",
    data() {
        return {
            dataSource: ""
        };
    },
    mounted() {
        this.init();
    },
    methods:{
        init() {
            this.$api.inHosNo.queryPatientInfoList({
                data: {inHosNo: ""}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = response.data.Data;
                }
            })
        },
        handleSetDefault(inHosNo, index) {
            this.$api.inHosNo.updatePatientInfo({
                data: {
                    isDefault: 0,
                    inHosNo
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSource.forEach(function(val, curIndex) {
                        val.isDefault = (index == curIndex) ? 0 : 1;
                    });
                    this.$toast({
                        type: "success",
                        duration: 2000,
                        message: "设置成功",
                        forbidClick: true,
                        onClose: () => {
                            this.$router.go(-1);
                        }
                    })
                }
            })
        },
    },
};
</script>
<style scoped>

.title {
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
}
.content {
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.card {
    width: 98%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.card .wrap {
    height: 100%;
}
.wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.wrap .name {
    color: #292C34;
}
.wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
.default-icon {
    width: 48px;
    height: 18px;
    line-height: 18px;
    border-radius: 10px 0 10px 0 / 10px 0 10px 0;
    background-image: linear-gradient(#00CAFF, #2872FF);
    font-size: 10px;
    color: #fff;
    text-align: center;
    right: 0;
    bottom: 0;
}
</style>


