<template>
    <div class="main-bg">
        <div class="select-box clear">
           <van-cell class="patient" :border="false" :title="patName + ' ' + patMedNo" title-style="text-align: left;" is-link @click="handleShowSelectCard"/>
           <van-cell class="date fr"  is-link arrow-direction="down" :title="year + '年' + month + '月'" title-style="text-align: right;" @click="handleShowDatePicker"/>
        </div>
        <div class="wrapper">
            <div class="radius-10 m-b-10" v-for="(item, index) in dataSource" :key="'list' + index">
                <div class="content">
                    <van-row class="order-state">
                        <van-col class="font14" span="20">门诊号：{{item.visitNo}}</van-col>
                        <van-col class="color-0078ff" span="4" @click="handleGoDetail(item.visitNo, item.visitTime)">明细 ></van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-keshi.png')" />
                            <span>就诊科室</span>
                        </van-col>
                        <van-col span="12">{{item.deptName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-yisheng.png')" />
                            <span>就诊医生</span>
                        </van-col>
                        <van-col span="12">{{item.doctorName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-riqi.png')" />
                            <span>就诊时间</span>
                        </van-col>
                        <van-col span="12">{{item.visitTime}}</van-col>
                    </van-row>
                </div>
            </div>
            <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
        </div>
        <van-popup class="select-card-modal radius-10" v-model="showPopup">
            <div class="title">请选择就诊人</div>
            <div class="popup">
                <div class="content">
                    <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="handleClosePopup(item.patId, item.patName, item.patMedNo)">
                        <div class="wrap">
                            <div class="row">
                                <span class="name">{{item.patName}}</span>
                            </div>
                            <div class="row card-code">
                                <span>卡号 :</span>
                                <span>{{item.patMedNo}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
        <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
            <van-datetime-picker
            v-model="currentDate"
            type="year-month"
            title="选择查询日期"
            @confirm="handleDateComfirm"
            @cancel="handleDateCancel"
            />
        </van-popup>
    </div>
</template>
<script>
export default {
    name: "DayList",
    data() {
        return {
            patId: "",
            patName: "",
            patMedNo: "",
            showPopup: false,
            cardList: "",
            dataSource: [],
            showDatePicker: false,
            currentDate: new Date(),
            year: new Date().getFullYear(),
            month: new Date().getMonth() + 1,
        };
    },
    beforeRouteLeave (to, from, next) {
        // 若是去Account页面，清空keep-alive缓存，防止下次进入该页面还展示上次的缓存
        if (to.name == 'Account') {
            this.$vnode.parent.componentInstance.cache = {};
            this.$vnode.parent.componentInstance.keys = [];
        }
        next();
    },
    mounted() {
        let owner = this;
        this.$api.outpatient.queryCardInfo({
            data: {patId: ""},
            reqLoading: false,
            resToastClear: false,
        }).then((response) => {
            if (response.data.Code == 0) {
                let data = response.data.Data;
                for (let i = 0; i < data.length; i++)　{
                    let item = data[i];
                    if (item.isDefault == 0) {
                        owner.patId = item.patId;
                        owner.patName = item.patName;
                        owner.patMedNo = item.patMedNo;
                        owner.queryMedicalRecordList();
                        break;
                    }
                };
                owner.cardList = response.data.Data;
            }
        })
    },
    methods: {
        handleShowSelectCard() {
            this.showPopup = true;
        },
        handleClosePopup(patId, patName, patMedNo) {
            this.showPopup = false;
            this.patId = patId;
            this.patName = patName;
            this.patMedNo = patMedNo;
            this.queryMedicalRecordList();
        },
        handleShowDatePicker() {
            this.showDatePicker = true;
        },
        handleDateComfirm(val) {
            this.showDatePicker = false;
            this.year = val.getFullYear();
            this.month = val.getMonth() + 1;
            this.queryMedicalRecordList();
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleGoDetail(visitNo, visitTime) {
            this.$router.push({name: "DayListDetail", query: {visitNo, visitTime}});
        },
        queryMedicalRecordList() {
            let owner = this;
            let year = this.year + "";
            let month = this.month + "";
            let queryDate = year + "-" + (month.length > 1 ? month : "0" + month);
            this.$api.outpatient.queryMedicalRecordList({
                data: {
                    patId: owner.patId,
                    queryDate
                },
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    owner.dataSource = data;
                }
                else if (response.data.Code != -2) {
                    owner.dataSource = [];
                }
            })
        }
    }
};

</script>
<style scoped>
.select-box {
    height: 16vh;
}
.wrapper {
    height: 81vh;
    overflow: scroll;
}
.patient {
    background-color: #0078ff;
    color: #fff;
}
.patient .van-icon {
    color: #fff;
}
.date {
    width: 130px;
    background-color: transparent;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.content {
    background-color: #fff;
    padding: 0 15px 10px 15px;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .van-row .van-col .van-icon {
    margin-right: 15px;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
</style>



