import instance from '../http';
import inHosNoUrl from '../url/inHosNo';
const inHosNo = {
    queryInHospitalCertificate(config) {
        //查询入院登记证信息
        return instance(inHosNoUrl.queryInHospitalCertificate, config);
    },
    inHospitalRegister(config) {
        //入院登记
        return instance(inHosNoUrl.inHospitalRegister, config);
    },
    bindPatientInfo(config) {
        //绑定住院患者信息
        return instance(inHosNoUrl.bindPatientInfo, config);
    },
    queryPatientInfoList(config) {
        //查询绑定的住院患者信息列表
        return instance(inHosNoUrl.queryPatientInfoList, config);
    },
    queryPatientInfoByID(config) {
        //根据住院号查询患者信息
        return instance(inHosNoUrl.queryPatientInfoByID, config);
    },
    unbindPatientInfo(config) {
        //解绑住院患者信息
        return instance(inHosNoUrl.unbindPatientInfo, config);
    },
    updatePatientInfo(config) {
        //更新住院患者信息
        return instance(inHosNoUrl.updatePatientInfo, config);
    },
    queryInHosList(config) {
        //查询住院清单
        return instance(inHosNoUrl.queryInHosList, config);
    },
    queryInHosPayRecord(config) {
        //查询住院缴费记录
        return instance(inHosNoUrl.queryInHosPayRecord, config);
    },
    queryInHosSettleInfo(config) {
        //查询结算信息
        return instance(inHosNoUrl.queryInHosSettleInfo, config);
    },
}

export default inHosNo;


