<template>
    <div class="main-bg">
        <div class="radius-10">
            <div class="title linear">门诊号：{{visitNo}}</div>
            <van-cell title-style="text-align: left;" title="姓名" :value="dataSource.patName" />
            <van-cell title-style="text-align: left;" title="性别" :value="dataSource.patSex && (dataSource.patSex == 1 ? '男' : '女')" />
            <van-cell title-style="text-align: left;" title="就诊科室" :value="dataSource.deptName" />
            <van-cell title-style="text-align: left;" title="就诊医生" :value="dataSource.doctorName" />
            <van-cell title-style="text-align: left;" title="诊断" :value="dataSource.diagnosis"/>
        </div>
        <div class="grid" v-if="dataSource.details">
            <van-row class="row">
                <van-col span="8">项目名称</van-col>
                <van-col span="4">数量</van-col>
                <van-col span="4">单位</van-col>
                <van-col span="4">价格(元)</van-col>
                <van-col span="4">费用合计(元)</van-col>
            </van-row>
            <van-row class="row" v-for="(item, index) in dataSource.details" :key="'row' + index">
                <van-col span="8">{{item.itemName}}</van-col>
                <van-col span="4">{{item.number && Number(item.number).toFixed(2)}}</van-col>
                <van-col span="4">{{item.unit}}</van-col>
                <van-col span="4">{{item.price && Number(item.price).toFixed(2)}}</van-col>
                <van-col span="4">{{item.consume && Number(item.consume).toFixed(2)}}</van-col>
            </van-row>
            <van-row class="row">
                <van-col span="8">总计</van-col>
                <van-col span="4"> - </van-col>
                <van-col span="4"> - </van-col>
                <van-col span="4"> - </van-col>
                <van-col span="4">{{total}}</van-col>
            </van-row>
        </div>
    </div>
</template>
<script>
export default {
    name: "DayListDetail",
    data() {
        return {
            dataSource: "",
            total: "",
        };
    },
    computed: {
        visitNo() {
            return this.$route.query.visitNo;
        },
        visitTime() {
            return this.$route.query.visitTime;
        }
    },
    mounted() {
        this.queryConsumeList();
    },
    methods: {
        queryConsumeList() {
            let owner = this;
            this.$api.outpatient.queryConsumeList({
                data: {
                    visitNo: owner.visitNo,
                    visitTime: owner.visitTime
                }
            }).then((response) => {
                    if (response.data.Code == 0) {
                        owner.dataSource = owner.wrapData(response.data.Data);
                        setTimeout(() => {owner.perfectGrid();}, 100);
                    }
                });
        },
        wrapData(data) {
            let total = 0;
            for (let i = 0; i < data.details.length; i++) {
                let item = data.details[i];
                total +=  parseFloat(Number(item.consume).toFixed(2));
            }
            this.total = Number(total).toFixed(2);

            return data;
        },
        perfectGrid() {
            let rowEle = $(".row");
            for (let i = 0; i < rowEle.length; i++) {
                let item = rowEle.eq(i);
                let height = item.height();
                let cols = item.find(".van-col");
                for (let k = 0; k < cols.length; k++) {
                    cols.eq(k).css("height", height);
                }
            };
        }
    }
};

</script>
<style scoped>
.title {
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    background-color: #0078ff;
}
.grid {
    width: 95%;
    margin: 10px auto;
    padding: 0 1px 1px 0;
    border-bottom: 0.5px solid #a5a5a5;
    border-right: 0.5px solid #a5a5a5;
}
.grid .van-row:nth-child(2n-1) {
    background-color: #ececec;
}
.grid .van-row:nth-child(2n) {
    background-color: #fff;
}
.grid .van-row .van-col {
    min-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 0.5px solid #a5a5a5;
    border-left: 0.5px solid #a5a5a5;
}
.grid .van-row .van-col:first-child {
    padding-left: 3px;
    justify-content: flex-start;
    text-align: left;
}
</style>



