import axios from 'axios';
import { Toast } from 'vant';


// 提示函数 
// 禁止点击蒙层、显示一秒后关闭
const loading = (forbidClick = true) => {
    Toast.loading({
        message: '加载中...',
        duration: 0,
        forbidClick
    });
}

export function all (...request) {
    loading();
    return axios.all(request).then(axios.spread(function (...response) {
        // 两个请求现在都执行完成
        Toast.clear();
        return response;
    }));
}


