<template>
    <div class="main-bg">
        <van-row class="doctor-summary radius-10">
            <van-col class="pic" span="6">
                <van-image width="70" height="70" round :src="dataSource.doctorAvatar" />
            </van-col>
            <van-col class="des" span="18">
                <div><span class="name strong">{{dataSource.doctorName}}</span>{{dataSource.doctorTitle}}</div>
                <div>所属科室： <span class="strong">{{dataSource.deptName}}</span></div>
            </van-col>
        </van-row>
        <van-tabs 
            class="detail-type radius-10"
            v-model="active"
            :swipeable="true"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
          >
            <van-tab title="排班信息">
                <template #title>
                    <span class="custom-tab-title">排班信息</span>
                </template>
                <van-row class="schedule-title">
                    <van-col span="6">
                        <van-icon size="20" :name="require('../../../assets/images/img/riqi.png')" />
                        <span>日期</span>
                    </van-col>
                    <van-col span="9">
                        <van-icon size="20" :name="require('../../../assets/images/img/shangwu.png')" />
                        <span>上午</span>
                    </van-col>
                    <van-col span="9">
                        <van-icon size="20" :name="require('../../../assets/images/img/xiawu.png')" />
                        <span>下午</span>
                    </van-col>
                </van-row>

                <van-row class="schedule-content active" v-for="(item, index) in dataSource.details" :key="'detail' + index">
                    <van-col class="date" span="6">
                        {{item.date.slice(5) + " " + item.week}}
                    </van-col>
                    <van-col class="am" span="9" @click="handleGoRegisterDetail(item.statusAM, item.date, 0, item.amNotAppoint, item.timeSpanAMList)">
                        <span class="active1-item" v-if="item.amNotAppoint">不可预约 ({{item.totalNumAM - item.availableNumAM}}/{{item.totalNumAM}})</span>
                        <span v-else-if="item.statusAM == 0">预约 ({{item.totalNumAM - item.availableNumAM}}/{{item.totalNumAM}})</span>
                        <span class="active1-item" v-else-if="item.statusAM == 1">约满 ({{item.totalNumAM}}/{{item.totalNumAM}})</span>
                        <span class="active2-item" v-else>未坐诊</span>
                    </van-col>
                    <van-col class="pm" span="9"  @click="handleGoRegisterDetail(item.statusPM, item.date, 1, item.pmNotAppoint, item.timeSpanPMList)">
                        <span class="active1-item" v-if="item.pmNotAppoint">不可预约 ({{item.totalNumPM - item.availableNumPM}}/{{item.totalNumPM}})</span>
                        <span v-else-if="item.statusPM == 0">预约 ({{item.totalNumPM - item.availableNumPM}}/{{item.totalNumPM}})</span>
                        <span class="active1-item" v-else-if="item.statusPM == 1">约满 ({{item.totalNumPM}}/{{item.totalNumPM}})</span>
                        <span class="active2-item" v-else>未坐诊</span>
                    </van-col>
                </van-row>
            </van-tab>
            <van-tab title="医生介绍">
                <div class="doctor-introduce">
                    <div class="row2">
                        <van-icon size="20" :name="require('../../../assets/images/img/shanchang.png')" />
                        <span>简介</span>
                    </div>
                    <div class="row3">{{dataSource.doctorDes}}</div>
                </div>
                
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
export default {
    name: "DoctorDetail",
    data() {
        return {
            active: 0,
            dataSource: ""
        };
    },
    computed: {
        type() {
            return this.$route.query.type;
        },
        hasTimeRange() {
            if (this.isCurrent) {
                return false;
            }
            else {
                return this.$store.state.timeRange;
            }
        },
        isCurrent() {
            return this.$route.query.type == 0;
        },
    },
    mounted() {
        let owner = this;
        let querys = this.$route.query;
        let deptNo = querys.deptNo;
        let doctorNo = querys.doctorNo;
        let type = this.type;
        let outpatientApis = this.$api.outpatient;
        let targetApi = this.hasTimeRange ? outpatientApis.queryDoctorDetailTime : outpatientApis.queryDoctorDetailAM;
        targetApi({
            data: {deptNo, doctorNo, type},
            resLoading: true,
        }).then((response) => {
            if (response.data.Code == 0) {
                owner.dataSource = owner.wrapData(response.data.Data);
            }
        })
    },
    methods: {
        handleGoRegisterDetail(status, date, timeFlag, notAppoint, timeRange) {
            let data = {};
            data["deptName"] = this.dataSource.deptName;
            data["deptNo"] = this.dataSource.deptNo;
            data["doctorName"] = this.dataSource.doctorName;
            data["doctorNo"] = this.dataSource.doctorNo;
            data["registerFee"] = this.dataSource.registerFee;
            data["clinicFee"] = this.dataSource.clinicFee;
            data["appointDate"] = date;
            data["timeFlag"] = timeFlag;
            if (this.hasTimeRange && timeRange) {
                data["timeRange"] = this.wrapTimeRange(timeRange);
            };
            if (status == 0 && !notAppoint) {
                let type = this.type;
                this.$router.push({name: "RegisterDetail", params: {data}, query:  {type, timeFlag}}).catch(()=>{});
            }
        },
        wrapData(data) {
            if (this.type == 0) {
                // 如果是当日挂号，限制上午最晚可预约时间和下午最晚可预约时间
                let curDate = new Date();
                let curYear = curDate.getFullYear();
                let curMonth = curDate.getMonth();
                let curDay = curDate.getDate();
                let curTime = curDate.getTime();
                let amTimeArr = this.$store.state.amLastAppointTime.split(":");
                let pmTimeArr = this.$store.state.pmLastAppointTime.split(":");
                let amLastAppointTime = new Date(curYear, curMonth, curDay, amTimeArr[0], amTimeArr[1]).getTime();
                let pmLastAppointTime = new Date(curYear, curMonth, curDay, pmTimeArr[0], pmTimeArr[1]).getTime();
                for (let i = 0; i < data.details.length; i++) {
                    let item = data.details[i];
                    if (curTime - amLastAppointTime >= 0) {
                        item.amNotAppoint = true;
                    }
                    if(!item.amNotAppoint || curTime - pmLastAppointTime >= 0) {
                        item.pmNotAppoint = true;
                    }
                }
            }
            return data;
        },
        wrapTimeRange(timeRange) {
            let timeRangeArr = [];
            let timeRangeObjArr = [];
            for (let i = 0; i < timeRange.length; i++) {
                let item = timeRange[i];
                let startTime = item.clinicTime.replace(" ", "");
                let endTime = item.clinicTimeEnd.replace(" ", "");
                let isDisabled = item.availableNum <= 0;
                startTime = startTime.length == 5 ? startTime : "0" + startTime;
                endTime = endTime.length == 5 ? endTime : "0" + endTime;
                let timeItem = startTime + "-" + endTime;
                if (!timeRangeArr.includes(timeItem)) {
                    timeRangeArr.push(timeItem);
                    timeRangeObjArr.push({time: timeItem, isDisabled});
                }
            }
            return timeRangeObjArr;
        },
    }
};

</script>
<style scoped>
.doctor-summary {
    background-color: #fff;
    padding: 0px 10px;
    height: 100px;
    margin-bottom: 10px;
}
.doctor-summary .van-col {
    height: 100%;
    padding: 15px 0;
}
.doctor-summary .pic {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.doctor-summary .des {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    font-size: 14px;
    color: #88929d;
    padding-left: 10px;
}
.doctor-summary .des .name {
    margin-right: 20px;
}
.doctor-summary .des .strong{
    color: black;
}
.schedule-title {
    height: 40px;
}
.schedule-title .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
}
.schedule-title .van-col>span {
    font-size: 13px;
}
.schedule-content {
    height: 40px;
    margin-top: 10px;
}
.schedule-content .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
    font-size: 12px;
}
.schedule-content .van-col>span {
    height: 40px;
    line-height: 40px;
    width: 110px;
    text-align: center;
    color: #0078FF;
    background-color: #fff;
}
.schedule-content.active .am>span,
.schedule-content.active .pm>span {
    color: #fff;
    background-color: #0078FF;
    box-shadow: 0px 0px 5px #0aaaf3;
}
.schedule-content.active .date {
    color: #0078FF;
}
.schedule-content span.active1-item {
    background-color: #d6d9da !important;
    color: #58626c !important;
    box-shadow: none !important;
}
.schedule-content span.active2-item {
    background: #d6d9da url("../../../assets/images/img/tiaowen_bg.png") no-repeat!important;
    color: #58626c !important;
    box-shadow: none !important;
}
.doctor-introduce {
    padding: 10px;
}
.doctor-introduce>div {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}
.doctor-introduce .van-icon{
    margin-right: 10px;
}
.doctor-introduce .row2>span{
    font-size: 13px;
    color: #88929d;
}
.doctor-introduce .row3 {
    text-align: left;
    color: black;
    font-size: 14px;
    margin-top: 20px;
    line-height: 20px;
}
.detail-type>>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.detail-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
</style>



