<template>
    <div class="charge main-bg">
        <van-form @submit="handleGoCharge" :show-error-message="false">
            <van-field
                class="radius-10"
                type="number"
                v-model="inHosNo"
                placeholder="请输入住院号"
                :rules="[{ required: true, message: '请输入住院号' }]"
                center
                clearable
            />
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <base-button class="m-t-20" text="确定" native-type="submit" :btnDisabled="btnDisabled" size="large" />
        </van-form>
    </div>
</template>

<script>
import BButton from "../../base/BButton";
export default {
    name: "QueryOthersInHosNo",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            inHosNo: "",
            btnDisabled: false
        };
    },
    methods: {
        handleGoCharge() {
            let systemType = this.globalMethods.getCookie("systemType");
            let payType = systemType == "0A" ? "支付宝" : "微信";
            this.$dialog.confirm({
                title: '提示',
                confirmButtonText: "继续",
                message: `请使用本人的${payType}进行支付,勿让他人代付，窗口进行退费时，费用将按原支付方式返还到${payType}账户`,
            })
            .then(() => {
                this.queryPatientInfoByID();
            })
            .catch(() =>{});
        },
        queryPatientInfoByID() {
            this.btnDisabled = true;
            this.$api.inHosNo.queryPatientInfoByID({
                data: {
                    inHosNo: this.inHosNo,
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: {
                    forbidClick: false,
                },
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.$router.push({name: "ChargeDeposit", query: {inHosNo: this.inHosNo}});
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            });
        }
    }
};

</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: blue;
}

</style>



