import Vue from 'vue';
import Router from 'vue-router';
import store from '../store/index';
import Tabbar from '@/components/views/Tabbar';
import Default from '@/components/views/Default';
import Transfer from '@/components/views/Transfer';
//////////////////////////////////////门诊服务模块////////////////////////////////////
import Outpatient from '@/components/views/outpatient/Outpatient';
import CardList from '@/components/views/outpatient/CardList';
import SelectBindType from '@/components/views/outpatient/SelectBindType';
import BindCard from '@/components/views/outpatient/BindCard';
import ManageCard from '@/components/views/outpatient/ManageCard';
import CardDetail from '@/components/views/outpatient/CardDetail';
import Charge from '@/components/views/outpatient/Charge';
import Pay from '@/components/views/outpatient/Pay';
import QueryReport from '@/components/views/outpatient/QueryReport';
import ReportDetail from '@/components/views/outpatient/ReportDetail';
import AppointRegister from '@/components/views/outpatient/AppointRegister';
import DoctorList from '@/components/views/outpatient/DoctorList';
import DoctorDetail from '@/components/views/outpatient/DoctorDetail';
import RegisterDetail from '@/components/views/outpatient/RegisterDetail';
import MyRegister from '@/components/views/outpatient/MyRegister';
import CodeDetail from '@/components/views/outpatient/CodeDetail';
import QueryOthersCardByNum from '@/components/views/outpatient/QueryOthersCardByNum';
import QueryOthersCardByName from '@/components/views/outpatient/QueryOthersCardByName';
import DayList from '@/components/views/outpatient/DayList';
import DayListDetail from '@/components/views/outpatient/DayListDetail';
import PayRecord from '@/components/views/outpatient/PayRecord';
import OutpInvoice from '@/components/views/outpatient/OutpInvoice';
import OutpInvoiceList from '@/components/views/outpatient/OutpInvoiceList';
import WaittingDiag from '@/components/views/outpatient/WaittingDiag';
import NucleicAcidTest from '@/components/views/outpatient/NucleicAcidTest';
import AsociaSecuPayResult from '@/components/views/outpatient/AsociaSecuPayResult';
import NucleicAcidTestAppoint from '@/components/views/outpatient/NucleicAcidTestAppoint';
//////////////////////////////////////////住院服务模块///////////////////////////////////////
import Hospital from '@/components/views/hospital/Hospital';
import InHosNoList from '@/components/views/hospital/InHosNoList';
import BindInHosNo from '@/components/views/hospital/BindInHosNo';
import QueryInhosCertificate from '@/components/views/hospital/QueryInhosCertificate';
import InHosRegisterInfo from '@/components/views/hospital/InHosRegisterInfo';
import ManageInhosInfo from '@/components/views/hospital/ManageInhosInfo';
import InhosInfoDetail from '@/components/views/hospital/InhosInfoDetail';
import InHosList from '@/components/views/hospital/InHosList';
import InHosCodeDetail from '@/components/views/hospital/InHosCodeDetail';
import InHosPayRecord from '@/components/views/hospital/InHosPayRecord';
import ChargeDeposit from '@/components/views/hospital/ChargeDeposit';
import QueryOthersInHosNo from '@/components/views/hospital/QueryOthersInHosNo';
import QueryInHosSettleInfo from '@/components/views/hospital/QueryInHosSettleInfo';
/////////////////////////////////////////医院信息模块/////////////////////////////////////
import HospitalInfo from '@/components/views/hospitalInfo/HospitalInfo';
/////////////////////////////////////////我的模块////////////////////////////////////////
import Account from '@/components/views/account/Account';

Vue.use(Router)
let checkBinded = function(to, from, next) {
    let binded = store.state.binded;
    let sociaSecuCard = store.state.sociaSecuCard;
    let name = sociaSecuCard ? "SelectBindType" : "BindCard";
    if (binded) {
        next();
    }
    else {
        next({name});
    }
};
let checkBindedInHosNo = function(to, from, next) {
    let bindedInHosNo = store.state.bindedInHosNo;
    if (bindedInHosNo) {
        next();
    }
    else {
        next({name: "BindInHosNo"});
    }
};

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Tabbar',
            component: Tabbar,
            redirect: function(to) {
                let timeStamp = new Date().getTime();
                return { path: '/outpatient', query: {V: timeStamp} };
            },
            children: [
                {
                    path: 'outpatient',
                    name: 'Outpatient',
                    component: Outpatient,
                    meta: {
                        title: "门诊服务"
                    }
                },
                {
                    path: 'hospital',
                    name: 'Hospital',
                    component: Hospital,
                    meta: {
                        title: "住院服务"
                    }
                },
                {
                    path: 'hospitalInfo',
                    name: 'HospitalInfo',
                    component: HospitalInfo,
                    meta: {
                        title: "医院信息"
                    }
                },
                {
                    path: 'account',
                    name: 'Account',
                    component: Account,
                    meta: {
                        title: "个人中心"
                    }
                }
            ]
        },
        {
            path: '/default',
            name: 'Default',
            component: Default,
            meta: {
                title: "default"
            }
        },
        {
            path: '/transfer',
            name: 'Transfer',
            component: Transfer,
            meta: {
                title: "transfer"
            }
        },
        ////////////////////////////////////////门诊服务模块//////////////////////////////////////////
        {
            path: '/selectBindType',
            name: 'SelectBindType',
            component: SelectBindType,
            meta: {
                title: "选择绑卡方式"
            }
        },
        {
            path: '/bindCard',
            name: 'BindCard',
            component: BindCard,
            meta: {
                title: "绑定就诊卡"
            }
        },
        {
            path: '/cardList',
            name: 'CardList',
            component: CardList,
            meta: {
                title: "设置默认就诊人"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/manageCard',
            name: 'ManageCard',
            component: ManageCard,
            meta: {
                title: "就诊卡管理"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/cardDetail',
            name: 'CardDetail',
            component: CardDetail,
            meta: {
                title: "就诊卡信息"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/charge',
            name: 'Charge',
            component: Charge,
            meta: {
                title: "充值"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/pay',
            name: 'Pay',
            component: Pay,
            meta: {
                title: "缴费"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/asociaSecuPayResult',
            name: 'AsociaSecuPayResult',
            component: AsociaSecuPayResult,
            meta: {
                title: "社保卡支付结果"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/queryReport',
            name: 'QueryReport',
            component: QueryReport,
            meta: {
                title: "报告查询",
                keepAlive: true,//此组件需要被缓存
            },
            beforeEnter: (to, from, next) => {
                let binded = store.state.binded;
                let bindedInHosNo = store.state.bindedInHosNo;
                let type = to.query.type;
                if (type == "0") {
                    if (binded) {
                        next();
                    }
                    else {
                        next({name: "BindCard"});
                    }
                }
                else if (type == "1") {
                    if (bindedInHosNo) {
                        next();
                    }
                    else {
                        next({name: "BindInHosNo"});
                    }
                }
                else {
                    next({name: "Default"});
                }
            }
        },
        {
            path: '/reportDetail',
            name: 'ReportDetail',
            component: ReportDetail,
            meta: {
                title: "报告详情"
            },
        },
        {
            path: '/appointRegister',
            name: 'AppointRegister',
            component: AppointRegister,
            meta: {
                title: "科室信息"
            }
        },
        {
            path: '/doctorList',
            name: 'DoctorList',
            component: DoctorList,
            meta: {
                title: "医生列表"
            }
        },
        {
            path: '/doctorDetail',
            name: 'DoctorDetail',
            component: DoctorDetail,
            meta: {
                title: "医生坐诊详情"
            }
        },
        {
            path: '/registerDetail',
            name: 'RegisterDetail',
            component: RegisterDetail,
            meta: {
                title: "挂号详情"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/myRegister',
            name: 'MyRegister',
            component: MyRegister,
            meta: {
                title: "我的挂号"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/codeDetail',
            name: 'CodeDetail',
            component: CodeDetail,
            meta: {
                title: "二维码详情"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/queryOthersCardByNum',
            name: 'QueryOthersCardByNum',
            component: QueryOthersCardByNum,
            meta: {
                title: "查询他人就诊卡信息"
            },
            beforeEnter: checkBinded
        },
        {
            path: '/queryOthersCardByName',
            name: 'QueryOthersCardByName',
            component: QueryOthersCardByName,
            meta: {
                title: '查询他人就诊卡信息'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/dayList',
            name: 'DayList',
            component: DayList,
            meta: {
                title: '门诊费用查询',
                keepAlive: true,//此组件需要被缓存
            },
            beforeEnter: checkBinded
        },
        {
            path: '/dayListDetail',
            name: 'DayListDetail',
            component: DayListDetail,
            meta: {
                title: '门诊费用明细'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/payRecord',
            name: 'PayRecord',
            component: PayRecord,
            meta: {
                title: '充值记录'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/outpInvoice',
            name: 'OutpInvoice',
            component: OutpInvoice,
            meta: {
                title: '电子发票'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/outpInvoiceList',
            name: 'OutpInvoiceList',
            component: OutpInvoiceList,
            meta: {
                title: '发票列表'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/waittingDiag',
            name: 'WaittingDiag',
            component: WaittingDiag,
            meta: {
                title: '候诊查询'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/nucleicAcidTest',
            name: 'NucleicAcidTest',
            component: NucleicAcidTest,
            meta: {
                title: '核酸检测支付'
            },
            beforeEnter: checkBinded
        },
        {
            path: '/nucleicAcidTestAppoint',
            name: 'NucleicAcidTestAppoint',
            component: NucleicAcidTestAppoint,
            meta: {
                title: '核酸检测预约'
            },
            beforeEnter: checkBinded
        },
        //////////////////////////////////////////住院服务模块///////////////////////////////////////////
        {
            path: '/bindInHosNo',
            name: 'BindInHosNo',
            component: BindInHosNo,
            meta: {
                title: '绑定住院号'
            }
        },
        {
            path: '/inHosNoList',
            name: 'InHosNoList',
            component: InHosNoList,
            meta: {
                title: '设置默认住院号'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/queryInhosCertificate',
            name: 'QueryInhosCertificate',
            component: QueryInhosCertificate,
            meta: {
                title: '住院登记证查询'
            }
        },
        {
            path: '/inHosRegisterInfo',
            name: 'InHosRegisterInfo',
            component: InHosRegisterInfo,
            meta: {
                title: '住院登记信息'
            }
        },
        {
            path: '/manageInhosInfo',
            name: 'ManageInhosInfo',
            component: ManageInhosInfo,
            meta: {
                title: '住院信息管理'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/inhosInfoDetail',
            name: 'InhosInfoDetail',
            component: InhosInfoDetail,
            meta: {
                title: '住院信息详情'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/inHosList',
            name: 'InHosList',
            component: InHosList,
            meta: {
                title: '住院日清单'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/inHosCodeDetail',
            name: 'InHosCodeDetail',
            component: InHosCodeDetail,
            meta: {
                title: '二维码详情'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/inHosPayRecord',
            name: 'InHosPayRecord',
            component: InHosPayRecord,
            meta: {
                title: '住院缴费记录'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/chargeDeposit',
            name: 'ChargeDeposit',
            component: ChargeDeposit,
            meta: {
                title: '续交押金'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/queryOthersInHosNo',
            name: 'QueryOthersInHosNo',
            component: QueryOthersInHosNo,
            meta: {
                title: '查询他人住院信息'
            },
            beforeEnter: checkBindedInHosNo
        },
        {
            path: '/queryInHosSettleInfo',
            name: 'QueryInHosSettleInfo',
            component: QueryInHosSettleInfo,
            meta: {
                title: '结算信息查询'
            }
        },
    ]
})



