<template>
    <div>
        <div class="code-detail-wrap bgimg100 pr">
            <div class="slide-box" ref="slideBox" :class="classObj">
                <div class="slide-item flex_column flex_item_mid flex_item_between pr" v-for="(item, index) in dataInner" :key="'avatar' + index" @click="handleTransferCode(index, item.patId)">
                    <div class="avatar">
                        <img src="../../assets/images/gentleman.png" v-if="item.patSex == 1">
                        <img src="../../assets/images/lady.png" v-if="item.patSex == 2">
                    </div>
                    <span class="name font16 color-fff" :class="{'font-w6': fontIndex == index}">{{item.patName}}</span>
                    <div class="arrow pa" v-if="currentIndex == index">
                        <div class="triangle"></div>
                    </div>
                </div>
            </div>
            <div class="arrow2 pa" :style="arrStyleObj" v-if="currentIndex == 5">
                <div class="triangle"></div>
            </div>
            <div class="qrcode-wrap p-l-15 p-r-15 p-b-10">
                <div class="code p-t-10 p-b-25 flex_column flex_item_mid flex_item_between" :class="codeClassObj">
                    <div class="font17 m-t-10"><span class="color-0078ff font-w6">{{currentPat.patName}}</span><span>的就诊二维码</span></div>
                    <div id="qrcode" ref="qrcode"></div>
                    <div class="flex_dom flex_item_mid font13" @click="handleRefreshCode(currentPat.patId)" v-if="isOutPatient && refresh"><van-icon class="m-r-5" name="replay" size="15" />刷新</div>
                    <div class="flex_dom flex_item_mid font13" v-if="isOutPatient && !refresh"><van-icon class="color-0078ff m-r-5" size="15" name="passed" />已刷新</div>
                </div>
                <div class="info">
                    <div class="title">{{isOutPatient ? "就诊卡信息" : "住院号信息"}}</div>
                    <div class="content">
                        <div class="patmeno">
                            <div class="label">{{isOutPatient ? "卡号" : "住院号"}}</div>
                            <div class="value">{{isOutPatient ? currentPat.patMedNo : currentPat.inHosNo}}</div>
                        </div>
                        <div class="idno">
                            <div class="label">身份证号</div>
                            <div class="value">{{currentPat.patIdNo}}</div>
                        </div>
                        <div class="phone">
                            <div class="label">手机号</div>
                            <div class="value">{{currentPat.patPhone || "未添加"}}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import QRCode from 'qrcodejs2';
export default {
    name: "com-code-detail",
    components: {
        QRCode,
    },
    data() {
        return {
            currentIndex: "",
            currentPat: "",
            arrowLeft: "",
            fontIndex: "",
            promise: null,
            refresh: true,
        };
    },
    props: {
        type: {
            type: [String, Number],
            default: 0
        },
        data: {
            type: Array,
            default() {
                return [
                    {
                        patName: "王先生",
                        patSex: "1",
                        patMedNo: "41132834234207141336",
                        qrcodeStr: "00000000000000000000000",
                        patIdNo: "411328199307141336",
                        patPhone: "13671897043",
                        isDefault: 1,
                    },
                ]
            }
        }
    },
    computed: {
        isOutPatient() {
            return this.type == 0;
        },
        classObj() {
            return {
                "slide-box-flex": this.dataInner.length < 5
            }
        },
        codeClassObj() {
            return {
                "outpatient": this.isOutPatient,
                "hospital": !this.isOutPatient
            }
        },
        arrStyleObj() {
            return {
                "left": this.arrowLeft + "px"
            }
        },
        dataInner() {
            return this.data;
        }
    },
    watch: {
        dataInner: function (val) {
            this.init();
        },
    },
    mounted() {
        this.init();
    },
    methods: {
        init() {
            for (let i = 0; i < this.dataInner.length; i++) {
                let item = this.dataInner[i];
                if (item.isDefault == 0) {
                    this.currentPat = item;
                    this.fontIndex = i;
                    if(this.dataInner.length < 5) {
                        this.currentIndex = i;
                    }
                    else {
                        setTimeout(() => {this.arrowControl(i)})
                    }
                    break;
                }
            }
            this.triggerCreateQrcode();
        },
        triggerCreateQrcode() {
            this.$nextTick (function () {
                let clientWidth = document.documentElement.clientWidth;
                let codeWdith = (clientWidth/375)*232.5;
                let codeHeight = codeWdith;
                this.createQrcode("qrcode", codeWdith, codeHeight);
            });
        },
        createQrcode (idName, width, height) {
            let text = this.isOutPatient ? this.currentPat.qrcodeStr : this.currentPat.inHosNo;
            this.$refs.qrcode.innerHTML = "";
            let qrcode = new QRCode(idName, {
                width,
                height,
                text
            })
        },
        arrowControl(index) {
            let slideBoxEle = this.$refs.slideBox;
            let clientWidth = document.documentElement.clientWidth;
            if (index == 0) {
                this.currentIndex = 5;
                slideBoxEle.scrollLeft = 0;
                this.arrowLeft = (clientWidth/375)*30;
            }
            else if (index == 4) {
                this.currentIndex = 5;
                slideBoxEle.scrollLeft = slideBoxEle.scrollWidth;
                this.arrowLeft = (clientWidth/375)*330;
            }
            else {
                this.currentIndex = index;
            }
        },
        handleTransferCode(index, patId) {
            this.refresh = true;
            if (this.currentIndex != index) {
                this.fontIndex = index;
                if(this.dataInner.length < 5) {
                    this.currentIndex = index;
                }
                else {
                    this.arrowControl(index);
                };
                if (this.isOutPatient) {
                    this.queryQrcodeStr(patId);
                    this.promise.then((value) => {
                        this.currentPat = this.dataInner[index];
                        this.currentPat['qrcodeStr'] = value;
                        this.triggerCreateQrcode();
                    })
                }
                else {
                    this.currentPat = this.dataInner[index];
                    this.triggerCreateQrcode();
                }
                
            };
        },
        handleRefreshCode(patId) {
            this.refresh = false;
            setTimeout(() => {
                this.refresh = true;
            }, 5000)
            this.queryQrcodeStr(patId);
            this.promise.then((value) => {
                this.currentPat['qrcodeStr'] = value;
                this.triggerCreateQrcode();
            })
        },
        queryQrcodeStr(patId) {
            let owner = this;
            owner.promise = new Promise(function(resolve, reject) {
                owner.$api.outpatient.getQrcodeStr({
                    data: {patId},
                    reqLoading: false,
                    resToastClear: false,
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        resolve(response.data.Data)
                    }
                })
            });
        }
    }
};
</script>
<style scoped>
.code-detail-wrap {
    height: 159.5px;
    background: url('../../assets/images/card_bg.png') no-repeat;
}
.slide-box {
    overflow-x: scroll;
    overflow-y: hidden;
    display: -webkit-box;
    -webkit-overflow-scrolling: touch;
    padding-top: 20px;
}
.slide-item {
    width: 80px;
    height: 75px;
    box-sizing: content-box;
    margin-right: 5px;
    padding-bottom: 30px;
}
.slide-box::-webkit-scrollbar {
    width:0;
    height:0;
    display: none;
}
.slide-item .avatar {
    width: 46px;
    height: 46px;
    background-color: #fff;
    border-radius: 23px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.slide-item .avatar img{
    width: 70%;
    height: 80%;
}
.slide-box-flex {
    display: flex;
    justify-content: space-around;
    overflow-x: hidden;
}
.arrow {
    bottom: -5px;
}
.arrow2 {
    bottom: 29px;
}
.triangle {
    display: inline-block;
    font-size: 0;
    overflow: hidden;
    position: relative;
    top: 2px;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
}
.triangle:before {
    content: "";
    position: relative;
    display: inline-block;
    border: 8px solid transparent;
    bottom: 2px;
    border-top-width: 0;
    border-bottom-width: 10px;
    border-bottom-color: #fff;
}
.qrcode-wrap .code {
    background-color: #fff;
    border-radius: 8px;
}
.qrcode-wrap .code.outpatient {
    height: 360px;
}
.qrcode-wrap .code.hospital {
    height: 320px;
}
.info {
    margin: 10px 0;
    background-color: #fff;
    border-radius: 8px;
}
.info .title {
    height: 48px;
    line-height: 48px;
    border-bottom: 1px solid #EEEEEE;
    color: #2A2D34;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
    padding: 0 15px;
}
.info .content {
    padding: 0 15px;
    font-size: 15px;
    color: #4A4E5A;
}
.info .content>div {
    display: flex;
    justify-content: space-between;
    height: 45px;
    line-height: 45px;
}
.info .content .patmeno,
.info .content .idno {
    border-bottom: 1px solid #EEEEEE;
}
.info .content .label {
    width: 30%;
    text-align: left;
}
.info .content .value {
    width: 70%;
    text-align: right;
    font-family: "Roboto-Regular";
}
</style>


