<template>
    <div class="container main-bg">
        <van-form @submit="handleSubmit" :show-error-message="false">
            <div class="radius-10">
                <van-field
                v-model="patName"
                name="patName"
                placeholder="请输入姓名"
                label="姓名"
                label-width="120"
                :rules="[{ required: true, message: '请输入姓名' }]"
                clearable
                ></van-field>
                <van-field
                name="patIdNo"
                v-model="patIdNo"
                type="number"
                label="身份证号"
                placeholder="请输入身份证号"
                label-width="120"
                :rules="[{ required: false, message: '请输入身份证号' }]"
                maxlength="18"
                clearable
                />
                <van-field
                name="patPhone"
                v-model="patPhone"
                type="number"
                label="手机号"
                placeholder="请输入手机号"
                label-width="120"
                :rules="[{ required: false, message: '请输入手机号' }]"
                maxlength="11"
                clearable
                />
            </div>
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <base-button class="m-t-20" text="确定" native-type="submit" :btnDisabled="btnDisabled" size="large" />
            <div class="bottom-tips"><span class="strong" @click="handleGoQueryOthersCardByNum">知道就诊卡号</span></div>
        </van-form>
        <van-popup class="select-card-modal radius-10" v-model="showPopup" @close="handleClosePopup">
            <div class="title">该用户有多张就诊卡，请选择要绑定的就诊卡</div>
            <div class="popup">
                <div class="content">
                    <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="handleGoOthersChargeOrPay(item.patId)">
                        <div class="wrap">
                            <div class="row">
                                <span class="name">{{item.patName}}</span>
                            </div>
                            <div class="row card-code">
                                <span>卡号 :</span>
                                <span>{{item.patMedNo}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "QueryOthersCardByName",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            type: "",
            patName: "",
            patIdNo: "",
            patPhone: "",
            cardList: "",
            btnDisabled: false,
            showPopup: false
        };
    },
    mounted() {
        this.type = this.$route.query.type;
    },
    methods: {
        handleSubmit(values) {
            //valid the form of patIdNo and patPhone
            if (!this.validForm(values)) {
                return;
            };
            this.btnDisabled = true;
            let data = {};
            data["patName"] = values.patName;
            data["patIdNo"] = values.patIdNo;
            data["patPhone"] = values.patPhone;
            this.$api.outpatient.queryCardList({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    if (resData.length == 1) {
                        this.handleGoOthersChargeOrPay(resData[0].patId);
                    }
                    else {
                        this.cardList = resData;
                        this.showPopup = true;
                    }
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                    this.$toast({
                        message: response.data.Msg,
                        forbidClick: false,
                    })
                }
            })
        },
        handleGoQueryOthersCardByNum() {
            let type = this.type;
            this.$router.replace({name: "QueryOthersCardByNum", query: {type}});
        },
        handleClosePopup() {
            this.btnDisabled = false;
        },
        handleGoOthersChargeOrPay(val) {
          let patId = val;
          let name = this.type;
          this.$router.replace({name, query: {patId}});
        },
        validForm(values) {
            if (!values.patIdNo && !values.patPhone) {
                this.$toast({
                    message: "身份证号和手机号不能同时为空",
                    forbidClick: false
                });
                return false;
            };
            if (values.patIdNo) {
                if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                    this.$toast({
                        message: "身份证号格式错误",
                        forbidClick: false
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    message: "手机号格式错误",
                    forbidClick: false
                });
                return false;
            };
            return true;
        }
    }
};
</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
</style>



