<template>
    <div>
        <div class="card-detail-wrap main-bg p-t-25">
            <div class="profile flex_dom flex_item_mid">
                <div class="avatar pr m-r-15">
                    <img class="pa abs-mid-X" src="../../assets/images/gentleman.png" v-if="dataInner.patSex == 1">
                    <img class="pa abs-mid-X" src="../../assets/images/lady.png" v-if="dataInner.patSex == 2">
                    <span class="patSex pa bgimg100" :class="classObj"></span>
                </div>
                <div class="info flex_column flex_item_between color-fff">
                    <div class="name tl font18 font-w6">{{dataInner.patName}}</div>
                    <div class="code font12">
                        <span>{{isOutpatient ? "卡号：" : "住院号："}}</span>
                        <span>{{isOutpatient ? dataInner.patMedNo : dataInner.inHosNo}}</span>
                    </div>
                </div>
            </div>
            <div class="content m-t-25">
                <div class="panel p-l-15 p-r-15 radius-10">
                    <div class="title tl">个人信息</div>
                    <div class="cell border-bottom flex_dom flex_item_between">
                        <span class="label">手机号</span>
                        <span class="value">{{dataInner.patPhone != "" ? dataInner.patPhone : "未添加"}}</span>
                    </div>
                    <div class="cell flex_dom flex_item_between">
                        <span class="label">身份证号</span>
                        <span class="value">{{dataInner.patIdNo}}</span>
                    </div>
                </div>
                <div class="panel p-l-15 p-r-15 m-t-10 radius-10">
                    <div class="title tl">{{isOutpatient ? "就诊卡信息" : "住院号信息"}}</div>
                    <div class="cell border-bottom flex_dom flex_item_between">
                        <span class="label">{{isOutpatient ? "就诊卡余额" : "押金余额"}}</span>
                        <div class="value">
                            <span class="balance">{{isOutpatient ? dataInner.cardBalance : dataInner.deposit}}</span>
                            <span class="unit">元</span>
                            <span class="charge m-l-15" @click="handleOnCharge">去充值</span>
                        </div>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between" v-if="isOutpatient">
                        <span class="label">优惠金额</span>
                        <span class="value">{{dataInner.discountAmount}} 元</span>
                    </div>
                    <div class="cell flex_dom flex_item_between">
                        <span class="label">{{isOutpatient ? "建卡日期" : "入院日期"}}</span>
                        <span class="value">{{isOutpatient ? dataInner.operateDate : dataInner.inHosDate}}</span>
                    </div>
                </div>
                <div class="set-default radius-10 flex_dom flex_item_between flex_item_mid m-t-10 p-l-15 p-r-15">
                    <span>{{isOutpatient ? "设为默认就诊人" : "设为默认住院号"}}</span>
                    <van-switch v-model="checked" size="20" @change="handleOnChange" />
                </div>
                <van-button class="delete radius-10 m-t-20" :disabled="disabled" @click="handleOnDeleteCard">{{isOutpatient ? "删除就诊人" : "删除住院人"}}</van-button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "com-card-detail",
    data() {
        return {
            checked: this.isDefault == 0,
            disabled: this.btnDisabled
        };
    },
    props: {
        type: {
            type: [String, Number],
            default: 0
        },
        data: {
            type: Object,
            default() {
                return {
                    patName: "患者姓名",
                    patSex: "1",
                    patMedNo: "11111111111111",
                    patPhone: "13674848495",
                    patIdNo: "4113281993098765437",
                    cardBalance: "18.4",
                    operateDate: "2020-09-09 08:56:49",
                }
            }
        },
        isDefault: {
            type: [String, Number],
            default: 1
        },
        btnDisabled: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        dataInner() {
            return this.data;
        },
        isOutpatient() {
            return this.type == 0;
        },
        classObj() {
            return {
                gentleman: this.dataInner.patSex == 1,
                lady: this.dataInner.patSex == 2
            }
        },
    },
    watch: {
        isDefault(val) {
            this.checked = val == 0;
        },
        btnDisabled(val) {
            this.disabled = val;
        }
    },
    methods: {
        handleOnChange() {
            let isDefault = this.checked ? "0" : "1";
            this.$emit("switch-change", isDefault);
        },
        handleOnCharge() {
            this.$emit("go-charge");
        },
        handleOnDeleteCard() {
            this.$emit("delete-card");
        },
        toggleSwitch(checked) {
            if (arguments.length > 0) {
                this.checked = checked
            }
            else {
                this.checked = !this.checked;
            }
        }
    }
};
</script>
<style scoped>
.card-detail-wrap {
    background: url('../../assets/images/card_bg.png') no-repeat;
    background-size: 100%;
}
.profile .avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    border: 1px solid #7BB6EC;
    background-color: #E3F4FC;
}
.profile .avatar img {
    width: 70%;
    height: 80%;
    bottom: 0;
}
.profile .avatar .patSex {
    width: 17px;
    height: 16px;
    bottom: 0;
    right: -2px;
}
.profile .avatar .patSex.gentleman {
    background: url('../../assets/images/man.png') no-repeat;
}
.profile .avatar .patSex.lady {
    background: url('../../assets/images/woman.png') no-repeat;
}
.profile .info {
    height: 45px;
}
.content .panel {
    background-color: #fff;
}
.content .panel .title {
    height: 48px;
    line-height: 48px;
    color: #292C34;
    font-size: 18px;
    font-weight: bold;
}
.content .panel .cell {
    height: 46px;
    line-height: 46px;
}
.border-bottom {
    border-bottom: 0.5px solid #EEEEEE;
}
.content .panel .value {
    font-family: "Roboto-Regular";
}
.balance {
    font-size: 18px;
    color: #FF6257;
}
.charge {
    font-size: 13px;
    color: #0078FF;
    padding: 10px;
}
.set-default {
    height: 45px;
    background-color: #fff;
}
.delete {
    width: 100%;
    height: 43px;
    line-height: 43px;
    color: #FF6257;
    font-size: 16px;
    background-color: #fff;
    border: 0.5px solid #FFDDDB;
}
</style>


