import locale from "./locale.js";
const state = {
    // 请求头auth=token
    token: "",
    // 前端必要的字典
    locale,
    //是否绑定就诊卡
    binded: false,
    // 是否绑定住院号
    bindedInHosNo: false,
    //医院信息url
    hospitalInfoUrl: window.g.hospitalInfoUrl,
    // 预约挂号url
    appointRegisterUrl: window.g.appointRegisterUrl,
    // 当日挂号URL
    currentRegisterUrl: window.g.currentRegisterUrl,
    // 候诊查询URL
    waittingDiagUrl: window.g.waittingDiagUrl,
    // 我的挂号URL
    myRegisterUrl: window.g.myRegisterUrl,
    // 预约平台跳转地址
    transferUrl: window.g.transferUrl,
    // 是否可以在线建卡
    allowCreatedCard: window.g.outpatientConfig.allowCreatedCard,
    // 建卡成功提示文案，若配置为""，则显示默认提示文案
    newCardSuccessTips: window.g.outpatientConfig.newCardSuccessTips,
    // 是否显示缴费功能
    personalPay: window.g.outpatientConfig.personalPay,
    // 是否显示预约挂号功能
    appointRegister: window.g.outpatientConfig.appointRegister,
    // 是否显示当日挂号功能
    currentRegister: window.g.outpatientConfig.currentRegister,
    // 挂号提交前的提示文案
    registerSubmitTips: window.g.outpatientConfig.registerSubmitTips,
    // 是否显示我的挂号功能
    myRegister: window.g.outpatientConfig.myRegister,
    // 是否显示分时段功能
    timeRange: window.g.outpatientConfig.timeRange,
    // 挂号预约详细时间段间隔（分钟），支持任意正整数
    appointDetailTimeSpeed: window.g.outpatientConfig.appointDetailTimeSpeed,
    // 预约挂号是否可在线支付
    payAvailable: window.g.outpatientConfig.payAvailable,
    //当日挂号上午预约截止时间
    amLastAppointTime: window.g.outpatientConfig.amLastAppointTime,
     //当日挂号下午预约截止时间
    pmLastAppointTime: window.g.outpatientConfig.pmLastAppointTime,
    //预约挂号完成是否展示预约密码弹窗
    showPassword: window.g.outpatientConfig.showPassword,
    //是否支持社保卡
    sociaSecuCard: window.g.outpatientConfig.sociaSecuCard,
    //门诊/住院是否显示电子票据功能
    invoice: window.g.outpatientConfig.invoice,
    //是否展示候诊查询功能
    waittingDiag: window.g.outpatientConfig.waittingDiag,
    //是否展示核酸检测功能
    nucleicAcidTest:window.g.outpatientConfig.nucleicAcidTest,
    //核酸检测是否可预约
    nucleicAcidTestAppoint: window.g.outpatientConfig.nucleicAcidTestAppoint,
    //核酸检测预约成功后的提示
    nucleicAcidTestAppointSuccessTips:window.g.outpatientConfig.nucleicAcidTestAppointSuccessTips,
    //报告显示结果提示
    reportDetailTips: window.g.outpatientConfig.reportDetailTips,
    //是否显示自助入院功能
    selfHelpInHos: window.g.hospitalConfig.selfHelpInHos,
    //是否显示结算信息查询功能
    settleInfo: window.g.hospitalConfig.settleInfo,
}
export default state;


