<template>
    <div>
        <com-code-detail :type="1" :data="dataSource"></com-code-detail>
    </div>
</template>
<script>
import CCodeDetail from "../../common/CCodeDetail";
export default {
    name: "InHosCodeDetail",
    components: {
        [CCodeDetail.name]: CCodeDetail,
    },
    data() {
        return {
            dataSource: []
        };
    },
    mounted() {
        let inHosNo = this.$route.query.inHosNo;
        this.queryCardInfo(inHosNo);
    },
    methods: {
        queryCardInfo(inHosNo) {
            this.$api.inHosNo.queryPatientInfoList({
                data: {inHosNo: ""},
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    if (inHosNo) {
                        for (let i = 0; i < resData.length; i++) {
                            let item = resData[i];
                            item.isDefault = (item.inHosNo == inHosNo) ? "0" : "1";
                        }
                    }
                    this.dataSource = resData;
                }
            })
        },
    }
};
</script>
<style scoped>

</style>


