<template>
    <div class="main-bg">
            <div class="radius-10 m-b-10" v-for="(item, index) in dataSource" :key="'reg' + index">
                <div class="content">
                    <van-row class="order-state">
                        <van-col class="font12" span="20">订单号：{{item.serialNo}}</van-col>
                        <van-col class="color1" span="4">{{item.statusLabel}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-jiuzhenren.png')" />
                            <span>就诊人</span>
                        </van-col>
                        <van-col span="12">{{item.patName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-yisheng.png')" />
                            <span>就诊医生</span>
                        </van-col>
                        <van-col span="12">{{item.doctorName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-keshi.png')" />
                            <span>就诊科室</span>
                        </van-col>
                        <van-col span="12">{{item.deptName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-riqi.png')" />
                            <span>就诊时间</span>
                        </van-col>
                        <van-col span="12">{{item.appointDateLabel}}</van-col>
                    </van-row>
                    <van-row v-if="item.passWord && showPassword">
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-yymm.png')" />
                            <span class="color2">预约密码</span>
                        </van-col>
                        <van-col class="c   olor2" span="12">{{item.passWord}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-shijian.png')" />
                            <span>订单时间</span>
                        </van-col>
                        <van-col span="12">{{item.operateDate}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <van-icon :name="require('../../../assets/images/img/dd-feiyong.png')" />
                            <span>挂号费</span>
                        </van-col>
                        <van-col class="color3" span="12">{{item.totalFee}}元</van-col>
                    </van-row>
                    <div class="btn" v-if="item.status == 0 || item.status == 1">
                        <span class="remain-time" v-if="item.status == 0 && payAvailable">{{remainTime[index]}}</span>
                        <span class="operate-btn" @click="handleCancelAppoint(index)">取消预约</span>
                        <span class="m-l-20 operate-btn" @click="handlePayOrder(index)" v-if="item.status == 0 && payAvailable && item.countDown > 0">立即支付</span>
                    </div>
                </div>
            </div>
        <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
    </div>
</template>
<script>
export default {
    name: "MyRegister",
    data() {
        return {
            dataSource: "",
            remainTime: [],
        };
    },
    mounted() {
        let patId = this.$route.query.patId;
        let patMedNo = this.$route.query.patMedNo;
        this.$api.outpatient.queryAppointRegisterList({
            data: {patId, patMedNo}
        }).then((response) => {
            if (response.data.Code == 0) {
                this.dataSource = this.wrapDataSource(response.data.Data);
            }
        })
    },
    computed: {
        payAvailable() {
            return this.$store.state.payAvailable;
        },
        isTimeRange() {
            return this.$store.state.timeRange;
        },
        showPassword() {
            return this.$store.state.showPassword;
        }
    },
    methods: {
        handleCancelAppoint(index) {
            let curData = this.dataSource[index];
            if (curData.status == 0) {
                this.cancelAppoint(curData);
            }
            else if (curData.status == 1){
                this.registerRefund(curData);
            }
        },
        cancelAppoint(curData, isShowModaal = true) {
            let data = {};
            data["patId"] = curData.patId;
            data["patName"] = curData.patName;
            data["patMedNo"] = curData.patMedNo;
            data["deptNo"] = curData.deptNo;
            data["doctorNo"] = curData.doctorNo;
            data["timeFlag"] = curData.timeFlag;
            data["appointDate"] = curData.appointDate.slice(0, 10);
            data["appointTime"] = "";
            data["type"] = 1;
            data["passWord"] = curData.passWord;
            data["clinicType"] = 0;
            this.$api.outpatient.appointRegisterOrCancel({
                data,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    curData.status = 2;
                    curData.statusLabel = "已取消";
                    if (isShowModaal) {
                        this.$dialog({ message: "取消预约成功" });
                    }
                }
            })
        },
        registerRefund(curData) {
            let data = {};
            data["patId"] = curData.patId;
            data["visitNo"] = curData.visitNo;
            data["serialNo"] = curData.serialNo;
            data["refundFee"] = curData.totalFee;
            data["refundDes"] = "预约挂号退款";
            data["payType"] = curData.payType;
            data["tradeNo"] = curData.tradeNo;
            this.$api.outpatient.appointRegisterRefund({
                data,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    curData.status = 2;
                    curData.statusLabel = "已取消";
                    this.$dialog({ message: "取消预约成功, 退款将原路返还" });
                }
            })
        },
        handlePayOrder(index) {
            let curData = this.dataSource[index];
            let passWord = curData.passWord;
            let patId = curData.patId;
            let fee = curData.totalFee;
            let patMedNo = curData.patMedNo;
            let attach = "7-" + passWord;
            this.$api.outpatient.getJsApi({
                data: {
                    patId,
                    body: "预约挂号",
                    fee,
                    detail: "预约挂号支付",
                    visitNo: patMedNo,
                    attach
                },
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = this.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        this.globalMethods.aliPay(resData);
                    }
                    else if (systemType == "0B") {
                        this.globalMethods.wxPay(resData);
                    }
                }
            });
        },
        wrapDataSource(data) {
            let owner = this;
            for (let i = 0; i < data.length; i++) {
                let curItem = data[i];
                curItem.statusLabel = this.$store.state.locale["register-status-" + curItem.status];
                let timeflagLabel = this.$store.state.locale["timeflag-" + curItem.timeFlag];
                let hasTimeRange = curItem.passWord && this.isTimeRange;
                if (hasTimeRange) {
                    curItem.appointDateLabel = curItem.appointDate;
                }
                else {
                    curItem.appointDateLabel = curItem.appointDate.slice(0, 10) + timeflagLabel;
                };
                // 计算剩余支付时间
                let calcRemainTime = function() {
                    let index = i;
                    let curItem = data[i];
                    let remainTime = parseInt(curItem.countDown, 10) - 1;
                    if (remainTime > 0 && curItem.status == 0 && owner.payAvailable) {
                        let timer = setInterval(() => {
                            remainTime--;
                            let minutes = parseInt(remainTime / 60, 10);
                            let seconds = remainTime % 60;
                            minutes = (minutes + "").length == 1 ? "0" + minutes : minutes;
                            seconds = (seconds + "").length == 1 ? "0" + seconds : seconds;
                            owner.$set(owner.remainTime, index, "剩余支付时间：" + minutes + ":" + seconds);
                            if (parseInt(minutes, 10) == 0 && parseInt(seconds, 10) == 0) {
                                clearInterval(timer);
                                owner.dataSource[index].status = 2;
                                owner.dataSource[index].statusLabel = "已取消";
                                owner.cancelAppoint(curItem, false);
                            }
                        }, 1000);
                    }
                }
                calcRemainTime();
            }
            return data;
        }
    }
};

</script>
<style scoped>
.content {
    background-color: #fff;
    padding: 10px 15px;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .van-row .van-col .van-icon {
    margin-right: 15px;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.content .btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px 0;
    border-top: 0.5px solid #e2d7d7;
}
.content .btn .remain-time {
    font-size: 14px;
    color: #f3a20c;
    margin-right: 20px;
}
.content .btn .operate-btn {
    width: 70px;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    color: #fff;
    padding: 5px;
    border-radius: 3px;
    font-size: 14px;
}
.color1 {
    color:#ce8e1b !important;
}
.color2 {
    color: #ff6257 !important;
}
.color3 {
    color:#ff6257 !important;
}
</style>



