// axios封装
// 请求拦截、响应拦截、错误统一处理
import axios from 'axios';
import router from '../router';
import { Toast } from 'vant';
import methods from '../methods/index';

// 提示函数
// 禁止点击蒙层、显示一秒后关闭
const loading = (forbidClick = true) => {
    Toast.loading({
        message: '加载中...',
        duration: 0,
        forbidClick
    });
}
const tip = (message, duration = 2000, forbidClick = true) => {
    Toast({
        message,
        duration,
        forbidClick
    });
}

// token过期，跳转到首页重新登录
const toLogin = () => {
    methods.delCookie("token");
    let url = window.location.href;
    if (url.includes("utpatient")) {
        window.location.reload();
    }
    else {
        router.replace({name: "Outpatient"});
    }
}

// 请求成功后的错误码处理
const resHandle = (res) => {
    // 状态码判断
    switch (res.data.Code) {
        // -1: 没数据或其他错误，直接弹出错误信息
        case -1:
            if (res.config.resLoading) {
                let resLoading = res.config.resLoading;
                tip(res.data.Msg, resLoading.duration, resLoading.forbidClick);
            }
            break;
        // -2 token过期，重新登录
        case -2:
            tip('toke过期，即将重新登录', 2000);
            setTimeout(() => {
                toLogin();
            }, 2000);
            break;
        default:
            console.log(res.data.Msg);
    }
}

// 创建axios实例
var instance = axios.create({
    timeout: 1000 * 20,
    baseURL: window.g.apiUrl,
    method: "post"
});


// 请求拦截器 
instance.interceptors.request.use(
    config => {
        // 请求前遮罩层配置
        if ('reqLoading' in config) {
            if (config.reqLoading) {
                loading(config.reqLoading.forbidClick);
            }
        }
        else {
            loading();
        }
        // 每次请求前都携带token
        const token = methods.getCookie("token");
        token && (config.headers.auth = token);
        return config;
    },
    error => Promise.error(error)
)
// 响应拦截器
instance.interceptors.response.use(
    // 请求成功
    res => {
        if (res.status === 200) {
            if ("resToastClear" in res.config) {
                if (res.config.resToastClear) {
                    Toast.clear();
                }
            }
            else {
                Toast.clear();
            }
            resHandle(res);
            return Promise.resolve(res)
        }
        else {
            return Promise.reject(res);
        }
    },
    // 请求失败
    error => {
        return Promise.reject(error);
    }
);

export default instance;


