<template>
    <div class="tabbar-wrap pf">
        <div class="tabbar-container pa flex_dom">
            <span class="item flex_column flex_item_mid flex_item_center" @click="handleOnCurrentItem(0)">
                <base-tabbar-btn :text="data.text[0]" :defaultIcon="data.defaultIcon[0]" :activeIcon="data.activeIcon[0]" :default-color="data.defaultColor" :active-color="data.activeColor" :status="index == 0 ? 1 : 0"></base-tabbar-btn>
            </span>
            <span class="item flex_column flex_item_mid flex_item_center" @click="handleOnCurrentItem(1)">
                <base-tabbar-btn :text="data.text[1]" :defaultIcon="data.defaultIcon[1]" :activeIcon="data.activeIcon[1]" :default-color="data.defaultColor" :active-color="data.activeColor" :status="index == 1 ? 1 : 0"></base-tabbar-btn>
            </span>
            <span class="item add-btn-wrap pr" @click="handleGoAddCard">
                <div class="add-btn abs-mid-X">
                    <span class="abs-mid cross1"></span>
                    <span class="abs-mid cross2"></span>
                </div>
            </span>
            <span class="item flex_column flex_item_mid flex_item_center" @click="handleOnCurrentItem(2)">
                <base-tabbar-btn :text="data.text[2]" :defaultIcon="data.defaultIcon[2]" :activeIcon="data.activeIcon[2]" :default-color="data.defaultColor" :active-color="data.activeColor" :status="index == 2 ? 1 : 0"></base-tabbar-btn>
            </span>
            <span class="item flex_column flex_item_mid flex_item_center" @click="handleOnCurrentItem(3)">
                <base-tabbar-btn :text="data.text[3]" :defaultIcon="data.defaultIcon[3]" :activeIcon="data.activeIcon[3]" :default-color="data.defaultColor" :active-color="data.activeColor" :status="index == 3 ? 1 : 0"></base-tabbar-btn>
            </span>
        </div>
    </div>
</template>
<script>
import BTabbarBtn from "../base/BTabbarBtn";
export default {
    name: "com-tabbar",
    components: {
        [BTabbarBtn.name]: BTabbarBtn
    },
    data() {
        return {
            index: this.active
        };
    },
    props: {
        data: {
            type: Object,
            default() {
                return  {
                    text: ["门诊", "住院", "医院信息", "我的"],
                    defaultIcon: [],
                    activeIcon: [],
                    defaultColor: "#8191A2",
                    activeColor: "#0078FF",
                }
            }
        },
        active: {
            type: [String, Number],
            default: 0
        }
    },
    watch: {
        active(val) {
            this.index = val;
        }
    },
    methods: {
        handleOnCurrentItem(index) {
            if (this.index != index) {
                this.index = index;
                this.$emit("change", index);
            };
        },
        handleGoAddCard() {
            this.$emit("add-card");
        }
    }
};
</script>
<style scoped>
.tabbar-wrap {
    width: 100%;
    height: 80px;
    background: url('../../assets/images/tabbar_bg.png') no-repeat;
    background-size: 100% 100%;
    bottom: 0;
}
.tabbar-container {
    height: 51px;
    width: 100%;
    bottom: 0;
}
.tabbar-container .item{
    height: 100%;
    width: 18.75%;
}
.add-btn-wrap {
    width: 25% !important;
}
.add-btn {
    width: 44px;
    height: 44px;
    background-image: linear-gradient(to bottom left, #00CAFF, #2872FF);
    border-radius: 22px;
    box-shadow: -1px 1px 5px #2872FF;
    top: -5px;
}
.add-btn>span {
    border-radius: 1.5px;
    background-color: #fff;
}
.add-btn .cross1 {
    width: 20px;
    height: 3px;
}
.add-btn .cross2 {
    width: 3px;
    height: 20px;
}
</style>


