<template>
    <div class="container">
        <van-empty
        class="custom-image"
        :image="require('../../../assets/images/outpatient/bind_type.png')"
        description="请选择绑卡方式"
        />
        <van-button class="m-b-10 m-t-90" type="info" color="#5199FF" block round @click="handleGetAuthCode">社保卡建档</van-button>
        <van-button plain type="info" block round @click="handleGoBindCard">院内就诊卡建档</van-button>
        
    </div>
</template>
<script>
export default {
    name: "SelectBindType",
    data() {
        return {
            
        };
    },
    methods: {
        handleGetAuthCode() {
            this.$api.outpatient.getSociaSecuCardAuthCode({
                data: {
                    redirect_url: "#/bindCard"
                }
            }).then((response) => {
                if (response.data.Code == 0) {
                    window.location.href = response.data.Data;
                }
            })
        },
        handleGoBindCard() {
            this.$router.push({name: "BindCard"}).catch(()=>{});
        }
    }
};
</script>
<style scoped>
.container {
    height: 100vh;
    padding: 0 15px;
    background-color: #f0f0f0;
}
.custom-image >>> .van-empty__image {
    width: 320px;
    height: 200px;
}
</style>


