<template>
    <div class="main-bg">
        <van-search
        v-model="inHosNo"
        show-action
        :error="inputError"
        shape="round"
        background="#0078ff"
        placeholder="请输入住院号"
        @search="onSearch"
        @input="onInput"
        >
            <template #action>
                <div class="search" @click="onSearch">查询</div>
            </template>
        </van-search>
        <div class="wrapper">
            <div class="radius-10 m-t-10" v-for="(item, index) in dataSource" :key="'list' + index">
                <div class="content">
                    <van-row class="order-state">
                        <van-col class="size1" span="12">
                            <van-tag plain type="primary">{{item.invoiceFlagLabel}}</van-tag>
                        </van-col>
                        <van-col :class="{color1: (item.invoiceStatus == 0 || item.invoiceStatus == 1), color2: item.invoiceStatus == 3, color3:item.invoiceStatus == 2}" span="12" @click="handleGoAction(index)">{{item.actionLabel}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>住院人</span>
                        </van-col>
                        <van-col span="12">{{item.patName}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>住院号</span>
                        </van-col>
                        <van-col span="12">{{item.inHosNo}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>入院次数</span>
                        </van-col>
                        <van-col span="12">{{item.inHosTimes}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>出院时间</span>
                        </van-col>
                        <van-col span="12">{{item.outHospitalDate}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>预缴总额</span>
                        </van-col>
                        <van-col span="12">{{Number(item.totalAmount).toFixed(2) + '元'}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>保险报销金额</span>
                        </van-col>
                        <van-col span="12">{{Number(item.insuranceAmount).toFixed(2)}}</van-col>
                    </van-row>
                    <van-row>
                        <van-col span="12">
                            <span>退款金额</span>
                        </van-col>
                        <van-col span="12">{{Number(item.refundAmount).toFixed(2) + '元'}}</van-col>
                    </van-row>
                    <div class="consume">
                        <span>消费金额：{{Number(item.consume).toFixed(2) + '元'}}</span>
                    </div>
                </div>
            </div>
            <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
        </div>
    </div>
</template>
<script>
export default {
    name: "QueryInHosSettleInfo",
    data() {
        return {
            dataSource: "",
            inputError: false,
            inHosNo: ""
        };
    },
    methods: {
        queryInHosSettleInfo() {
            this.$api.inHosNo.queryInHosSettleInfo({
                data: {inHosNo: this.inHosNo}
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = this.wrapData(response.data.Data);
                }
            });
        },
        onInput() {
            this.inputError =  false;
        },
        onSearch() {
            if (this.inHosNo) {
                this.queryInHosSettleInfo();
            }
            else {
                this.inputError = true;
            }
        },
        handleGoAction(index) {
            let curItem = this.dataSource[index];
            let invoiceFlag = curItem.invoiceFlag;
            let operationModel = curItem.operationModel;
            if (invoiceFlag != 0) {
                if (operationModel == 0) {
                    window.open(curItem.invoiceUrl)
                }
                else {
                    window.open(`/static/pdf/web/viewer.html?file=${encodeURIComponent(curItem.invoiceUrl)}`)
                }
            }
            else {
                this.makeOutInvoice(curItem.serialNo);
            }
        },
        makeOutInvoice(serialNo) {
            this.$api.outpatient.makeOutInvoice({
                data: {serialNo}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.$toast({
                        message: "已插入开票队列，请稍后再来查看",
                        duration: "2000",
                        forbidClick: true
                    });
                }
            })
        },
        wrapData(data) {
            if (data) {
                return data.map((curVal) => {
                    curVal.invoiceFlagLabel = this.$store.state.locale[`invoiceFlag-${curVal.invoiceFlag}`];
                    curVal.actionLabel = this.$store.state.locale[`invoiceStatus-${curVal.invoiceStatus}`];
                    
                    return curVal;
                })
            }
            else {
                return data;
            }
            
        }
    }
};

</script>
<style scoped>
.van-search .van-search__action {
    background-color: #0078ff;
}
.search {
    color: #fff;
}
.wrapper {
    height: 88vh;
    overflow: scroll;
}
.content {
    background-color: #fff;
    padding: 10px 15px;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.content .consume {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 10px 0;
    border-top: 0.5px solid #e2d7d7;
}
.size1 {
    font-size: 14px !important;
}
.color1 {
    color: #0078ff!important;
}
.color2 {
    color:#ce8e1b !important;
}
.color3 {
    color: #ff6257 !important;
}

</style>



