<template>
  <div class="container main-bg">
    <van-form @submit="handleSubmit" :show-error-message="false">
        <div class="radius-10">
            <!-- 姓名 -->
            <van-field
                name="patName"
                v-model="patName"
                label="姓名"
                label-width="120"
                placeholder="请输入患者姓名"
                :rules="[{ required: true, message: '请输入患者姓名' }]"
                clearable
            />
            <!-- 身份证号码 -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                label="身份证号"
                placeholder="请输入患者身份证号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入患者身份证号' }]"
                clearable
            />
            <!-- 入院登记证编号 -->
            <van-field
                name="inHosCerNo"
                v-model="inHosCerNo"
                label="入院登记证编号"
                placeholder="请输入入院登记证编号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: false, message: '请输入入院登记证编号' }]"
                clearable
            />
        </div>
        <!-- 提示 -->
        <div class="tips">
            <span class="strong">温馨提示：</span><span class="normal">请输入患者相关信息，并准确核对后再进行下一步操作！</span>
        </div>
        <base-button class="m-t-20" text="查询" size="large" native-type="submit" :btnDisabled="btnDisabled" />
    </van-form>
  </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "QueryInhosCertificate",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            inHosCerNo: "",
            patName: "",
            patIdNo: "",
            btnDisabled: false,
        };
    },
    methods: {
        handleSubmit(values) {
            //valid the form of patIdNo
            if (!this.validForm(values)) {
                return;
            };
            this.btnDisabled = true;
            let data = {};
            data["inHosCerNo"] = values.inHosCerNo;
            data["patName"] = values.patName;
            data["patIdNo"] = values.patIdNo;
            this.$api.inHosNo.queryInHospitalCertificate({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    this.$router.replace({name: "InHosRegisterInfo", params: {data}})
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        validForm(values) {
            if (!values.patIdNo && !values.inHosCerNo) {
                this.$toast({
                    message: "身份证号和入院证编号不能同时为空",
                    forbidClick: false
                });
                return false;
            };
            if (values.patIdNo) {
                if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                    this.$toast({
                        message: "身份证号格式错误",
                        forbidClick: false
                    });
                    return false;
                }
                else {
                    return true;
                }
            };
            return true;
        },
        toastFail(msg) {
            this.btnDisabled = false;
            this.$toast({
                message: msg,
                forbidClick: false,
            })
        }
    }
};
</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}
</style>



