<template>
    <div class="main-bg">
        <ul class="date-container radius-10" v-if="!isCurrent">
            <li v-for="(item, index) in dateList" :key="index" @click="hanldeSelectDate(index)" :class="{active: index == curSelect}">
                <span>{{item.week}}</span>
                <span>{{item.day}}</span>
            </li>
        </ul>
        <div class="title" v-if="dataSource.length > 0 && !isCurrent">坐诊医生列表</div>
        <div class="title" v-if="dataSource.length > 0 && isCurrent">今日坐诊医生列表</div>
        <van-row class="doctor-list radius-10" v-for="(item, index) in dataSource" :key="'doc' + index">
            <van-col class="pic" span="6">
                <van-image width="70" height="70" round :src="item.doctorAvatar" />
            </van-col>
            <van-col class="des" span="14">
                <div class="strong">{{item.doctorName}}</div>
                <div>所属科室：{{item.deptName}}</div>
                <div>剩余号源：<span class="strong">{{item.appointAvailableNum}}</span>个</div>
            </van-col>
            <van-col class="btn" span="4" @click="handleGoDoctorDetail(item.doctorNo)">
                <van-icon size="20" :name="require('../../../assets/images/img/gh-rq.png')" />
                <span>预约</span>
            </van-col>
        </van-row>
        <van-empty description="暂无排班信息" v-if="dataSource.length == 0" />
    </div>
</template>
<script>
export default {
    name: "DoctorList",
    data() {
        return {
            dateList: "",
            curSelect: 0,
            dataSource: ""
        };
    },
    created() {
        let dateList = [];
        for (let i = 1; i < 8; i++) {
            dateList.push(this.globalMethods.getDate(i));
        }
        this.dateList = dateList;
    },
    mounted() {
        let add = this.isCurrent ? -1 : 0;
        this.hanldeSelectDate(add);
    },
    computed: {
        isCurrent() {
            return this.$route.query.type == "0";
        },
        hasTimeRange() {
            if (this.isCurrent) {
                return false;
            }
            else {
                return this.$store.state.timeRange;
            }
        },
        isCurrent() {
            return this.$route.query.type == 0;
        },
    },
    methods: {
        hanldeSelectDate(value) {
            this.curSelect = value;
            let dateObj = this.globalMethods.getDate(value + 1);
            let date = dateObj.year + "-" + dateObj.month + "-" + dateObj.day;
            let query = this.$route.query;
            let deptNo = query.deptNo;
            let type = query.type;
            let appointType = this.hasTimeRange ? "1" : "0";
            this.$api.outpatient.getDoctorList({
                data: {deptNo, date, type, appointType }
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = response.data.Data;
                }
                else if (response.data.Code != -2) {
                    this.dataSource = "";
                }
            })
        },
        handleGoDoctorDetail(doctorNo) {
            let deptNo = this.$route.query.deptNo;
            let type = this.$route.query.type;
            this.$router.push({name: "DoctorDetail", query: {deptNo, doctorNo, type}});
        }
    }
};

</script>
<style scoped>
.date-container {
    width: 100%;
    display: flex;
    background-color: #fff;
}
.date-container li{
    width: 14.28%;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 5px 0;
    color: #0078FF;
    background-color: #fff;
}
.date-container li.active {
    background-color: #0078FF;
    color: #fff;
}
.title {
    text-align: left;
    font-size: 15px;
    color: #514c4c;
    padding: 10px;
}
.doctor-list {
    background-color: #fff;
    padding: 0px 10px;
    height: 100px;
    margin-bottom: 10px;
}
.doctor-list .van-col {
    height: 100%;
    padding: 15px 0;
}
.doctor-list .pic {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.doctor-list .des {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    font-size: 14px;
    color: #88929d;
    padding-left: 10px;
    border-right: 0.5px solid #d4d8dd;
}
.doctor-list .des .strong {
    color: black;
}
.doctor-list .btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 14px;
    color: #0078FF;
}
</style>



