<template>
    <div class="main-bg">
        <div>
            <van-cell-group class="card-item radius-10">
                <van-cell title-style="text-align: left;" title="入院登记证编号" :value="dataSource.inHosCerNo" />
                <van-cell title-style="text-align: left;" title="姓名" :value="dataSource.patName" />
                <van-cell title-style="text-align: left;" title="性别" :value="dataSource.patSex == 1 ? '男' : '女'" />
                <van-cell title-style="text-align: left;" title="年龄" :value="dataSource.patAge + '岁'" />
                <van-cell title-style="text-align: left;" title="身份证号" :value="dataSource.patIdNo" />
                <van-cell title-style="text-align: left;" title="入院科室" :value="dataSource.deptName"/>
                <van-cell title-style="text-align: left;" title="门诊诊断" :value="dataSource.diagnosis"/>
                <van-cell title-style="text-align: left;" title="入院日期" :value="dataSource.inHosDate"/>
                <van-cell title-style="text-align: left;" title="联系方式" :value="dataSource.patPhone"/>
            </van-cell-group>
            <base-button class="m-t-20" text="确认入院" :btnDisabled="btnDisabled" size="middle" @confirm="handleComfirm" />
        </div>
    </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "InHosRegisterInfo",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            btnDisabled: false,
            dataSource: "",
        };
    },
    mounted() {
        this.dataSource = this.$route.params.data;
    },
    methods: {
        handleComfirm() {
            this.$api.inHosNo.inHospitalRegister({
                data: {
                    inHosCerNo: this.dataSource.inHosCerNo
                },
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("bindedInHosNo", true);
                    this.$router.go(-1);
                }
            });
        }
    }
};

</script>
<style scoped>
.card-item {
    margin-bottom: 15px;
}
.confirm-btn {
    margin: 30px;
}
</style>



