<template>
    <div>

    </div>
</template>
<script>
export default {
    name: "AsociaSecuPayResult",
    data() {
        return {
            seconds: 0,
            timer: null,
        };
    },
    mounted() {
        this.$toast.loading({message: '支付中...', duration: 0, forbidClick: true});
        this.queryOrderYB();
        this.timer = setInterval(() => {
            this.seconds++;
        }, 1000)
    },
    methods: {
        queryOrderYB() {
            this.$api.outpatient.queryOrderYB({
                data: {
                    medTransId: this.$store.state.medTransId
                },
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    clearInterval(this.timer);
                    this.$toast({
                        type: "success",
                        message: "支付完成，支付结果以微信推送消息为准",
                        forbidClick: true,
                        onClose: () => {
                            this.closePage();
                        }
                    })
                }
                else {
                    setTimeout(() => {
                        if (this.seconds < 60) {
                            console.log("即将发起请求");
                            this.queryOrderYB && this.queryOrderYB();
                        }
                        else {
                            clearInterval(this.timer);
                            this.$toast({
                                type: "fail",
                                message: "支付超时，支付结果以微信推送消息为准",
                                forbidClick: true,
                                onClose: () => {
                                    this.closePage();
                                }
                            })
                        }
                    }, 2000)
                }
            })
        },
        closePage() {
            // 判断是否是微信环境
            if (typeof WeixinJSBridge == "undefined") {
                this.$router.replace({name: "Outpatient"});
            }
            else {
                //关闭微信浏览器  安卓系统
                document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
                //关闭微信浏览器   ios系统
                WeixinJSBridge.call('closeWindow');
            }
        }
    },
    beforeDestroy() {
        //实例销毁前置空queryOrderYB方法，防止页面跳转后重复调用
        this.queryOrderYB = null;
    }
};
</script>
<style scoped>

</style>


