<template>
    <div class="container main-bg">
        <div class="card radius-10">
            <div class="card-code">
                <span class="label">住院号：</span>
                <span class="code">{{dataSource.inHosNo}}</span>
            </div>
            <div class="card-name">
                <span class="label">{{dataSource.patName}}</span>
                <span class="code">{{dataSource.patIdNo}}</span>
            </div>
        </div>
        <van-cell-group class="cellphone radius-10">
            <van-cell title="手机号码" :value="dataSource.patPhone" title-class="cellphone-num-label" value-class="cellphone-num"/>
            <van-cell title="余额" :value="dataSource && Number(dataSource.deposit).toFixed(2) + '元'" title-class="balance-label" value-class="balance"/>
        </van-cell-group>
        <van-cell-group class="radius-10">
            <van-cell title="充值金额" title-style="text-align: left"/>
            <van-field
                v-model="amount"
                type="number"
                center
                clearable
                label="￥"
                label-width="30"
                placeholder="请输入充值金额"
                @input="handleInputChange"
            >
            <template #extra >
                <span class="unit">元</span>
            </template>
            </van-field>
        </van-cell-group>
        <div class="tips m-t-10 m-l-45">
            <p class="tl font14 color-ff0000" v-if="fieldError">充值金额必须大于0</p>
        </div>
        <base-button class="m-t-70" text="充值" size="middle" :btnDisabled="btnDisabled" @confirm="handleCharge" />
    </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "ChargeDeposit",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            inHosNo: "",
            amount: "",
            dataSource: "",
            btnDisabled: false,
            fieldError: false
        };
    },
    mounted() {
        this.inHosNo = this.$route.query.inHosNo;
        this.$api.inHosNo.queryPatientInfoByID({
            data: {
                inHosNo: this.inHosNo,
            }
        }).then ((response) => {
            if (response.data.Code == 0) {
                let resData = response.data.Data;
                this.dataSource = resData;
            }
        });
    },
    methods: {
        handleCharge() {
            this.btnDisabled = true;
            if (!this.validInput()) {
                return false;
            };
            this.$api.outpatient.getJsApi({
                data: {
                    patId: this.dataSource.inHosNo,
                    body: "续交押金",
                    fee: this.amount,
                    detail: "续交押金",
                    visitNo: this.dataSource.inHosNo,
                    attach: "5-" + this.dataSource.inHosNo + "-"
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = this.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        this.globalMethods.aliPay(resData);
                    }
                    else if (systemType == "0B") {
                        this.globalMethods.wxPay(resData, () => {
                            this.btnDisabled = false;
                        });
                    }
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            });
        },
        handleInputChange() {
            this.validInput();
        },
        validInput() {
            let val = this.amount;
            if (!val || val == 0) {
                this.fieldError = true;
                this.btnDisabled = false;
                return false;
            }
            else {
                this.amount = this.globalMethods.limitFloat(val);
                this.fieldError = false;
                return true;
            }
        }
    }
};

</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.card {
    background: linear-gradient(#3853e8, #2cb8f7);
    height: 100px;
    box-sizing: border-box;
    color: #fff;
    padding: 10px;
    font-size: 14px;
}
.card-code {
    text-align: left;
    margin-bottom: 30px;
}
.card-name {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card-name .label{
    font-size: 18px;
}
.cellphone {
    margin: 10px 0;
}
.cellphone-num-label {
    text-align: left;
    color: #9e9e9e;
}
.cellphone-num {
    color: black;
}
.balance {
    color: black;
}
.balance-label {
    text-align: left;
    color: #9e9e9e;
}
.unit {
    margin-left: 10px;
}
.tips {
    height: 30px;
}
</style>



