// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store/index';
import axios from 'axios';
import methods from './methods/index';
import vant from './vant/index';
import 'vant/lib/icon/local.css';
import './styles/global.css';
import './styles/common.css';
import 'amfe-flexible/index.js';
import api from './request/api/index';
import Vconsole from 'vconsole';
let vConsole = new Vconsole();
Vue.use(vConsole);
// 封装后的axios
Vue.prototype.$api = api;
Vue.config.productionTip = false;
console.log(343343);

// 解决跨域
// axios.defaults.baseURL = '/api';

// 引入mock数据，关闭则注释该行
require('./mock');

// 初始化项目中依赖的vant组件
for (let i = 0; i < vant.vantComs.length; i++) {
    Vue.use(vant.vantComs[i]);
};
// 从Cookie中读取绑定状态，防止刷新页面状态丢失
store.commit("binded", methods.getCookie("binded") == "true");
store.commit("bindedInHosNo", methods.getCookie("bindedInHosNo") == "true");
store.commit("medTransId", methods.getCookie("medTransId"));
// 为vue添加全局方法
Vue.prototype.globalMethods = methods;
// 更新状态(binded, bindedInHosNo)
Vue.prototype.globalMethods.updateStatus = function(name, value) {
    methods.setCookie(name, value);
    store.commit(name, value);
};
// 用户登录返回重定向url
Vue.prototype.globalMethods.login = function() {
    api.login.getAuthUrl({
        data: {
            redirect_url: "#/outpatient",
            systemType: methods.getCookie("systemType")
        },
        resLoading: false,
    }).then((response) => {
        if (response.data.Code == 0) {
            let data = response.data.Data;
            window.location = data;
        }
    })
};

// 路由跳转展示当前路由标题
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    };
    next();
});

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    axios,
    components: { App },
    template: '<App/>'
});



