<template>
    <div class="service-card" :class="classObj" :style="styleObj">
        <div class="flex_column tl">
            <span class="m-b-5 font-w6 title">{{title}}</span>
            <span class="title-desc">{{titleDesc}}</span>
        </div>
    </div>
</template>
<script>
export default {
    name: "base-service-card",
    data() {
        return {
            
        };
    },
    props: {
        scale: {
            type: String,
            default: "large"
        },
        title: {
            type: String,
            default: "服务名称"
        },
        titleDesc: {
            type: String,
            default: "服务描述"
        },
        icon: {
            type: String,
        }
    },
    computed: {
        classObj() {
            return {
                "large": this.scale == "large",
                "middle": this.scale == "middle",
            }
        },
        styleObj() {
            let url = this.icon;
            return {
                "background-image": `url(${url})`
            }
        }
    }
};
</script>
<style scoped>
.service-card {
    background-repeat: no-repeat;
    background-position: bottom right;
    background-color: #FAFBFE;
}
.title {
    color: #3D414D;
}
.title-desc {
    color: #818694;
}
.service-card.large {
    width: 167.5px;
    height: 170px;
    padding: 22px 15px;
    background-size: 100% auto;
}
.service-card.large .title {
    font-size: 16px;
}
.service-card.large .title-desc {
    font-size: 12px;
}
.service-card.middle {
    width: 167.5px;
    height: 80px;
    padding: 13.5px 10px;
    background-size: 60.6% 72.5%;
}
.service-card.middle .title {
    font-size: 14px;
}
.service-card.middle .title-desc {
    font-size: 10px;
}
</style>


