<template>
    <div class="container">
        <div v-if="waittingList.length > 0">
            <div class="view-panel p-t-10">
                <div class="m-b-10">您前面还有</div>
                <div>
                    <span class="font50">{{dataSource.beforeNum}}</span>
                    <span>位</span>
                </div>
                <div class="m-t-10">请注意提前一定时间到达诊室门口等候！</div>
                <div class="flex_dom flex_item_around m-t-20">
                    <div class="my-no flex_column p-t-20">
                        <span class="m-b-10">我的号</span>
                        <span>{{dataSource.myCallSeqNo}}</span>
                    </div>
                    <div class="cur-no flex_column p-t-20">
                        <span class="m-b-10">当前号</span>
                        <span>{{dataSource.callSeqNo}}</span>
                    </div>
                </div>
            </div>
            <div class="waitting-list">
                <van-notice-bar mode="closeable" background="#ecf9ff" color="#1989fa" :wrapable="true" v-if="waittingList.length > 1">
                    <template #default>
                        <div class="notice-bar-text">提示：您目前有多个挂号处于候诊状态，可手动切换查看其他挂号候诊情况</div>
                    </template>
                </van-notice-bar>
                <div class="card pr" v-for="(item, index) in waittingList" :key="`waitLit${index}`" @click="handleTransferRegister(index)">
                    <div class="wrap">
                        <div class="row">
                            <span class="name">医生：{{item.doctorName}}</span>
                        </div>
                        <div class="row dept-name">
                            <span>科室: {{item.deptName}}</span>
                        </div>
                    </div>
                    <div class="default-icon pa" v-if="curIndex == index">当前显示</div>
                </div>
            </div>
        </div>
        <van-empty description="您目前没有候诊记录" v-else/>
    </div>
</template>
<script>
export default {
    name: "WaittingDiag",
    data() {
        return {
            curIndex: 0,
            dataSource: "",
            waittingList: [],
        };
    },
    mounted() {
        this.queryRegisterQueue();
    },
    methods: {
        queryRegisterQueue() {
            this.$api.outpatient.registerQueue({
                data: {}
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.waittingList = response.data.Data;
                    this.dataSource = this.waittingList[0];
                }
            })
        },
        handleTransferRegister(index) {
            this.curIndex = index;
            this.dataSource = this.waittingList[index];
        }
    }
};
</script>
<style scoped>
.container {
    min-height: 100vh;
}
.view-panel {
    height: 280px;
    color: #fff;
    background-image: linear-gradient(to bottom right, #00CAFF, #008CFF);
}
.font50 {
    font-size: 50px;
}
.my-no,
.cur-no {
    width: 100px;
    height:100px;
    border-radius: 50px;
}
.my-no {
    background-color: rgba(255, 255, 255, 0.3);
}
.cur-no {
    color: #008CFF;
    background-color: #fff;
}
.waitting-list {
    padding: 10px 15px;
}
.card {
    width: 98%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.card .wrap {
    height: 100%;
}
.wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.wrap .name {
    color: #292C34;
}
.wrap .dept-name {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
.default-icon {
    width: 68px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px 0 10px 0 / 10px 0 10px 0;
    background-image: linear-gradient(#00CAFF, #2872FF);
    font-size: 10px;
    color: #fff;
    text-align: center;
    right: 0;
    bottom: 0;
}
.notice-bar-text {
    font-size: 12px;
    text-align: left;
    line-height: 17px;
}
</style>


