<template>
    <div class="main-bg">
        <van-cell class="patient" :border="false" :title="patName + ' ' + (type == 0 ? patMedNo : inHosNo)" title-style="text-align: left;" is-link @click="handleShowSelectCard"/>
        <van-row class="date-btn" type="flex" gutter="10">
            <van-col span="6" offset="6" @click="handleQueryReport(-7)"><span :class="{active: btnIndex == -7}">七天</span></van-col>
            <van-col span="6" @click="handleQueryReport(-30)"><span :class="{active: btnIndex == -30}">一个月</span></van-col>
            <van-col span="6" @click="handleQueryReport(-90)"><span :class="{active: btnIndex == -90}">三个月</span></van-col>
        </van-row>
        <van-tabs 
            class="report-type radius-10"
            :class="tabClassObj"
            v-model="active"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
        >
            <van-tab :title="isNat ? '核酸检测报告' : '检验报告' ">
                <template #title>
                    <span class="custom-tab-title" :class="tabTitleClassObj">{{isNat ? '核酸检测报告' : '检验报告' }}</span>
                </template>
                <div class="card-item radius-10" @click="handleGoReportDetail(item.applyNo, 'inspe')" v-for="(item, index) in dataSourceInspe" :key="'report1' + index">
                    <van-cell-group>
                        <van-cell title-style="text-align: left; font-weight: bold; color: #292c34;" :title="item.itemName" :value="item.applyDate" />
                        <van-cell title-style="text-align: left;" :title="item.diagnosis" center>
                            <template #right-icon>
                                <van-icon name="arrow"/>
                            </template>
                        </van-cell>
                    </van-cell-group>
                </div>
                <van-empty description="暂无数据" v-if="dataSourceInspe.length == 0"/>
            </van-tab>
            <!-- 如果是核酸检测报告，则不显示这个tab -->
            <van-tab title="检查报告" v-if="!isNat">
                <div class="card-item radius-10" @click="handleGoReportDetail(item.applyNo, 'check')" v-for="(item, index) in dataSourceCheck" :key="'report2' + index">
                    <van-cell-group>
                        <van-cell title-style="text-align: left; font-weight: bold; color: #292c34;" :title="item.itemName" :value="item.applyDate" />
                        <van-cell title-style="text-align: left;" :title="item.diagnostic" center>
                            <template #right-icon>
                                <van-icon name="arrow"/>
                            </template>
                        </van-cell>
                    </van-cell-group>
                </div>
                <van-empty description="暂无数据" v-if="dataSourceCheck.length == 0"/>
            </van-tab>
        </van-tabs>
        <van-popup class="select-card-modal radius-10" v-model="showPopup">
            <div class="title">{{type == 0 ? "请选择就诊人" : "请选择住院人"}}</div>
            <div class="popup">
                <div class="content">
                    <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="handleClosePopup(type == 0 ? item.patId : item.inHosNo, item.patName, item.patMedNo)">
                        <div class="wrap">
                            <div class="row">
                                <span class="name">{{item.patName}}</span>
                            </div>
                            <div class="row card-code">
                                <span>{{type == 0 ? "卡号" : "住院号"}}</span>
                                <span>{{type == 0 ? item.patMedNo : inHosNo}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
export default {
    name: "QueryReport",
    data() {
        return {
            active: 0,
            startDate: "",
            endDate: "",
            dataSourceInspe: "",
            dataSourceCheck: "",
            inHosNo: "",
            patId: "",
            patName: "",
            patMedNo: "",
            btnIndex: "",
            cardList: "",
            showPopup: false,
        };
    },
    computed: {
        type() {
            return this.$route.query.type;
        },
        isNat() {
            return this.$route.query.isNat;
        },
        tabClassObj() {
            return {
                "report-nat": this.isNat
            }
        },
        tabTitleClassObj() {
            return {
                "custom-tab-title-divider": !this.isNat
            }
        },
    },
    beforeRouteLeave (to, from, next) {
        // 若是去Account页面，清空keep-alive缓存，防止下次进入该页面还展示上次的缓存
        if (to.name == 'Account') {
            this.$vnode.parent.componentInstance.cache = {};
            this.$vnode.parent.componentInstance.keys = [];
        }
        next();
    },
    mounted() {
        if (this.type == 0) {
            this.queryCardInfo();
        }
        else if (this.type == 1) {
            this.queryInHosNo();
        }
    },
    methods: {
        handleShowSelectCard() {
            this.showPopup = true;
        },
        handleClosePopup(patId, patName, patMedNo) {
            this.showPopup = false;
            if (this.type == 0) {
                this.patId = patId;
            }
            else if (this.type == 1) {
                this.inHosNo = patId;
            }
            this.patName = patName;
            this.patMedNo = patMedNo;
            this.handleQueryReport(this.btnIndex);
        },
        handleGoReportDetail(val, type) {
            if (type == "check") {
                var data, dataSourceCheck = this.dataSourceCheck;
                for (let i = 0; i < dataSourceCheck.length; i ++) {
                    let item = dataSourceCheck[i];
                    if (item.applyNo == val) {
                        data = item;
                        break;
                    }
                }
            };
            this.$router.push({name: "ReportDetail", params: {data}, query: {applyNo: val, type}});
        },
        handleQueryReport(val) {
            this.btnIndex = val;
            this.startDate = this.getDate(val);
            this.endDate = this.getDate(0);
            let data = {};
            data["startDate"] = this.startDate;
            data["endDate"] = this.endDate;
            data["type"] = this.type;
            if (this.type == 0) {
                data["patId"] = this.patId;
            }
            else if (this.type == 1) {
                data["inHosNo"] = this.inHosNo;
            }
            //判断是否是核酸检测报告
            if (this.isNat) {
                this.queryReportNat(data);
            }
            else {
                let requestConfig = {
                    data,
                    reqLoading: false,
                    resToastClear: false
                }
                let outpatientApis = this.$api.outpatient;
                let queryReportInspe = outpatientApis.queryReportInspe;
                let queryReportCheck = outpatientApis.queryReportCheck;
                this.$api.all(queryReportInspe(requestConfig), queryReportCheck(requestConfig)).then((response) => {
                    let inspeData = response[0].data.Data;
                    let checkData = response[1].data.Data;
                    this.dataSourceInspe = inspeData ? inspeData : "";
                    this.dataSourceCheck = checkData ? checkData : "";
                })
            }
        },
        queryReportNat(data) {
            this.$api.outpatient.queryReportNat({
                data,
                resLoading: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSourceInspe = response.data.Data;
                }
            });
        },
        queryCardInfo() {
            this.$api.outpatient.queryCardInfo({
                data: {patId: ""},
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    for (let i = 0; i < data.length; i++)　{
                        let item = data[i];
                        if (item.isDefault == 0) {
                            this.patId = item.patId;
                            this.patName = item.patName;
                            this.patMedNo = item.patMedNo;
                            this.handleQueryReport(-7);
                            break;
                        }
                    };
                    this.cardList = response.data.Data;
                }
            })
        },
        queryInHosNo() {
            this.$api.inHosNo.queryPatientInfoList({
                data: {inHosNo: ""},
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    for (let i = 0; i < data.length; i++)　{
                        let item = data[i];
                        if (item.isDefault == 0) {
                            this.inHosNo = item.inHosNo;
                            this.patName = item.patName;
                            this.handleQueryReport(-7);
                            break;
                        }
                    };
                    this.cardList = response.data.Data;
                }
            })
        },
        getDate(val) {
            let date = this.globalMethods.getDate(val);
            return date.year + "-" + date.month + "-" + date.day;
        }
    }
};

</script>
<style scoped>
.patient {
    background-color: #0078ff;
    color: #fff;
}
.patient .van-icon {
    color: #fff;
}
.date-btn {
    margin: 10px 0;
}
.date-btn .van-col>span{
    background-color: #0078ff;
    display: block;
    height: 30px;
    line-height: 30px;
    text-align: center;
    color: #fff;
    font-size: 14px;
}
.date-btn .van-col>span.active {
    box-shadow: 0px 0px 8px #000000;
    font-weight: bold;
    transform:scale(1.1, 1.1);
}
.card-item {
    margin: 20px 0;
}
.report-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
/* 如果是核酸检测报告只有一个tab，用白色覆盖掉下划选中标识杠 */
.report-nat >>> .van-tabs__line {
    background-color: #fff;
    background-image: linear-gradient(to right, #fff , #fff);
}
.report-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
}
.custom-tab-title-divider {
    border-right: .5px solid #CDCDCD;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
</style>



