<template>
    <div class="operate-bar flex_dom flex_item_mid font17">
        <span class="icon m-r-5" :style="styleObj1"></span>
        <span class="font-w6 first-txt">{{textInner.firstTxt}}</span>
        <span class="font-w6" :style="styleObj2">{{textInner.secondTxt}}</span>
    </div>
</template>
<script>
export default {
    name: "base-operate-bar",
    data() {
        return {
            textInner: this.text
        };
    },
    props: {
        text: {
            type: Object,
            default: {
                firstTxt: "常用",
                secondTxt: "服务",
            }
        },
        gradient: {
            type: String,
            default: "#00CAFF, #008CFF"
        },
        fontColor: {
            type: String,
            default: "#0078FF"
        }
    },
    computed: {
        styleObj1() {
            let colorArr = this.gradient.split(",");
            return {
               "background-image": `linear-gradient(${colorArr[0]}, ${colorArr[1]})`
            }
        },
        styleObj2() {
            return {
                "color": this.fontColor
            }
        }
    }
};
</script>
<style scoped>
.operate-bar {
    height: 20px;
}
.icon {
    width: 3.5px;
    height: 17px;
    border-radius: 2px;
}
.first-txt {
    color: #2A2D34;
    margin-right: 2px;
}
</style>


