<template>
    <div>
        <com-card-detail :type="0" :data="dataSource" :is-default="dataSource.isDefault" :btn-disabled="btnDisabled" @go-charge="handleGoPersonalPay" @delete-card="handleUnbind" @switch-change="handleSetDefault($event, dataSource.patId)" ref="cardDetail"></com-card-detail>
    </div>
</template>
<script>
import CCardDetail from "../../common/CCardDetail";
export default {
    name: "CardDetail",
    components: {
        [CCardDetail.name]: CCardDetail,
    },
    data() {
        return {
            btnDisabled: false,
            dataSource: {}
        };
    },
    mounted() {
        let patId = this.patId;
        this.$api.outpatient.queryCardInfoById({
            data: {
                code: patId,
                type: 0,
            },
            resLoading: {
                forbidClick: false,
            },
        }).then((response) => {
            if (response.data.Code == 0) {
                this.dataSource = response.data.Data[0];
            }
        })
    },
    computed: {
        patId() {
            return this.$route.query.patId;
        }
    },
    methods: {
        handleGoPersonalPay() {
            let patId = this.patId;
            this.$router.push({name: "Charge", query: {patId}});
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onOpened: () => {
                    this.$router.go(-1);
                }
            })
        },
        handleUnbind() {
            this.btnDisabled = true;
            let patId = this.patId;
            this.$api.outpatient.unBindCard({
                data: {patId},
                reqLoading: false,
                resToastClear: false,
                resLoading: {
                    forbidClick: false,
                }
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.toastSuccess("解绑成功");
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            })
        },
        handleSetDefault(isDefault, patId) {
            this.$api.outpatient.updateCardInfo({
                data: {
                    isDefault,
                    patId
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    console.log("设置默认卡成功");
                }
                else if (response.data.Code != -2) {
                    this.$refs.cardDetail.toggleSwitch(true);
                }
            })
        },
    }
};

</script>
<style scoped>

</style>



