<template>
    <div class="main-bg">
        <van-cell class="patient radius-10 m-b-15" :border="false" :title="patName + ' ' + patMedNo" title-style="text-align: left;" is-link @click="handleShowSelectCard"/>
        <van-tabs 
            class="detail-type radius-10"
            v-model="active"
            :swipeable="true"
            title-inactive-color="#818594" 
            title-active-color="#292C34"
            @change="handleRefresh"
          >
            <van-tab title="核酸检测预约">
                <template #title>
                    <span class="custom-tab-title">核酸检测预约</span>
                </template>
                <van-row class="schedule-title">
                    <van-col span="11">
                        <span>日期</span>
                    </van-col>
                    <van-col span="13">
                        <span>号源</span>
                    </van-col>
                </van-row>
                <van-row class="schedule-content active" v-for="(item, index) in dataSourceNatAppointList" :key="`appoint${index}`">
                    <van-col class="date" span="11">
                        {{item.appointDate.slice(5)}} {{item.week}}
                    </van-col>
                    <van-col class="am" span="13" @click="handleNatAppoint(item.status, item.appointId, index)">
                        <span v-if="item.status">预约 ({{item.totalNum - item.availableNum}}/{{item.totalNum}})</span>
                        <span class="active1-item" v-else>约满 ({{item.totalNum - item.availableNum}}/{{item.totalNum}})</span>
                    </van-col>
                </van-row>
                <van-empty description="暂无数据" v-if="dataSourceNatAppointList.length == 0"/>
            </van-tab>
            <van-tab title="我的预约">
                <div class="p-t-10 p-b-10">
                    <div class="radius-10 m-b-10"  v-for="(item, index) in dataSourceMyNatAppointList" :key="`myAppoint${index}`">
                        <div class="content">
                            <van-row class="order-state">
                                <van-col class="size1" span="20">订单状态</van-col>
                                <van-col class="color1" span="4">{{item.statusLabel}}</van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">就诊人</van-col>
                                <van-col span="12">{{item.patName}}</van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">身份证号</van-col>
                                <van-col span="12">{{item.idNo}}</van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">预约时间</van-col>
                                <van-col span="12">{{item.appointDate}}</van-col>
                            </van-row>
                            <van-row>
                                <van-col span="12">操作时间</van-col>
                                <van-col span="12">{{item.operDate}}</van-col>
                            </van-row>
                            <div class="btn" v-if="item.status == 0">
                                <span class="m-l-20 operate-btn" @click="handleGoPayOrder(index)">立即支付</span>
                            </div>
                        </div>
                    </div>
                    <van-empty description="暂无数据" v-if="dataSourceMyNatAppointList.length == 0"/>
                </div>
            </van-tab>
        </van-tabs>
        <van-popup class="select-card-modal radius-10" v-model="showPopup">
            <div class="title">请选择就诊人</div>
            <div class="popup">
                <div class="content">
                    <div class="card" v-for="(item, index) in dataSourceCardInfo" :key="'card' + index" @click="handleClosePopup(item)">
                        <div class="wrap">
                            <div class="row">
                                <span class="name">{{item.patName}}</span>
                            </div>
                            <div class="row card-code">
                                <span>卡号 :</span>
                                <span>{{item.patMedNo}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </van-popup>
    </div>
</template>
<script>
export default {
    name: "NucleicAcidTestAppoint",
    data() {
        return {
            active: 0,
            patId: "",
            patName: "",
            patMedNo: "",
            idNo: "",
            showPopup: false,
            dataSourceCardInfo: "",
            dataSourceNatAppointList: "",
            dataSourceMyNatAppointList: "",
        };
    },
    mounted() {
        let query = this.$route.query;
        this.updateDeepLink(query);
        this.queryData();
    },
    computed: {
        nucleicAcidTestAppointSuccessTips() {
            return this.$store.state.nucleicAcidTestAppointSuccessTips;
        }
    },
    methods: {
        handleShowSelectCard() {
            this.showPopup = true;
        },
        handleClosePopup(item) {
            this.showPopup = false;
            this.updateDeepLink(item);
            this.getNatAppointList();
            this.queryMyNatAppointList();
        },
        queryData() {
            let queryCardInfoConfig = {
                data: {
                    patId: "",
                },
                reqLoading: false,
                resToastClear: false
            }
            let requestNatAppointListConfig = {
                data: {},
                reqLoading: false,
                resToastClear: false
            }
            let requestMyNatAppointListConfig = {
                data: {
                    patId: this.patId,
                    idNo: this.idNo
                },
                reqLoading: false,
                resToastClear: false
            }
            let outpatientApis = this.$api.outpatient;
            let queryCardInfo = outpatientApis.queryCardInfo;
            let getNatAppointList = outpatientApis.getNatAppointList;
            let queryMyNatAppointList = outpatientApis.queryMyNatAppointList;
            this.$api.all(queryCardInfo(queryCardInfoConfig), getNatAppointList(requestNatAppointListConfig), queryMyNatAppointList(requestMyNatAppointListConfig)).then((response) => {
                let cardInfo = response[0].data.Data;
                let natAppointList = this.wrapNatAppointListData(response[1].data.Data);
                let myNatAppointList = this.wrapMyNatAppointListData(response[2].data.Data);
                this.dataSourceCardInfo = cardInfo ? cardInfo : "";
                this.dataSourceNatAppointList = natAppointList ? natAppointList : "";
                this.dataSourceMyNatAppointList = myNatAppointList ? myNatAppointList : "";
            })
        },
        getNatAppointList(callback) {
            this.$api.outpatient.getNatAppointList({
                data: {},
                reqLoading: false,
                resToastClear: false
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSourceNatAppointList = this.wrapNatAppointListData(response.data.Data);
                    callback && callback();
                }
            })
        },
        queryMyNatAppointList(callback) {
            this.$api.outpatient.queryMyNatAppointList({
                data: {
                    patId: this.patId,
                    idNo: this.idNo
                },
                reqLoading: false,
                resToastClear: false
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.dataSourceMyNatAppointList = this.wrapMyNatAppointListData(response.data.Data);
                    callback && callback();
                }
            })
        },
        handleGoPayOrder(index) {
            this.queryMyNatAppointList(() => {
                let curData = this.dataSourceMyNatAppointList[index];
                if (curData.status == 0) {
                    let query = this.$route.query;
                    this.$router.push({name: "NucleicAcidTest", query})
                }
                else {
                    this.$toast({
                        message: "该订单超时未支付，系统已经取消",
                        duration: 2000,
                        forbidClick: false
                    });
                }
            });
        },
        handleNatAppoint(isAvailable, appointId, index) {
            if (isAvailable) {
                this.getNatAppointList(() => {
                    let curData = this.dataSourceNatAppointList[index];
                    if (curData.availableNum > 0) {
                        this.$api.outpatient.natAppoint({
                            data: {
                                appointId,
                                patId: this.patId,
                                patName: this.patName,
                                idNo: this.idNo,
                            },
                            reqLoading: false,
                            resToastClear: false,
                            resLoading: true,
                        }).then((response) => {
                            if (response.data.Code == 0) {
                                this.$dialog.alert({
                                    message: this.nucleicAcidTestAppointSuccessTips,
                                })
                                let query = this.$route.query;
                                this.$router.push({name: "NucleicAcidTest", query});
                            }
                        })
                    }
                    else {
                        this.$toast({
                            message: "该天已约满",
                            duration: 2000,
                            forbidClick: false
                        });
                    }
                })
            }
        },
        handleRefresh(name, title) {
            if (name == 0) {
                this.getNatAppointList();
            }
            else if (name == 1){
                this.queryMyNatAppointList();
            }
        },
        wrapNatAppointListData(data) {
            if (data) {
                return data.map((curVal) => {
                    curVal.status = curVal.availableNum > 0;
                    return curVal;
                })
            }
            else {
                return data;
            }
        },
        wrapMyNatAppointListData(data) {
            if (data) {
                return data.map((curVal) => {
                    curVal.statusLabel = this.$store.state.locale["register-status-" + curVal.status];
                    return curVal;
                })
            }
            else {
                return data;
            }
        },
        updateDeepLink(data) {
            this.patId = data.patId;
            this.patName = data.patName;
            this.patMedNo = data.patMedNo;
            this.idNo = data.patIdNo;
            let query = this.$route.query;
            // 确保newQuery的key与query的key始终保持一致
            let newQuery = {
                patId: this.patId,
                patName: this.patName,
                patMedNo: this.patMedNo,
                patIdNo: this.idNo
            };
            // 判断如果newQuery不等于query就重置deeplink中的参数
            let isResetQuery = false;
            for (let key in newQuery) {
                if (query[key] != newQuery[key]) {
                    isResetQuery = true;
                }
            };
            if (isResetQuery) {
                this.$router.replace({name: "NucleicAcidTestAppoint", query: newQuery});
            }
        }
    }
};

</script>
<style scoped>
.patient {
    background-color: #fff;
    color: #494d5a;
}
/* 左边tab内容 */
.schedule-title {
    height: 40px;
}
.schedule-title .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
}
.schedule-title .van-col>span {
    font-size: 13px;
}
.schedule-content {
    height: 40px;
    margin-top: 10px;
}
.schedule-content .van-col {
    display: flex;
    justify-content:center;
    align-items: center;
    height: 100%;
    color: #88929d;
    font-size: 12px;
}
.schedule-content .van-col>span {
    height: 40px;
    line-height: 40px;
    width: 130px;
    text-align: center;
    color: #0078FF;
    background-color: #fff;
}
.schedule-content.active .am>span,
.schedule-content.active .pm>span {
    color: #fff;
    background-color: #0078FF;
    box-shadow: 0px 0px 5px #0aaaf3;
}
.schedule-content.active .date {
    color: #0078FF;
}
.schedule-content span.active1-item {
    background-color: #d6d9da !important;
    color: #58626c !important;
    box-shadow: none !important;
}
/* 右边tab内容 */
.content {
    background-color: #fff;
    padding: 10px 15px;
    width: 100%;
}
.content .van-row .van-col{
    height: 30px;
    line-height: 30px;
    padding: 5px 0;
    font-size: 14px;
}
.content .van-row .van-col:first-child {
    display: flex;
    align-items: center;
    color: #6d6868;
}
.content .van-row .van-col:last-child {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #000;
}
.content .van-row.order-state .van-col{
    height: 40px;
    border-bottom: 0.5px solid #e2d7d7;
}
.content .btn {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 15px;
    border-top: 0.5px solid #e2d7d7;
}
.content .btn .operate-btn {
    width: 70px;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    color: #fff;
    padding: 5px;
    border-radius: 3px;
    font-size: 14px;
}
.size1 {
    font-size: 14px !important;
}
.color1 {
    color:#ce8e1b !important;
}
/* 就诊人选择弹框 */
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
/* tab标签样式 */
.detail-type>>>.van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.detail-type>>>.van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
</style>



