<template>
    <div>
    </div>
</template>
<script>
export default {
    name: "Transfer",
    data() {
        return {
            
        };
    },
    mounted() {
        let auth_code, backurl, state;
        let href = window.location.href;
        let url = href.includes("?") ? href.split("?")[1] : "";
        let search = url.split("&");
        for (let i = 0; i < search.length; i++) {
            let item = search[i];
            if (item.includes("auth_code")) {
                auth_code = item.split("=")[1];
            }
            if (item.includes("backurl")) {
                backurl = item.split("=")[1];
            }
            if (item.includes("state")) {
                state = item.split("=")[1];
            }
        };
        
        window.location = `${this.$store.state.transferUrl}?auth_code=${auth_code}&backurl=${backurl}&state=${state}`;
    }
};
</script>
<style scoped>

</style>


