<template>
    <div class="card-container pr bgimg100">
        <div class="color-fff font18  p-t-15 p-l-20 tl font-w6">{{type == 0 ? "门诊" : "住院"}}</div>
        <div class="card-box pa bgimg100" :class="classObjectBg">
            <div class="card-content-unbind pa" v-if="!status" @click="handleGoBindCard">
                <div class="add bgimg100 fl hgt_full"></div>
                <div class="fl color-fff p-l-5 hgt_full flex_column flex_item_between">
                    <div class="tl font19 font-w6">{{type == 0 ? "添加就诊人" : "添加住院人"}}</div>
                    <div class="tl font13">{{type == 0 ? "您还没有就诊人请先添加" : "您还没有住院人请先添加"}}</div>
                </div>
            </div>
            <div class="card-content-binded flex_column flex_item_between" v-else>
                <div class="profile flex_dom flex_item_mid">
                    <span class="name color-fff font17 m-r-10">{{dataInner.patName}}</span>
                    <span class="sex bgimg100" :class="classObjectSex"></span>
                </div>
                <div class="card-code flex_column color-fff">
                    <span class="code-label font12 m-b-5">{{type == 0 ? "卡号" : "住院号"}}</span>
                    <span class="code font20 font-w6">{{dataInner.patMedNo || dataInner.inHosNo}}</span>
                </div>
                <div class="flex_dom">
                    <div class="operate bg-fff m-r-5 flex_dom flex_item_center flex_item_mid">
                        <span class="transfer-icon bgimg100"></span>
                        <span class="font13 m-l-5" @click="handleSetDefault">切换</span>
                    </div>
                    <div class="operate bg-fff flex_dom flex_item_center flex_item_mid">
                        <span class="qrcode-icon bgimg100"></span>
                        <span class="font13 m-l-5" @click="handleOnGoShowCode">刷码</span>
                    </div>
                </div>
            </div>
            <div class="default-icon pa bgimg100" v-if="status ? isDefault : false"></div>
        </div>
    </div>
</template>
<script>
export default {
    name: "com-card",
    data() {
        return {

        };
    },
    props: {
        status: {
            type: Boolean,
            default: false
        },
        type: {
            type: [Number, String],
            default: 0
        },
        data: {
            type: Object,
            default: {
                patName: "姓名",
                patSex: 1,
                patMedNo: "1111111111111111",
            }
        },
        isDefault: {
            type: Boolean,
            default: true
        },
    },
    computed: {
        classObjectBg() {
            return {
                outpatient: this.type == 0,
                hospital: this.type == 1
            }
        },
        classObjectSex() {
            return {
                man: this.dataInner.patSex == 1,
                woman: this.dataInner.patSex == 2
            }
        },
        dataInner() {
            return this.data;
        }
    },
    methods: {
        handleOnGoShowCode() {
            this.$emit("show-code");
        },
        handleGoBindCard() {
            this.$emit("go-bind-card");
        },
        handleSetDefault() {
            this.$emit("set-default");
        }
    }
};
</script>
<style scoped>
.card-container {
    width: 100%;
    height: 159.5px;
    background: url('../../assets/images/card_bg.png') no-repeat;
}
.card-box {
    width: 345px;
    height: 150px;
    top: 50px;
    left: 50%;
    transform: translateX(-50%);
    padding: 17px 15px;
    border-radius: 0 40px 10px 10px / 0 40px 10px 10px;
    box-shadow: 0px 40px 50px -30px rgba(0, 150, 255, .4);
}
.card-box.outpatient {
    background: url('../../assets/images/outpatient/card_sub_bg.png') no-repeat;
}
.card-box.hospital {
    background: url('../../assets/images/hospital/card_sub_bg.png') no-repeat;
}
.card-content-unbind {
    bottom: 43.5px;
    height: 47.5px;
}
.card-content-unbind .add {
    width: 47.5px;
    background: url('../../assets/images/add.png') no-repeat;
}
.card-content-binded {
    height: 100%;
    text-align: left;
}
.card-content-binded .sex {
    width: 21px;
    height: 21px;
}
.card-content-binded .sex.man {
    background: url('../../assets/images/man.png') no-repeat;
}
.card-content-binded .sex.woman {
    background: url('../../assets/images/woman.png') no-repeat;
}
.card-content-binded .code-label {
    color: rgba(255, 255, 255, 0.6);
}
.card-content-binded .operate {
    width: 70px;
    height: 28px;
    border-radius: 14px;
    color: #0078ff;
    box-shadow: 0px 0px 4px;
}
.qrcode-icon {
    background: url('../../assets/images/qr_code.png') no-repeat;
    width: 13px;
    height: 13px;
}
.transfer-icon {
    background: url('../../assets/images/transfer.png') no-repeat;
    width: 13px;
    height: 13px;
}
.default-icon {
    background: url('../../assets/images/default.png') no-repeat;
    width: 21px;
    height: 21px;
    right: 15px;
    top: 15px;
}
</style>


