<template>
    <div>
        <com-code-detail :type="0" :data="dataSource"></com-code-detail>
    </div>
</template>
<script>
import CCodeDetail from "../../common/CCodeDetail";
export default {
    name: "CodeDetail",
    components: {
        [CCodeDetail.name]: CCodeDetail,
    },
    data() {
        return {
            dataSource: [],
            promise: null,
            cardInfo: ""
        };
    },
    mounted() {
        let patId = this.$route.query.patId;
        this.queryCardInfo(patId);
    },
    methods: {
        queryCardInfo(patId) {
            let owner = this;
            owner.$api.outpatient.queryCardInfo({
                data: {patId: ""},
                resToastClear: false,
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    if (patId) {
                        for (let i = 0; i < resData.length; i++) {
                            let item = resData[i];
                            if (item.patId == patId) {
                                item.isDefault = "0";
                                owner.queryQrcodeStr(patId);
                                owner.promise.then(function(value) {
                                    item.qrcodeStr = value;
                                    owner.dataSource = resData;
                                    owner.$toast.clear();
                                })
                            }
                            else {
                                item.isDefault = "1";
                            }
                        }
                    }
                    else {
                        for (let i = 0; i < resData.length; i++) {
                            let item = resData[i];
                            if (item.isDefault == 0) {
                                patId = item.patId;
                                owner.queryQrcodeStr(patId);
                                owner.promise.then(function(value) {
                                    item.qrcodeStr = value;
                                    owner.dataSource = resData;
                                    owner.$toast.clear();
                                })
                            }
                        }
                    }
                }
            })
        },
        queryQrcodeStr(patId) {
            let owner = this;
            owner.promise = new Promise(function(resolve, reject) {
                owner.$api.outpatient.getQrcodeStr({
                    data: {patId},
                    reqLoading: false,
                    resToastClear: false,
                }).then ((response) => {
                    if (response.data.Code == 0) {
                        resolve(response.data.Data)
                    }
                })
            });
        }
    }
};
</script>
<style scoped>

</style>


