<template>
    <div class="charge main-bg">
        <van-form @submit="handleGoCharge" :show-error-message="false">
            <van-field
            class="radius-10"
            v-model="code"
            type="number"
            placeholder="请输入他人就诊卡号"
            :rules="[{ required: true, message: '请输入他人就诊卡号' }]"
            center
            clearable
            ></van-field>
            <div class="tips">
                <span class="strong">温馨提示：</span><span>请输入患者相关信息，并再准确核对充值信息后，再执行充值操作！</span>
            </div>
            <base-button class="m-t-20" text="确定" native-type="submit" :btnDisabled="btnDisabled" size="large" />
            <div class="bottom-tips"><span class="strong" @click="handleGoQueryOthersCardByName">不知道就诊卡号</span></div>
        </van-form>
    </div>
</template>

<script>
import BButton from "../../base/BButton";
export default {
    name: "QueryOthersCardByNum",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            type: "",
            code: "",
            btnDisabled: false
        };
    },
    mounted() {
        this.type = this.$route.query.type;
    },
    methods: {
        handleGoCharge() {
            let systemType = this.globalMethods.getCookie("systemType");
            let payType = systemType == "0A" ? "支付宝" : "微信";
            this.$dialog.confirm({
                title: '提示',
                confirmButtonText: "继续",
                message: `请使用本人的${payType}进行支付,勿让他人代付，窗口进行退费时，费用将按原支付方式返还到${payType}账户`,
            })
            .then(() => {
                this.queryCardInfoById();
            })
            .catch(() =>{})
        },
        handleGoQueryOthersCardByName() {
            let type = this.type;
            this.$router.replace({name: "QueryOthersCardByName", query: { type }});
        },
        queryCardInfoById() {
            this.btnDisabled = true;
            this.$api.outpatient.queryCardInfoById({
                data: {
                    code: this.code,
                    type: 1
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: {
                    forbidClick: false
                },
            }).then((response) => {
                if (response.data.Code == 0) {
                    let patId = response.data.Data[0].patId;
                    this.$router.replace({name: this.type, query: { patId }});
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            })
        }
    }
};

</script>
<style scoped>
.tips {
    padding: 10px;
    font-size: 12px;
    text-align: left;
    color: gray;
    line-height: 17px;
}
.strong {
    color: #0078ff;
}
.bottom-tips {
    font-size: 12px;
    text-align: right;
    padding: 10px;
    margin: 10px;
    height: 20px;
    line-height: 20px;
}

</style>



