<template>
    <div class="container main-bg">
        <van-form @submit="handleSubmit" :show-error-message="false">
        <!-- 用户类型 -->
        <van-field v-model="userType" name="userType" v-show="false"></van-field>
        <van-tabs 
            class="user-type radius-10" 
            v-model="isChild"
            title-inactive-color="#818594"
            title-active-color="#292C34"
            v-show="!authCode"
            >
            <van-tab title="成人">
                <template #title>
                    <span class="custom-tab-title">成人</span>
                </template>
            </van-tab>
            <van-tab title="儿童"></van-tab>
        </van-tabs>
        <div class="section radius-10">
            <!-- 用户名 -->
            <van-field
                name="patName"
                v-model="patName"
                label="姓名"
                label-width="120"
                :placeholder="isChild ? '请输入儿童姓名' : '请输入姓名'"
                :rules="[{ required: true, message: '请输入儿童姓名' }]"
                :readonly="!!authCode"
                clearable
            ></van-field>
            <!-- 性别 -->
            <van-field
                name="patSex"
                label="性别"
                label-width="120"
                v-if="isChild"
                >
                    <template #input>
                        <van-radio-group v-model="patSex" direction="horizontal">
                            <van-radio name="1">男性</van-radio>
                            <van-radio name="2">女性</van-radio>
                        </van-radio-group>
                    </template>
            </van-field>
            <!-- 出生日期 -->
            <template v-if="isChild">
                <van-field
                    name="patBirthday"
                    v-model="patBirthday"
                    label="出生日期"
                    label-width="120"
                    placeholder="请选择出生日期"
                    @click-input="handleSelectDate"
                    :rules="[{ required: true, message: '请选择出生日期' }]"
                    readonly
                ></van-field>
                <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
                    <van-datetime-picker
                    v-model="currentDate"
                    type="date"
                    title="选择年月日"
                    :min-date="minDate"
                    :max-date="maxDate"
                    @confirm="handleDateComfirm"
                    @cancel="handleDateCancel"
                    />
                </van-popup>
            </template>
            <!-- 关系 -->
            <template v-if="!isSelf">
                <van-field
                    name="patRelation"
                    v-model="patRelation"
                    label="关系"
                    label-width="120"
                    placeholder="请选择与用户的关系"
                    @click="handleSelectRelation"
                    :rules="[{ required: true, message: '请选择与用户的关系' }]"
                    readonly
                ></van-field>
                <van-popup v-model="showRelationPicker" round position="bottom">
                    <van-picker
                    show-toolbar
                    :columns="columns"
                    @cancel="showRelationPicker = false"
                    @confirm="handleConfirm"
                    />
                </van-popup>
            </template>
            <!-- 监护人姓名 -->
            <van-field
                name="guardianName"
                v-model="guardianName"
                label="监护人姓名"
                label-width="120"
                placeholder="请输入监护人姓名"
                :rules="[{ required: true, message: '请输入监护人姓名' }]"
                clearable
                v-if="isChild"
            ></van-field>
            <!-- 监护人身份证号 -->
            <van-field
                name="patIdNo"
                v-model="patIdNo"
                :label="isChild ? '监护人身份证号' : '身份证号'"
                :placeholder="isChild ? '请输入监护人身份证号' : '请输入身份证号'"
                label-width="120"
                maxlength="18"
                :rules="[{ required: true, message: '请输入身份证号' }]"
                :readonly="!!authCode"
                clearable
            ></van-field>
            <!-- 社保卡号 -->
            <van-field
                name="ybCardNo"
                v-model="ybCardNo"
                label="社保卡号"
                placeholder="请输入社保卡号"
                label-width="120"
                maxlength="18"
                :rules="[{ required: true, message: '请输入社保卡号' }]"
                v-if="authCode"
                readonly
                clearable
            ></van-field>
            <!-- 监护人手机号 -->
            <van-field
                name="patPhone"
                type="number"
                v-model="patPhone"
                :label="isChild ? '监护人手机号' : '手机号'"
                :placeholder="isChild ? '请输入监护人手机号' : '请输入手机号'"
                label-width="120"
                maxlength="11"
                :rules="[{ required: true, message: '请输入手机号' }]"
                clearable
            ></van-field>
        </div>
        <div class="section radius-10">
            <!-- 是否本人 -->
            <van-field 
                name="switch" 
                label="是否本人" 
                label-width="120"
                >
                    <template #input>
                        <van-switch v-model="isSelf" size="20" />
                    </template>
            </van-field>
        </div>
        <!-- 提示 -->
        <div class="tips">
            <p class="normal m-b-5">温馨提示：</p>
            <p class="normal">1、若您还没有就诊卡，本次添加后将使用您提供的 <span class="danger">姓名、身份证号</span> 为您注册。</p>
            <p class="normal">2、若您已持有就诊卡，请确保<span class="danger">就诊卡内记录有您的身份证号（先到窗口补录身份证后再绑卡）</span> ，不要随意注册档案，否则可能导致您的病例信息丢失。</p>
        </div>
        <base-button text="添加就诊人" size="large" native-type="submit" :btnDisabled="btnDisabled" />
    </van-form>
    <van-popup class="select-card-modal radius-10" v-model="showPopup" @close="handleClosePopup">
        <div class="title">该用户有多张就诊卡，请选择要绑定的就诊卡</div>
        <div class="popup">
            <div class="content">
                <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="bindCard(item.patId)">
                    <div class="wrap">
                        <div class="row">
                            <span class="name">{{item.patName}}</span>
                        </div>
                        <div class="row card-code">
                            <span>卡号 :</span>
                            <span>{{item.patMedNo}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </van-popup>
  </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "BindCard",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            isChild: 0,
            patName: "",
            patSex: "1",
            currentDate: new Date(),
            minDate: new Date(1990, 0, 1),
            maxDate: new Date(2030, 11, 31),
            showDatePicker: false,
            patBirthday: "",
            isSelf: true,
            guardianName: "",
            patRelation: "",
            showRelationPicker: false,
            columns: ["父母", "配偶", "子女", "亲属", "朋友", "同事", "其他"],
            patIdNo: "",
            //社保卡号
            ybCardNo: "",
            //社保卡序列号
            noSeqNo: "",
            patPhone: "",
            showPopup: false,
            btnDisabled: false,
            cardList: [],
            formData: {},
            hisDataList: [],
        };
    },
    computed: {
        userType() {
            return this.isChild;
        },
        allowCreatedCard() {
            return this.$store.state.allowCreatedCard
        },
        sociaSecuCard() {
            return this.$store.state.sociaSecuCard;
        },
        newCardSuccessTips() {
            return this.$store.state.newCardSuccessTips;
        },
        authCode() {
            let authCode = null;
            let href = window.location.href;
            if (href.includes("authCode")) {
                let search = href.split("?")[1].split("&");
                for (let item of search) {
                    if (item.includes("authCode")) {
                        authCode = item.split("=")[1];
                        break;
                    }
                }
            }
            return authCode;
        }
    },
    mounted() {
        if (this.authCode) {
            this.$api.outpatient.getSociaSecuCardInfo({
                data: {
                    code: this.authCode
                }
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    this.patName = data.name;
                    this.patIdNo = data.cardId;
                    this.ybCardNo = data.cardNo;
                    this.noSeqNo = data.noSeqNo;
                }
            })
        }
    },
    methods: {
        handleSubmit(values) {
            let formData = values;
            //验证身份证号和手机号格式
            if (!this.validForm(formData)) {
                return;
            };
            this.formData = formData;
            this.btnDisabled = true;
            // 社保卡授权码存在，绑定社保卡，否则先查询卡列表再绑定就诊卡
            if (this.authCode) {
                this.bindSociaSecuCard();
            }
            else {
                this.queryCardList();
            }
        },
        queryCardList() {
            let data = {};
            let formData = this.formData;
            let userType = formData.userType;
            data["patName"] = formData.patName;
            if (userType) {
                data["guardianIdNo"] = formData.patIdNo;
                data["guardianPhone"] = formData.patPhone;
            }
            else {
                data["patIdNo"] = formData.patIdNo;
                data["patPhone"] = formData.patPhone;
            }
            this.$api.outpatient.queryCardList({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    this.hisDataList = resData;
                    if (resData.length == 1) {
                        let patId = resData[0].patId;
                        this.bindCard(patId);
                    }
                    else {
                        this.cardList = resData;
                        this.showPopup = true;
                    }
                }
                else if (response.data.Code == 1) {
                    if (this.allowCreatedCard) {
                        this.$dialog.confirm({
                            title: '提示',
                            message: '系统检测到您还没有就诊卡，点击确定将使用您输入的信息为您建卡',
                        })
                        .then(() => {
                            this.newCard();
                        })
                        .catch(() =>{
                            this.btnDisabled = false;
                        })
                    }
                    else {
                        this.$dialog.alert({
                            title: '提示',
                            message: '系统检测到您还没有就诊卡，请先到窗口建卡'
                        })
                        .then(() => {
                            this.btnDisabled = false;
                        })
                    }
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        handleSelectDate() {
            this.closeAllPicker();
            this.showDatePicker = true;
        },
        handleSelectRelation() {
            this.closeAllPicker();
            this.showRelationPicker = true;
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
        handleDateComfirm(value) {
            let year = value.getFullYear();
            let month = (Number(value.getMonth()) + 1) + "";
            month = month.length == 1 ? ("0" + month) : month;
            let day = value.getDate() + "";
            day = day.length == 1 ? ("0" + day) : day;
            this.patBirthday = year + "-" + month + "-" + day;
            this.showDatePicker = false;
        },
        handleConfirm(value) {
            this.patRelation = value;
            this.showRelationPicker = false;
        },
        handleClosePopup() {
            this.btnDisabled = false;
        },
        bindSociaSecuCard() {
            let data = {};
            let formData = this.formData;
            data["ybCardNo"] = formData.ybCardNo;
            data["ybCardSeq"] = this.noSeqNo;
            this.newCard(data);
        },
        bindCard(patId) {
            let owner = this;
            let formData = this.formData;
            let hisData;
            for (let i = 0; i < owner.hisDataList.length; i++) {
                let item = owner.hisDataList[i];
                if (item.patId == patId) {
                    hisData = item;
                    break;
                }
            };
            let data = owner.getParams(hisData, formData);
            data["patId"] = patId;
            data["patMedNo"] = hisData.patMedNo;
            this.queryBind(data);
        },
        queryBind(data) {
            this.$api.outpatient.bindCard({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("binded", true);
                    this.toastSuccess("绑定成功");
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg);
                }
            })
        },
        newCard(obj) {
            let data = this.getParams("", this.formData);
            if (obj) {
                data = Object.assign(data, obj);
            };
            this.$api.outpatient.createCard({
                data,
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.globalMethods.updateStatus("binded", true);
                    this.toastSuccess(this.newCardSuccessTips || "创建成功");
                }
                else if (response.data.Code != -2) {
                    this.toastFail(response.data.Msg)
                }
            })
        },
        getParams(hisData, formData) {
            let data = {};
            let userType = formData.userType;
            data["isChild"] = userType;
            data["patRelation"] = hisData.patRelation || formData.patRelation || "本人";
            data["patName"] = hisData.patName || formData.patName || "";
            data["patIdNo"] = !userType ? (hisData.patIdNo || formData.patIdNo) : "";
            data["patPhone"] = !userType ? (hisData.patPhone || formData.patPhone) : "";
            data["patSex"] = hisData.patSex || formData.patSex || "";
            data["patBirthday"] = hisData.patBirthday || formData.patBirthday || "";
            data["guardianName"] = hisData.guardianName || formData.guardianName || "";
            data["guardianIdNo"] = userType ? (hisData.guardianIdNo || formData.patIdNo) : "";
            data["guardianPhone"] = userType ? (hisData.guardianPhone || formData.patPhone) : "";
            return data;
        },
        validForm(values) {
            if(!/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(values.patIdNo)) {
                this.$toast({
                    message: "身份证号格式错误",
                    forbidClick: false
                });
                return false;
            }
            if(!/^1[3456789]\d{9}$/.test(values.patPhone)) {
                this.$toast({
                    message: "手机号格式错误",
                    forbidClick: false
                });
                return false;
            };
            return true;
        },
        closeAllPicker() {
            this.showDatePicker = false;
            this.showRelationPicker = false;
        },
        toastFail(msg) {
            this.btnDisabled = false;
            this.$toast({
                message: msg,
                forbidClick: false
            })
        },
        toastSuccess(msg) {
            this.$toast({
                type: "success",
                message: msg,
                forbidClick: true,
                onClose: () => {
                    this.$router.go(this.sociaSecuCard ? -2 : -1);
                }
            })
        }
    }
};
</script>
<style scoped>
.container >>> .van-cell::after {
    right: 16px;
}
.user-type >>> .van-tabs__line {
    background-color: #0078FF;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    width: 30px!important;
    bottom: 20px;
}
.user-type >>> .van-tab--active {
    font-weight: bold;
}
.custom-tab-title {
    display: block;
    width: 172px;
    border-right: .5px solid #CDCDCD;
}
.section {
    margin-top: 10px;
}
.tips {
    padding: 20px 0 30px 0;
    color: #494D5A;
    font-size: 12px;
    text-align: left;
}
.normal {
    line-height: 20px;
}
.danger {
    color: #FF6257;
}
.confirm-btn {
    height: 43px;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}

</style>



