<template>
    <div class="main-bg">
        <div>
            <div class="clear">
                <van-cell class="patient" :border="false" :title="patName + ' ' + inHosNo" title-style="text-align: left;" is-link @click="handleShowSelectCard"/>
                <van-cell class="date fr"  is-link arrow-direction="down" :title="year + '年' + month + '月' + day + '日'" title-style="text-align: right;" @click="handleShowDatePicker"/>
            </div>
            <div class="grid" v-if="dataSource.length > 0">
                <van-row class="row">
                    <van-col span="7">类目</van-col>
                    <van-col span="5">规格</van-col>
                    <van-col span="4">单价(元)</van-col>
                    <van-col span="4">数量</van-col>
                    <van-col span="4">总价(元)</van-col>
                </van-row>
                <van-row class="row" v-for="(item, index) in dataSource" :key="'row' + index">
                    <van-col span="7">{{item.itemName}}</van-col>
                    <van-col span="5">{{item.spec}}</van-col>
                    <van-col span="4">{{Number(item.price).toFixed(2)}}</van-col>
                    <van-col span="4">{{Number(item.number).toFixed(2)}}</van-col>
                    <van-col span="4">{{Number(item.consume).toFixed(2)}}</van-col>
                </van-row>
                <van-row>
                    <van-col span="7">总计</van-col>
                    <van-col span="5"> - </van-col>
                    <van-col span="4"> - </van-col>
                    <van-col span="4"> - </van-col>
                    <van-col span="4">{{total}}</van-col>
                </van-row>
            </div>
            <van-empty description="暂无数据" v-if="dataSource.length == 0"/>
            <van-popup v-model="showDatePicker" round position="bottom" :style="{ height: '40%' }">
                <van-datetime-picker
                v-model="currentDate"
                type="date"
                title="选择查询日期"
                @confirm="handleDateComfirm"
                @cancel="handleDateCancel"
                />
            </van-popup>
            <van-popup class="select-card-modal radius-10" v-model="showPopup">
                <div class="title">请选择就诊人</div>
                <div class="popup">
                    <div class="content">
                        <div class="card" v-for="(item, index) in cardList" :key="'card' + index" @click="handleClosePopup(item.inHosNo, item.patName)">
                            <div class="wrap">
                                <div class="row">
                                    <span class="name">{{item.patName}}</span>
                                </div>
                                <div class="row card-code">
                                    <span>住院号 :</span>
                                    <span>{{item.inHosNo}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </van-popup>
        </div>
    </div>
</template>
<script>
export default {
    name: "InHosList",
    data() {
        return {
            dataSource: "",
            startDate: "",
            endDate: "",
            showDatePicker: false,
            currentDate: new Date(),
            year: new Date().getFullYear(),
            month: new Date().getMonth() + 1,
            day: new Date().getDate(),
            total: 0,
            inHosNo: "",
            patName: "",
            cardList: "",
            showPopup: false,
        };
    },
    mounted() {
        this.getDate();
        this.queryCardInfo();
    },
    methods: {
        handleShowSelectCard() {
            this.showPopup = true;
        },
        handleClosePopup(inHosNo, patName) {
            this.showPopup = false;
            this.inHosNo = inHosNo;
            this.patName = patName;
            this.queryData();
        },
        queryCardInfo() {
            this.$api.inHosNo.queryPatientInfoList({
                data: {inHosNo: ""},
                reqLoading: false,
                resToastClear: false,
            }).then((response) => {
                if (response.data.Code == 0) {
                    let data = response.data.Data;
                    for (let i = 0; i < data.length; i++)　{
                        let item = data[i];
                        if (item.isDefault == 0) {
                            this.inHosNo = item.inHosNo;
                            this.patName = item.patName;
                            this.queryData();
                            break;
                        }
                    };
                    this.cardList = response.data.Data;
                }
            })
        },
        queryData() {
            this.$api.inHosNo.queryInHosList({
                data: {
                    startDate: this.startDate,
                    endDate: this.endDate,
                    inHosNo: this.inHosNo
                }
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.dataSource = this.wrapData(response.data.Data);
                    setTimeout(() => {this.perfectGrid()}, 100);
                }
                else if (response.data.Code != -2) {
                    this.dataSource = ""
                }
            });
        },
        getDate() {
            let date = this.globalMethods.getDate(0);
            this.startDate = `${date.year}-${date.month}-${date.day}`;
            this.endDate = this.startDate;
        },
        perfectGrid() {
            let rowEle = $(".row");
            for (let i = 0; i < rowEle.length; i++) {
                let item = rowEle.eq(i);
                let height = item.height();
                let cols = item.find(".van-col");
                for (let k = 0; k < cols.length; k++) {
                    cols.eq(k).css("min-height", height);
                }
            };
        },
        wrapData(data) {
            let total = 0;
            for (let i = 0; i < data.length; i++) {
                let item = data[i];
                total +=  parseFloat(Number(item.consume).toFixed(2));
            }
            this.total = Number(total).toFixed(2);

            return data;
        },
        handleShowDatePicker() {
            this.showDatePicker = true;
        },
        handleDateComfirm(val) {
            this.showDatePicker = false;
            this.year = val.getFullYear();
            this.month = val.getMonth() + 1;
            let month = `${this.month}`.length > 1 ? this.month : `0${this.month}`;
            this.day = val.getDate();
            let day = `${this.day}`.length > 1 ? this.day : `0${this.day}`;
            this.startDate = `${this.year}-${month}-${day}`;
            this.endDate = this.startDate;
            this.queryData();
        },
        handleDateCancel() {
            this.showDatePicker = false;
        },
    }
};

</script>
<style scoped>
.patient {
    background-color: #0078ff;
    color: #fff;
}
.patient .van-icon {
    color: #fff;
}
.date {
    width: 160px;
    background-color: transparent;
}
.grid {
    width: 95%;
    margin: 10px auto;
    padding: 0 1px 1px 0;
    border-bottom: 0.5px solid #a5a5a5;
    border-right: 0.5px solid #a5a5a5;
}
.grid .van-row:nth-child(2n-1) {
    background-color: #ececec;
}
.grid .van-row:nth-child(2n) {
    background-color: #fff;
}
.grid .van-row .van-col {
    min-height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 0.5px solid #a5a5a5;
    border-left: 0.5px solid #a5a5a5;
}
.grid .van-row .van-col:first-child {
    padding-left: 3px;
    justify-content: flex-start;
    text-align: left;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
    padding-bottom: 10px;
}
.popup .card {
    width: 93%;
    background-color: #fff;
    border-radius: 15px;
    padding: 15px;
    margin: 10px auto 0 auto;
    box-sizing: border-box;
}
.popup .card .wrap {
    height: 100%;
}
.popup .wrap .row {
    text-align: left;
    margin-bottom: 10px;
}
.popup .wrap .name {
    color: #292C34;
}
.popup .wrap .card-code {
    font-size: 13px;
    color: #818594;
    margin-bottom: 0px;
}
</style>



