<template>
    <div id="app">
        <router-view/>
        <com-tabbar :data="data" @change="handleOnChange" @add-card="handelGoAddCard" :active="active"></com-tabbar>
    </div>
</template>
<script>
import CTabbar from "../common/CTabbar";
export default {
  name: "Tabbar",
  components: {
        [CTabbar.name]: CTabbar,
    },
data() {
    return {
        active: 0,
        data: {
                text: ["门诊", "住院", "医院信息", "我的"],
                defaultIcon: [
                    require('../../assets/images/nav_outpatient.png'),
                    require('../../assets/images/nav_hospital.png'),
                    require('../../assets/images/nav_hospital_info.png'),
                    require('../../assets/images/nav_mine.png'),
                ],
                activeIcon: [
                    require('../../assets/images/nav_outpatient_active.png'),
                    require('../../assets/images/nav_hospital_active.png'),
                    require('../../assets/images/nav_hospital_info_active.png'),
                    require('../../assets/images/nav_mine_active.png'),
                ],
                defaultColor: "#8191A2",
                activeColor: "#0078FF",
            }
    };
},
computed: {
    sociaSecuCard() {
        return this.$store.state.sociaSecuCard;
    }
},
created() {
    this.checkActive();
},
beforeRouteUpdate (to, from, next) {
    this.checkActive(to.name);
    next();
},
methods: {
    handleOnChange(index) {
        this.active = index;
        if (index == 0) {
            this.$router.push({name: 'Outpatient'});
        }
        else if (index == 1) {
            this.$router.push({name: 'Hospital'});
        }
        else if (index == 2) {
            window.location = this.$store.state.hospitalInfoUrl;
        }
        else if (index == 3) {
            this.$router.push({name: 'Account'})
        }
    },
    handelGoAddCard() {
        if (this.active == 1) {
            this.$router.push({name: "BindInHosNo"});
        }
        else {
            let name = this.sociaSecuCard ? "SelectBindType" : "BindCard";
            this.$router.push({name});
        }
    },
    checkActive(name) {
        let routeName = name || this.$route.name;
        if (routeName == "Outpatient") {
            this.active = 0;
        }
        else if (routeName == "Hospital") {
            this.active = 1;
        }
        else if (routeName == "HospitalInfo") {
            this.active = 2;
        }
        else if (routeName == "Account") {
            this.active = 3;
        }
    }
}
};
</script>

<style scoped>

</style>



