<template>
    <div class="main-bg">
        <div class="base-info radius-10">
            <van-cell title="姓名" title-class="title-style" :icon="require('../../../assets/images/img/bs_name.png')" :value="dataSource.patName" value-class="value-style"/>
            <van-cell title="就诊卡号" title-class="title-style" :icon="require('../../../assets/images/img/bs_hospitalNum.png')" :value="dataSource.patMedNo" value-class="value-style"/>
            <van-cell title="科室" title-class="title-style" :icon="require('../../../assets/images/img/dd-keshi.png')" :value="dataSource.deptName" value-class="value-style"/>
            <van-cell title="医生姓名" title-class="title-style" :icon="require('../../../assets/images/img/dd-yisheng.png')" :value="dataSource.doctorName" value-class="value-style"/>
            <van-cell title="挂号费用" title-class="title-style" :icon="require('../../../assets/images/img/dd-feiyong.png')" :value="dataSource.totalFee && Number(dataSource.totalFee).toFixed(2) + '元'" value-class="value-style danger"/>
            <van-cell title="就诊时间" title-class="title-style" :icon="require('../../../assets/images/img/dd-riqi.png')" value-class="value-style">
                <template #default>
                    <div>{{dataSource.appointDate}}</div>
                </template>
            </van-cell>
        </div>
        <div class="clinic-type radius-10">
            <div class="title">请选择就诊类型</div>
            <van-radio-group v-model="clinicType" @change="handleChangeClinicType">
                <van-radio class="m-b-20" name="0">初诊</van-radio>
                <van-radio class="m-b-20" name="1">复诊</van-radio>
                <p class="tl font14 m-b-10 color-ff0000 wid100" v-if="clinicTypeTips">请选择就诊类型</p>
            </van-radio-group>
        </div>
        <template v-if="hasTimeRange">
            <div class="date-range radius-10">
                <div class="title flex_dom flex_item_between flex_item_mid">
                    <span>请选择就诊时间段</span>
                    <span class="color-ff6257">{{detailTimeRange}}</span>
                </div>
                <van-radio-group v-model="timeRange" direction="horizontal">
                    <van-radio v-for="(item, index) in dataSource.timeRange" :key="'timeRange' + index" :name="item.time" :disabled="item.isDisabled" @click="handleShowDetailTimeRange(item.isDisabled)">{{item.time}}</van-radio>
                    <p class="tl font14 m-b-10 color-ff0000 wid100" v-if="timeRangeTips">请选择就诊时间段</p>
                </van-radio-group>
            </div>
        </template>
        <base-button class="m-t-20" :text="isCurrent ? '立即支付' : '提交订单'" :btnDisabled="btnDisabled" size="large" @confirm="handleSubmit"/>
        <div class="notes" v-if="isCurrent">{{registerSubmitTips.current}}</div>
        <div class="notes" v-else>{{registerSubmitTips.appoint}}</div>
        <van-popup class="select-card-modal radius-10" :close-on-click-overlay="false" v-model="showPopup">
            <div class="title">请选择详细就诊时段</div>
            <div class="popup">
                <div class="content">
                    <van-radio-group v-model="detailTimeRange" direction="horizontal">
                        <van-radio v-for="(item, index) in detailTimeRangeDataSource" :key="'detailTimeRange' + index"  :name="item" @click="handleClosePopUp()">{{item}}</van-radio>
                    </van-radio-group>
                </div>
            </div>
        </van-popup>
        <van-dialog v-model="showDialog" confirm-button-text="我知道了" @confirm="handleGoMyRegister">
            <div class="dialog-content">
                <div class="icon">
                    <van-icon size="30" :name="require('../../../assets/images/img/dd-chenggong.png')" />
                    <span>预约成功！</span>
                </div>
                <div class="code">{{passWord}}</div>
                <div class="tips">支付完成后带预约码到分诊台取号排队候诊</div>
            </div>
        </van-dialog>
    </div>
</template>
<script>
import BButton from "../../base/BButton";
export default {
    name: "RegisterDetail",
    components: {
        [BButton.name]: BButton,
    },
    data() {
        return {
            dataSource: "",
            passWord: "",
            clinicType: "",
            timeRange: "",
            detailTimeRange: "",
            clinicTypeTips: false,
            timeRangeTips: false,
            btnDisabled: false,
            showDialog: false,
            showPopup: false,
        };
    },
    mounted() {
        let routeData = this.$route.params.data;
        let cardInfoConfig = {data: {patId: ""}};
        let registFeeConfig = {
            data: {
                deptNo: routeData.deptNo,
                doctorNo: routeData.doctorNo,
                timeFlag: routeData.timeFlag,
                appointDate: routeData.appointDate,
                registerFee: routeData.registerFee,
                clinicFee: routeData.clinicFee
            }
        };
        let outpatientApis = this.$api.outpatient;
        let queryCardInfo = outpatientApis.queryCardInfo;
        let queryRegisterFee = outpatientApis.queryRegisterFee;
        this.$api.all(queryCardInfo(cardInfoConfig), queryRegisterFee(registFeeConfig)).then((response) => {
            let cardInfoData = response[0].data.Data;
            let registFeeData = response[1].data.Data;
            if (cardInfoData && registFeeData) {
                let dataSource = {};
                for (let i = 0; i < cardInfoData.length; i++) {
                    let curItem = cardInfoData[i];
                    if (curItem.isDefault == 0) {
                        let userInfo = {};
                        userInfo["patId"] = curItem.patId;
                        userInfo["patName"] = curItem.patName;
                        userInfo["patMedNo"] = curItem.patMedNo;
                        Object.assign(dataSource, routeData, userInfo);
                        break;
                    }
                }
                dataSource["totalFee"] = registFeeData.totalFee;
                dataSource["isReduction"] = registFeeData.isReduction;
                this.dataSource = dataSource;
            }
        })
    },
    computed: {
        isAM() {
            return  this.$route.query.timeFlag == 0;
        },
        payAvailable() {
            return this.$store.state.payAvailable;
        },
        hasTimeRange() {
            if (this.isCurrent) {
                return false;
            }
            else {
                return this.$store.state.timeRange;
            }
        },
        isCurrent() {
            return this.$route.query.type == 0;
        },
        registerSubmitTips() {
            return this.$store.state.registerSubmitTips;
        },
        showPassword() {
            return this.$store.state.showPassword;
        },
        appointDetailTimeSpeed() {
            return this.$store.state.appointDetailTimeSpeed;
        },
        detailTimeRangeDataSource() {
            let startTime = this.timeRange.slice(0, 5);
            let endTime = this.timeRange.slice(6, 11);
            if (startTime && endTime) {
                return this.timeDevide(startTime, endTime);
                
            }
            else {
                return "";
            }
        }
    },
    methods: {
        handleSubmit() {
            if (!this.validForm()) {
                return;
            }
            if (this.isCurrent) {
                this.payOrder();
            }
            else {
                this.submitOrder();
            }
        },
        handleGoMyRegister() {
            let patId = this.dataSource.patId;
            let patMedNo = this.dataSource.patMedNo;
            this.$router.replace({name: "MyRegister", query: {patId, patMedNo}});
        },
        handleChangeClinicType() {
            this.clinicTypeTips = false;
        },
        handleShowDetailTimeRange(isDisabled) {
            if(!isDisabled) {
                this.showPopup = true;
            }
        },
        handleClosePopUp() {
            this.timeRangeTips = false;
            this.showPopup = false;
        },
        submitOrder() {
            this.btnDisabled = true;
            let data = this.dataSource;
            data["clinicType"] = this.clinicType;
            data["type"] = 0;
            if (this.hasTimeRange) {
                data["appointTime"] = this.detailTimeRange;
            }
            this.$api.outpatient.appointRegisterOrCancel({
                data,
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then((response) => {
                if (response.data.Code == 0) {
                    this.passWord = response.data.Data.passWord;
                    if (this.showPassword) {
                        this.showDialog = true;
                    }
                    else {
                        this.handleGoMyRegister();
                    }
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            })
        },
        payOrder() {
            this.btnDisabled = true;
            let data = this.dataSource;
            let patId = data.patId;
            let fee = data.totalFee;
            let patMedNo = data.patMedNo;
            let deptNo = data.deptNo;
            let doctorNo = data.doctorNo;
            let timeFlag = data.timeFlag;
            let attach = "6-" + deptNo + "-" + doctorNo + "-" + timeFlag;
            this.$api.outpatient.getJsApi({
                data: {
                    patId,
                    body: "当日挂号",
                    fee,
                    detail: "当日挂号支付",
                    visitNo: patMedNo,
                    attach
                },
                reqLoading: false,
                resToastClear: false,
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let resData = response.data.Data;
                    let systemType = this.globalMethods.getCookie("systemType");
                    if (systemType == "0A") {
                        this.globalMethods.aliPay(resData);
                    }
                    else if (systemType == "0B") {
                        this.globalMethods.wxPay(resData, () => {
                            this.btnDisabled = false;
                        });
                    }
                }
                else if (response.data.Code != -2) {
                    this.btnDisabled = false;
                }
            });
        },
        timeDevide(startTime, endTime) {
            let timeRangeArr = [];
            let endTimer = new Date().setHours(endTime.slice(0, 2), endTime.slice(3, 5));
            let rangeStart = startTime;
            let speed = this.appointDetailTimeSpeed;
            for (let i = speed; true; i += speed) {
                let newTime = this.getSpeedTime(startTime, i);
                let newTimeHours = `${newTime.hours}`.padStart(2, "0");
                let newTimeMinutes = `${newTime.minutes}`.padStart(2, "0");
                let rangeEnd = `${newTimeHours}:${newTimeMinutes}`;
                let newTimer = newTime.timer;
                if (newTimer-30000 <= endTimer) {
                    timeRangeArr.push(`${rangeStart}-${rangeEnd}`);
                    rangeStart = rangeEnd;
                }
                else {
                    break;
                }
            };
            return timeRangeArr;
        },
        getSpeedTime(time, speed) {
            let hours = time.slice(0, 2);
            let minutes = time.slice(3, 5);
            let time1 = new Date().setHours(hours, minutes);
            let time2 = time1 + speed*1000*60;
            let newHours = new Date(time2).getHours();
            let newMinutes = new Date(time2).getMinutes();
            return {hours: newHours, minutes: newMinutes, timer: time2};
	    },
        validForm() {
            let mark = true;
            if (this.clinicType == "") {
                this.clinicTypeTips = true;
                mark = false;
            }
            if (this.hasTimeRange && this.detailTimeRange == "") {
                this.timeRangeTips = true;
                mark = false;
            };
            return mark;
        }
    }
};

</script>
<style scoped>
.title-style {
    text-align: left;
    color: #877e7e;
}
.value-style {
    color: black;
    overflow: visible;
}
.danger {
    color: #ff6257;
}
.clinic-type,
.date-range {
    padding: 10px 20px 0 20px;
    background-color: #fff;
    margin-top: 10px;
}
.title {
    text-align: left;
    font-weight: bold;
}
.van-radio-group {
    padding: 15px 15px 0 15px;
}
.van-radio-group .van-radio:nth-child(2n-1) {
    margin-right: 30px;
}
.date-range .van-radio-group .van-radio {
    margin-bottom: 20px;
}
.select-card-modal .van-radio-group .van-radio {
    margin-bottom: 20px;
}
.notes {
    color: #9c9c9c;
    font-size: 14px;
    text-align: left;
    padding: 10px;
}
.dialog-content {
    padding: 20px 20px 40px 20px;
}
.dialog-content .icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.dialog-content .icon>span {
    font-size: 14px;
    color: #0078ff;
    text-align: center;
}
.dialog-content .code {
    margin-top: 20px;
    color: #ff6257;
    background-color: #e5e7e9;
    padding: 5px;
}
.dialog-content .tips {
    font-size: 14px;
    color: #999;
    margin-top: 15px;
}
.select-card-modal {
    width: 90%;
}
.select-card-modal .title {
    width: 100%;
    text-align: left;
    padding: 10px;
    font-size: 15px;
    color: #fff;
    background-image: linear-gradient(to right, #00CAFF , #2772FF);
    border-radius: 10px 10px 0 0 / 10px 10px 0 0;
    position: fixed;
    top: -1px;
    left: 0;
    z-index: 3000;
}
.popup .content {
    padding-top: 40px;
    max-height: 400px;
    overflow: scroll;
    background-color: #F4F6F9;
}
.confirm-btn {
    height: 43px;
}

</style>



