const inHosNo = {
    //查询入院登记证信息
    queryInHospitalCertificate: "/api/InHospital/InHospital/AsyncQueryInHospitalCertificate",
    //入院登记
    inHospitalRegister: "/api/InHospital/InHospital/AsyncSelfInHospital",
    //绑定住院患者信息
    bindPatientInfo: "/api/InHospital/PatientInfo/AsyncBindPatientInfo",
    //查询绑定的住院患者信息列表
    queryPatientInfoList: "/api/InHospital/PatientInfo/AsyncQueryCloudPatientInfoList",
    //根据住院号查询患者信息
    queryPatientInfoByID: "/api/InHospital/PatientInfo/AsyncQueryHisPatientInfoByID",
    //解绑住院患者信息
    unbindPatientInfo: "/api/InHospital/PatientInfo/AsyncUnBindCloudPatientInfo",
    //更新住院患者信息
    updatePatientInfo: "/api/InHospital/PatientInfo/AsyncUpdCloudPatientInfo",
    //查询住院清单
    queryInHosList: "/api/InHospital/Report/AsyncQueryInHospitalList",
    //查询住院缴费记录
    queryInHosPayRecord: "/api/InHospital/Report/AsyncQueryInHospitalDepositRecords",
    //查询结算信息
    queryInHosSettleInfo: "/api/InHospital/Report/AsyncQueryInHospitalSettleInfo",
}

export default inHosNo;


