<template>
    <div class="bg-fff p-b-5">
        <div class="m-b-80">
            <com-card @show-code="handleGoDetailPage('CodeDetail')" @go-bind-card="handleGoDetailPage(sociaSecuCard ? 'SelectBindType' : 'BindCard')" @set-default="handleSetDefault" :status="binded" :type="0" :data="dataSource"></com-card>
            <div class="main m-t-70 p-l-15 p-r-15">
                <div class="main-service p-b-15">
                    <base-operate-bar :text="{firstTxt:'常用', secondTxt: '服务'}"></base-operate-bar>
                    <van-grid :border="false" :column-num="4">
                        <van-grid-item>
                            <base-service-btn :text="'本人充值'" :icon="require('../../../assets/images/charge.png')" :gradient="'#00CAFF,#008CFF'" @click.native="handleGoPersonalCharge('Charge')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="personalPay">
                            <base-service-btn :text="'本人缴费'" :icon="require('../../../assets/images/pay.png')" :gradient="'#9DEB7E,#61C854'" @click.native="handldeGoPersonalPay('Pay')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="currentRegister">
                            <base-service-btn :text="'当日挂号'" :icon="require('../../../assets/images/outpatient/current_register.png')" :gradient="'#ff5d5d,#ffb879'" @click.native="handleGoCurrentRegister('AppointRegister')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="appointRegister">
                            <base-service-btn :text="'预约挂号'" :icon="require('../../../assets/images/outpatient/appoing.png')" :gradient="'#FFD418,#FDB404'" @click.native="handleGoAppointRegister('AppointRegister')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item>
                            <base-service-btn :text="'就诊人管理'" :icon="require('../../../assets/images/outpatient/manage_card.png')" :gradient="'#6BDF6B,#3FD18B'" @click.native="handleGoDetailPage('ManageCard')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="invoice">
                            <base-service-btn :text="'电子票据'" :icon="require('../../../assets/images/outpatient/invoice.png')" :gradient="'#C296FF,#6E6EFF'" @click.native="handleGoDetailPage('OutpInvoice')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="waittingDiag">
                            <base-service-btn :text="'候诊查询'" :icon="require('../../../assets/images/outpatient/waitting_diag.png')" :gradient="'#46EBF7,#00B1C5'" @click.native="handleGoWaittingDiag('WaittingDiag')"></base-service-btn>
                        </van-grid-item>
                        <van-grid-item v-if="nucleicAcidTest">
                            <base-service-btn :text="'核酸检测'" :icon="require('../../../assets/images/outpatient/nucleic_acid_testing.png')" :gradient="'#B9C2FF,#5671FD'" @click.native="handleGoNucleicAcidTest(nucleicAcidTestAppoint ? 'NucleicAcidTestAppoint' : 'NucleicAcidTest')"></base-service-btn>
                        </van-grid-item>
                    </van-grid>
                </div>
                <div class="more-service">
                    <base-operate-bar :text="{firstTxt:'更多', secondTxt: '服务'}"></base-operate-bar>
                    <div class="more-service flex_dom flex_item_between m-t-10">
                        <div class="left">
                            <base-service-card :scale="'large'" :icon="require('../../../assets/images/outpatient/more_service01.png')" :title="'我的挂号'" :title-desc="'查看挂号成功信息'" @click.native="handleGoMyRegister('MyRegister')" v-if="myRegister"></base-service-card>
                            <base-service-card :scale="'large'" :icon="require('../../../assets/images/outpatient/more_service02.png')" :title="'为他人充值'" :title-desc="'为他人的就诊卡充值'" @click.native="handleGoOthersCharge('QueryOthersCardByNum')" v-else></base-service-card>
                        </div>
                        <div class="right flex_column flex_item_between">
                            <template>
                                <base-service-card :scale="moreServiceScale" :icon="require('../../../assets/images/outpatient/more_service02.png')" :title="'为他人充值'" :title-desc="'为他人的就诊卡充值'" @click.native="handleGoOthersCharge('QueryOthersCardByNum')" v-if="myRegister"></base-service-card>
                                <base-service-card :scale="moreServiceScale" :icon="require('../../../assets/images/outpatient/more_service03.png')" :title="'为他人缴费'" :title-desc="'为他人的医嘱项目缴费'" @click.native="handleGoOthersPay('QueryOthersCardByNum')" v-if="personalPay"></base-service-card>
                            </template>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import CCard from "../../common/CCard";
import BOperateBar from "../../base/BOperateBar";
import BServiceBtn from "../../base/BServiceBtn";
import BServiceCard from "../../base/BServiceCard";
export default {
    name: "Outpatient",
    components: {
        [CCard.name]: CCard,
        [BOperateBar.name]: BOperateBar,
        [BServiceBtn.name]: BServiceBtn,
        [BServiceCard.name]: BServiceCard,
    },
    data() {
        return {
            border: false,
            dataSource: {},
        };
    },
    mounted() {
        this.setToken();
    },
    computed: {
        binded() {
            return this.$store.state.binded;
        },
        personalPay() {
            return this.$store.state.personalPay;
        },
        appointRegister() {
            return this.$store.state.appointRegister;
        },
        appointRegisterUrl() {
            return this.$store.state.appointRegisterUrl;
        },
        currentRegister() {
            return this.$store.state.currentRegister;
        },
        currentRegisterUrl() {
            return this.$store.state.currentRegisterUrl;
        },
        myRegister() {
            return this.$store.state.myRegister;
        },
        myRegisterUrl() {
            return this.$store.state.myRegisterUrl;
        },
        sociaSecuCard() {
            return this.$store.state.sociaSecuCard;
        },
        invoice() {
            return this.$store.state.invoice;
        },
        waittingDiag() {
            return this.$store.state.waittingDiag;
        },
        waittingDiagUrl() {
            return this.$store.state.waittingDiagUrl;
        },
        nucleicAcidTest() {
            return this.$store.state.nucleicAcidTest;
        },
        moreServiceScale() {
            return this.myRegister && this.personalPay ? "middle" : "large";
        },
        nucleicAcidTestAppoint() {
            return this.$store.state.nucleicAcidTestAppoint;
        },
    },
    methods: {
        handleGoCurrentRegister(name) {
            if (this.currentRegisterUrl) {
                window.location = this.currentRegisterUrl;
            }
            else {
                let query = {};
                query["type"] = 0;
                this.handleGoDetailPage(name, query);
            }
        },
        handleGoAppointRegister(name) {
            if (this.appointRegisterUrl) {
                window.location = this.appointRegisterUrl;
            }
            else {
                let query = {};
                query["type"] = 1;
                this.handleGoDetailPage(name, query);
            }
        },
        handleGoMyRegister(name) {
            if (this.myRegisterUrl) {
                window.location = this.myRegisterUrl;
            }
            else {
                let query = {};
                query["patId"] = this.dataSource.patId;
                query["patMedNo"] = this.dataSource.patMedNo;
                this.handleGoDetailPage(name, query);
            }
        },
        handleGoWaittingDiag(name) {
            if (this.waittingDiagUrl) {
                window.location = this.waittingDiagUrl;
            }
            else {
                this.handleGoDetailPage(name);
            }
        },
        handldeGoPersonalPay(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            this.handleGoDetailPage(name, query);
        },
        handleGoPersonalCharge(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            this.handleGoDetailPage(name, query);
        },
        handleGoOthersCharge(name) {
            let query = {};
            query["type"] = "Charge";
            this.handleGoDetailPage(name, query);
        },
        handleGoOthersPay(name) {
            let query = {};
            query["type"] = "Pay";
            this.handleGoDetailPage(name, query);
        },
        handleGoNucleicAcidTest(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            query["patName"] = this.dataSource.patName;
            query["patMedNo"] = this.dataSource.patMedNo;
            query["patIdNo"] = this.dataSource.patIdNo;
            this.handleGoDetailPage(name, query);
        },
        handleGoDetailPage(name, query) {
            if (query) {
                this.$router.push({name, query}).catch(()=>{});
            }
            else {
                this.$router.push({name}).catch(()=>{});
            }
        },
        handleSetDefault() {
            this.$router.push({name: "CardList"});
        },
        checkCookie() {
            // 初始化的时候清除缓存的token、systemType、init信息
            if (window.location.href.includes("systemType")) {
                this.globalMethods.delCookie("token");
                this.globalMethods.delCookie("systemType");
                this.globalMethods.delCookie("init");
            };
            let init = this.globalMethods.getCookie("init");
            let token = this.globalMethods.getCookie("token");
            if (init != "yes" && !token) {
                if (!this.globalMethods.getCookie("systemType")) {
                    let url = window.location.href;
                    let systemType = url.split("=")[1];
                    this.globalMethods.setCookie("systemType", systemType);
                }
                this.globalMethods.setCookie("init", "yes", "s8");
                this.globalMethods.login();
            }
        },
        setToken() {
            this.checkCookie();
            this.$toast.loading({message: '加载中...', forbidClick: true, duration: 0});
            let owner = this;
            let token = this.globalMethods.getCookie("token");
            // 若cookie里没有token就去获取token
            if(!token) {
                let href = window.location.href;
                let url = href.includes("?") ? href.split("?")[1] : "";
                let search = url.split("&");
                for (let i = 0; i < search.length; i++) {
                    let item = search[i];
                    if (item.includes("code")) {
                        var code = item.split("=")[1];
                        break;
                    }
                };
                if (code) {
                    this.$api.login.getToken({
                        data: {
                            code,
                            systemType: owner.globalMethods.getCookie("systemType")
                        },
                        reqLoading: false,
                        resToastClear: false,
                    }).then((response) => {
                        if (response.data.Code == 0) {
                            let data = response.data;
                            let token = data.Data;
                            owner.globalMethods.setCookie("token", token);
                            // 调用接口
                            owner.queryCardInfo();
                        }
                        else {
                            owner.$toast({
                                message: "发生错误，获取token失败",
                                forbidClick: true
                            })
                        }
                    })
                }
            }
            else {
                // 若cookie里面有token则直接调用接口
                owner.queryCardInfo();
            }
        },
        queryCardInfo() {
            let owner = this;
            this.$api.outpatient.queryCardInfo({
                data: {patId: ""},
                reqLoading: false,
                resLoading: true,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let dataArry = response.data.Data;
                    for (let i = 0; i < dataArry.length; i++) {
                        let item = dataArry[i];
                        if (item.isDefault == 0) {
                            owner.dataSource = item;
                            break;
                        }
                    }
                    owner.globalMethods.updateStatus("binded", true);
                } else if(response.data.Code != -2) {
                    owner.globalMethods.updateStatus("binded", false);
                };
            })
        },
    }
};

</script>
<style scoped>
.more-service .left {
    width: 167.5px;
    height: 170px;
}
.more-service .right {
    width: 167.5px;
    height: 170px;
}
</style>



