<template>
    <div>
        <div class="container radius-10 flex_dom flex_item_mid">
            <div class="info flex_dom flex_item_mid" @click="handleOnGoCardDetailPage">
                <div class="avatar m-r-10" :class="classObj">
                    <img src="../../assets/images/gentleman.png" v-if="dataInner.patSex == 1">
                    <img src="../../assets/images/lady.png" v-if="dataInner.patSex == 2">
                </div>
                <div class="content flex_column flex_item_between">
                    <div class="name tl">{{dataInner.patName}}</div>
                    <div class="code">
                        <span>{{type == 0 ? "卡号 :" : "住院号 :"}}</span>
                        <span>{{dataInner.patMedNo || dataInner.inHosNoLabel}}</span>
                    </div>
                </div>
            </div>
            <div class="line"></div>
            <div class="btn flex_column flex_item_mid flex_item_center pr" @click="handleOnGoCodeDetailPage">
                <img src="../../assets/images/qr_code_dark.png">
                <div class="text m-t-10">刷码就诊</div>
                <div class="default-icon pa" v-if="dataInner.isDefault == 0">默认</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "com-manage-card",
    components: {

    },
    data() {
        return {
            
        };
    },
    props: {
        type: {
            type: [String, Number],
            default: 0
        },
        data: {
            type: Object,
            default() {
                return {
                    patName: "患者姓名",
                    patSex: "2",
                    patMedNo: "11111111111111",
                    isDefault: 0
                }
            }
        }
    },
    computed: {
        dataInner() {
            return this.data;
        },
        classObj() {
            return {
                gentleman: this.dataInner.patSex == 1,
                lady: this.dataInner.patSex == 2
            }
        }
    },
    methods: {
        handleOnGoCodeDetailPage() {
            this.$emit("show-code");
        },
        handleOnGoCardDetailPage() {
            this.$emit("go-detail");
        }
    }
};
</script>
<style scoped>
.container {
    width: 100%;
    height: 100px;
    background-image: radial-gradient(circle at 9px 8px ,transparent 0%, transparent 8px,#fff 8px, #fff 100%);
    background-position: 249px -8px;
    background-size: 100% 100px;
}
.info {
    width: 241.7px;
    padding: 20px 0  20px 15px;
}
.line {
    width: 17.26px;
    /* border-right: 0.01333rem dashed #E0E0E0; */
    border-right: 1px dashed #E0E0E0;
    height: 80px;
}
.btn {
    width: 86.3px;
    height: 100%;
}
.info .avatar {
    width: 46px;
    height: 46px;
    border-radius: 23px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
}
.info .avatar.gentleman {
    background-color: #E3F4FC;
}
.info .avatar.lady {
    background-color: #FFE6EA;
}
.info .content {
    height: 45px;
}
.avatar img {
    width: 70%;
    height: 80%;
}
.btn img {
    width: 17.5px;
    height: 17.5px;
}
.btn .text {
    font-size: 10px;
    color: #828694;
}
.conent .name {
    color: #2A2D34;
    font-size: 16px;
}
.content .code {
    color: #828694;
    font-size: 13px;
}
.default-icon {
    width: 48px;
    height: 18px;
    line-height: 18px;
    border-radius: 10px 0 10px 0 / 10px 0 10px 0;
    background-image: linear-gradient(#00CAFF, #2872FF);
    font-size: 10px;
    color: #fff;
    text-align: center;
    right: 0;
    bottom: 0;
}
</style>


