<template>
    <div>
        <div class="confirm-btn inline_div radius-10" :class="classObj">
            <van-button block :color="theme" :loading="btnDisabled" loading-text="请稍后" :disabled="btnDisabled" :native-type="nativeType" @click="handleConfirm">{{text}}</van-button>
        </div>
    </div>
</template>
<script>
export default {
    name: "base-button",
    data() {
        return {
            
        };
    },
    props: {
        btnDisabled: {
            type: Boolean,
            default: false
        },
        text: {
            type: String,
            default: "确定"
        },
        size: {
            type: String,
            default: "large"
        },
        color: {
            type: String,
            default: "primary"
        },
        nativeType: {
            type: String,
            default: ""
        }
    },
    computed: {
        classObj() {
            return {
                large: this.size == "large",
                middle: this.size == "middle",
                small: this.size == "small",
                mini: this.size == "mini"
            }
        },
        theme() {
            if (this.color == "primary") {
                return "linear-gradient(to right, #00CAFF , #2772FF)";
            }
            else if (this.color == "warning") {
                return "linear-gradient(to right, #f9bca2 , #ff976a)"
            }
            else if (this.color == "danger") {
                return "linear-gradient(to right, #FF6257 , #FF0000)"
            }
        }
    },
    methods: {
        handleConfirm() {
            this.$emit("confirm");
        }
    }
};
</script>
<style scoped>
.confirm-btn.large {
    height: 43px;
    width: 100%;
}
.confirm-btn.middle {
    height: 43px;
    width: 250px;
}
.confirm-btn.small {
    height: 43px;
    width: 100px;
}
.confirm-btn.mini {
    height: 28px;
    width: 70px;
    border-radius: 3px;
}
.confirm-btn.mini .van-button {
    height: 28px;
}
.confirm-btn >>> .van-button__text {
    font-size: 16px;
}
.confirm-btn.mini >>> .van-button__text {
    font-size: 14px;
}
.confirm-btn >>> .van-button--normal {
    padding: 0;
}
</style>


