<template>
    <div class="flex_column flex_item_between flex_item_mid">
        <div class="icon bgimg100 m-b-5" :style="styleObj1"></div>
        <div class="txt" :style="styleObj2">{{text}}</div>
    </div>
</template>
<script>
export default {
    name: "base-tabbar-btn",
    data() {
        return {

        };
    },
    props: {
        text: {
            type: String,
            default: "名称"
        },
        defaultIcon: {
            type: String
        },
        activeIcon: {
            type: String
        },
        defaultColor: {
            type: String,
            default: "#8191A2"
        },
        activeColor: {
            type: String,
            default: "#0078FF"
        },
        status: {
            type: [String, Number],
            default: 0
        },
    },
    computed: {
        statusInner() {
            return this.status;
        },
        styleObj1() {
            let url = this.statusInner == 0 ? this.defaultIcon : this.activeIcon;
            return {
                "background-image": `url(${url})`
            }
        },
        styleObj2() {
            return {
                "color": this.statusInner == 0 ? this.defaultColor : this.activeColor
            }
        }
    }
};
</script>
<style scoped>
.icon {
    width: 22px;
    height: 22px;
    background-repeat: no-repeat;
}
.txt {
    font-size: 10px;
}
</style>


