<template>
    <div>
        <div class="card-detail-wrap main-bg p-t-25">
            <div class="profile flex_dom flex_item_mid">
                <div class="pr avatar-box m-r-15">
                    <div class="avatar pa">
                        <img class="pa abs-mid-X" :src="wxData.userAvatar" v-if="wxData.userAvatar">
                        <img class="pa abs-mid-X" src="../../../assets/images/default_avatar.png" v-else>
                    </div>
                    <span class="patSex pa bgimg100" :class="classObj" v-if="wxData.sex"></span>
                </div>
                <div class="info flex_column flex_item_between color-fff">
                    <div class="name tl font18 font-w6">{{wxData.nickName}}</div>
                    <div class="code tl font12">{{wxData.region}}</div>
                </div>
            </div>
            <div class="content m-t-25 m-b-80">
                <div class="panel radius-10 p-l-15 p-r-15">
                    <div class="title tl">门诊服务</div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoMyRegister('MyRegister')" v-if="myRegister">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/appoint.png">
                            <span class="m-l-10">我的挂号</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('WaittingDiag')" v-if="waittingDiag">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/waitting_diag.png">
                            <span class="m-l-10">候诊查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('PayRecord')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/charge.png">
                            <span class="m-l-10">就诊卡充值记录</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('DayList')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/consume.png">
                            <span class="m-l-10">门诊费用查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoQueryOutPatientReport('QueryReport')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/report_outpat.png">
                            <span class="m-l-10">门诊报告查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell flex_dom flex_item_between flex_item_mid" @click="handleGoQueryOutPatientNatReport('QueryReport')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/nucleic_acid_testing.png">
                            <span class="m-l-10">核酸检测报告查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                </div>
                <div class="panel radius-10 p-l-15 p-r-15 m-t-10">
                    <div class="title tl">住院服务</div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('InHosPayRecord')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/deposit.png">
                            <span class="m-l-10">住院押金预交记录</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell border-bottom flex_dom flex_item_between flex_item_mid" @click="handleGoDetailPage('InHosList')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/list_inhos.png">
                            <span class="m-l-10">住院日清单</span>
                        </div>
                        <span class="value"></span>
                    </div>
                    <div class="cell flex_dom flex_item_between flex_item_mid" @click="handleGoQueryInHosReport('QueryReport')">
                        <div class="label flex_dom flex_item_mid">
                            <img class="icon" src="../../../assets/images/account/report_inhos.png">
                            <span class="m-l-10">住院报告查询</span>
                        </div>
                        <span class="value"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "account",
    data() {
        return {
            dataSource: "",
            wxData: "",
        };
    },
    computed: {
        classObj() {
            return {
                gentleman: this.wxData.sex == 1,
                lady: this.wxData.sex == 2
            }
        },
        binded() {
            return this.$store.state.binded;
        },
        myRegister() {
            return this.$store.state.myRegister;
        },
        waittingDiag() {
            return this.$store.state.waittingDiag;
        },
    },
    mounted() {
        this.queryCardInfo();
        this.queryUserInfo();
    },
    methods: {
        handleGoMyRegister(name) {
            let query = {};
            query["patId"] = this.dataSource.patId;
            query["patMedNo"] = this.dataSource.patMedNo;
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryOutPatientReport(name) {
            let query = {};
            query["type"] = 0;
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryOutPatientNatReport(name) {
            let query = {};
            query["type"] = 0;
            query["isNat"] = "yes";
            this.handleGoDetailPage(name, query);
        },
        handleGoQueryInHosReport(name) {
            let query = {};
            query["type"] = 1;
            this.handleGoDetailPage(name, query);
        },
        handleGoDetailPage(name, query) {
            if (query) {
                this.$router.push({name, query}).catch(()=>{});
            }
            else {
                this.$router.push({name}).catch(()=>{});
            }
        },
        queryCardInfo() {
            this.$api.outpatient.queryCardInfo({
                data: {patId: ""}
            }).then ((response) => {
                if (response.data.Code == 0) {
                    let dataArry = response.data.Data;
                    for (let i = 0; i < dataArry.length; i++) {
                        let item = dataArry[i];
                        if (item.isDefault == 0) {
                            this.dataSource = item;
                            break;
                        }
                    }
                    this.globalMethods.updateStatus("binded", true);
                }
                else if (response.data.Code != -2) {
                    this.globalMethods.updateStatus("binded", false);
                };
            })
        },
        queryUserInfo() {
            this.$api.account.queryUserInfo({
                data: {},
                reqLoading: false,
                resToastClear: false,
            }).then ((response) => {
                if (response.data.Code == 0) {
                    this.wxData = this.wrapData(response.data.Data);
                }
            })
        },
        wrapData(data) {
            let province = data.province;
            let city = data.city;
            data.region = city ? `${province} | ${city}` : province;
            return data;
        }
    }
};
</script>
<style scoped>
.card-detail-wrap {
    background: url('../../../assets/images/card_bg.png') no-repeat;
    background-size: 100%;
}
.profile .avatar-box {
    width: 50px;
    height: 50px;
    border-radius: 25px;
}
.profile .avatar {
    width: 50px;
    height: 50px;
    border-radius: 25px;
    overflow: hidden;
    border: 1px solid #7BB6EC;
    /* background-color: #E3F4FC; */
}
.profile .avatar img {
    width: 100%;
    height: 100%;
    bottom: 0;
}
.profile  .patSex {
    width: 17px;
    height: 16px;
    bottom: 0;
    right: -2px;
}
.profile .patSex.gentleman {
    background: url('../../../assets/images/man.png') no-repeat;
}
.profile .patSex.lady {
    background: url('../../../assets/images/woman.png') no-repeat;
}
.profile .info {
    height: 45px;
}
.content .panel {
    background-color: #fff;
}
.content .panel .title {
    height: 48px;
    line-height: 48px;
    color: #292C34;
    font-size: 18px;
    font-weight: bold;
}
.content .panel .cell {
    height: 46px;
    line-height: 46px;
}
.content .panel .cell .icon {
    width: 22px;
    height: 22px;
}
.border-bottom {
    border-bottom: 0.5px solid #EEEEEE;
}
.content .panel .value {
    width: 25px;
    height: 25px;
    background: url('../../../assets/images/account/right.png') no-repeat;
    background-size: 100% 100%;
}
.balance {
    font-size: 18px;
    color: #FF6257;
}
.charge {
    font-size: 13px;
    color: #0078FF;
    padding: 10px;
}

</style>


